export async function compress(data: Blob | Uint8Array) {
  const input = data instanceof Blob ? data : new Blob([data])

  return new Response(
    input.stream().pipeThrough(new CompressionStream("gzip")),
  ).blob()
}

export async function decompress(data: Blob | Uint8Array) {
  const input = data instanceof Blob ? data : new Blob([data])

  return new Response(
    input.stream().pipeThrough(new DecompressionStream("gzip")),
  ).blob()
}
