export function formatPercentage(num: number, precision = 0) {
  if (num === 1) {
    precision = 0
  }

  return new Intl.NumberFormat("en-US", {
    style: "percent",
    maximumFractionDigits: precision,
    minimumFractionDigits: precision,
    // @ts-ignore
    roundingMode: "floor",
  }).format(num)
}

export function formatNum(num: number, precision = 0) {
  return new Intl.NumberFormat("en-US", {
    maximumFractionDigits: precision,
    minimumFractionDigits: precision,
    // @ts-ignore
    roundingMode: "floor",
  }).format(num)
}

export function formatOrdinalNum(num: number) {
  const asStr = String(num)
  if (/(4|5|6|7|8|9|0|11|12|13)$/.test(asStr)) {
    return `${num}th`
  }

  if (asStr.endsWith("1")) {
    return `${num}st`
  }

  if (asStr.endsWith("2")) {
    return `${num}nd`
  }

  return `${num}rd`
}

export function formatPercentageNice(num: number, precision = 0) {
  if (num === 0) {
    return "-"
  }

  if (num < 0.01 && precision === 0) {
    return "<1"
  }

  return formatPercentage(num, precision).slice(0, -1)
}

export function formatNumNice(num: number, precision = 0) {
  if (num === 0) {
    return "-"
  }

  if (num < 1 && precision === 0) {
    return "<1"
  }

  return formatNum(num, precision)
}

const S_IN_M = 60
const M_IN_H = 60
const MS_IN_S = 1000
const MS_IN_M = MS_IN_S * S_IN_M
const MS_IN_H = MS_IN_S * S_IN_M * M_IN_H

export function getHMS(ms: number) {
  const h = Math.floor(ms / MS_IN_H)
  const m = Math.floor((ms - h * MS_IN_H) / MS_IN_M)
  const s = Math.floor((ms - (h * MS_IN_H + m * MS_IN_M)) / MS_IN_S)

  return [h, m, s]
}

export function formatDuration(ms: number, asString: boolean = false) {
  const [h, m, s] = getHMS(ms)

  const chunks = []

  if (h > 0) {
    chunks.push([h, "h"])
  }

  if (m > 0) {
    chunks.push([m, "m"])
  }

  if (h === 0 && m === 0) {
    chunks.push([s, "s"])
  }

  return asString
    ? chunks.map((chunk) => chunk.join("")).join(" ")
    : chunks.flat()
}

export function formatTime(ms: number) {
  const [h, m, s] = getHMS(ms)

  return [
    String(h),
    String(m).padStart(2, "0"),
    String(s).padStart(2, "0"),
  ].join(":")
}
