import { notify } from "../actions/notify"
import state, { defaultSettings, defaultSummary } from "../state/state"
import storage from "../storage/storage"

export async function resetSettings() {
  await storage.general.set("settings", defaultSettings)
  await state.reload()
  notify({ title: "Default settings restored!", type: "ok", important: true })
}

export async function resetProgress() {
  await storage.booksStats.clear()
  await storage.chaptersStats.clear()
  await storage.paragraphsStats.clear()
  await storage.dailyStats.clear()
  await storage.general.set("statsSummary", defaultSummary)
  await state.reload()
  notify({ title: "Progress cleared!", type: "ok", important: true })
}
