export type Obj = {
  [key: string]:
    | Obj
    | string
    | number
    | boolean
    | null
    | Obj[]
    | number[]
    | string[]
}

function mergeArrays<T>(a: T[], b: T[]): T[] {
  return [...new Set([...a, ...b])]
}

export function merge(a: Obj, b: Obj): Obj {
  const result = structuredClone(a)

  for (const [key, val] of Object.entries(b)) {
    if (val === null || typeof val !== "object") {
      result[key] = val
    } else if (Array.isArray(val)) {
      const merged = mergeArrays((result[key] ?? []) as any[], val)
      result[key] = merged
    } else {
      result[key] = merge((result[key] ?? {}) as Obj, val)
    }
  }

  return result
}
