import storage from "../storage/storage"
import { decompress } from "../utils/gzip"

export async function getBookContent(bookId: string) {
  const res = await fetch(`/books/${bookId}/content.gz`)

  let data: string = ""

  const encoding = res.headers.get("content-encoding")

  if (encoding !== "gzip") {
    const compressed = await res.blob()
    const bytes = await decompress(compressed)
    data = new TextDecoder().decode(await bytes.arrayBuffer())
  } else {
    data = await res.text()
  }

  const chapters = data.split("\n\n").map((chapter) => {
    const [chapterId, ...paragraphs] = chapter.split("\n")
    return { id: chapterId, text: paragraphs.join("\n") }
  })

  for (const item of chapters) {
    const id = `${bookId}__${item.id}`
    await storage.booksContent.set(id, item.text)
  }

  return Object.fromEntries(
    chapters.map((chapter) => [chapter.id, chapter.text]),
  )
}
