import "./Confirm.css"
import { Component, createSignal, onMount } from "solid-js"
import { Portal } from "solid-js/web"

type Props = {
  requiredText: string
  onCancel: () => void
  onSumbmit: () => void
}

const Confirm: Component<Props> = (props) => {
  const [text, setText] = createSignal("")

  let inputRef: HTMLInputElement | undefined

  onMount(() => {
    if (!inputRef) {
      return
    }

    inputRef.focus()
  })

  return (
    <Portal>
      <section class="confirm">
        <div>
          <p>
            Type <code>{props.requiredText}</code> to confirm.
          </p>
          <form
            onSubmit={(e) => {
              e.preventDefault()

              if (text() !== props.requiredText) {
                return
              }

              props.onSumbmit()
            }}
          >
            <input
              type="text"
              value={text()}
              onInput={(e) => {
                setText(e.currentTarget.value)
              }}
              ref={inputRef}
              placeholder="type the phrase"
            />
            <button type="reset" onClick={props.onCancel}>
              Cancel
            </button>
            <input
              type="submit"
              disabled={text() !== props.requiredText}
              value="Confirm"
            />
          </form>
        </div>
      </section>
    </Portal>
  )
}

export default Confirm
