import "./Recommended.css"
import { Component, For, createMemo } from "solid-js"
import Book from "../../common/book/Book"
import state from "../../../state/state"
import { getBooksData } from "../../../actions/getBooksData"
import config from "../../../config"
import { shuffle } from "../../../utils/array"

const WEIGHTS = {
  PREFERRED_GENRE: 5,
  PREFERRED_LENGTH: 5,
  IN_READ_SERIES: 10,
  READ_AUTHOR: 5,
  RATING: 5,
  POPULARITY: 5,
}

const Recommended: Component = () => {
  const recommendedBooks = createMemo(() => {
    const usedBooks = []
    const completedBooks = []
    const unusedBooks = []
    const startedSeries: { [key: string]: Set<number> } = {}

    for (const book of state.get.booksIndex) {
      if (book.progress > 0) {
        usedBooks.push(book)

        if (book.progress === 1) {
          completedBooks.push(book)
        }

        if (book.series !== null) {
          if (!startedSeries[book.series]) {
            startedSeries[book.series] = new Set()
          }
          startedSeries[book.series].add(book.part)
        }
      } else {
        unusedBooks.push(book)
      }
    }

    // Filter out further parts of the series
    const unusedBooksNextParts = unusedBooks.filter((book) => {
      if (book.series === null || book.part === 1) {
        return true
      }

      const partsUsed = startedSeries[book.series]

      return partsUsed && Math.max(...partsUsed) + 1 === book.part
    })

    const readAuthors = new Set(
      completedBooks.map((bookInfo) => bookInfo.author),
    )

    for (const bookInfo of usedBooks) {
      if (bookInfo.series !== null) {
        if (!startedSeries[bookInfo.series]) {
          startedSeries[bookInfo.series] = new Set()
        }
        startedSeries[bookInfo.series].add(bookInfo.part)
      }
    }

    const favoriteGenres = new Set(state.get.settings.ui.favoriteGenres)
    const maxLength = state.get.settings.ui.favoriteMaxLength || Infinity

    const bookScores: { [key: string]: number } = {}

    for (const bookInfo of unusedBooksNextParts) {
      let score = 0

      /* Score genres */
      let genreWeight = WEIGHTS.PREFERRED_GENRE

      for (const genre of bookInfo.genres) {
        if (favoriteGenres.has(genre)) {
          score += genreWeight
          // Every additonal matching genre has diminishing returns (no lower than 1)
          genreWeight = Math.max(1, genreWeight - 1)
        }
      }

      /* Score length */

      const totalPages = Math.ceil(bookInfo.length / config.CHARACTERS_PER_PAGE)

      if (totalPages <= maxLength) {
        score += WEIGHTS.PREFERRED_LENGTH
      } else {
        const extraPages = totalPages - maxLength
        const reduction = Math.max(0, 1 - extraPages / 100) // some score form max 100 pages more
        score += WEIGHTS.PREFERRED_LENGTH * reduction
      }

      /* Score series */

      if (bookInfo.series && startedSeries[bookInfo.series]) {
        score += WEIGHTS.IN_READ_SERIES
      }

      if (readAuthors.has(bookInfo.author)) {
        score += WEIGHTS.READ_AUTHOR
      }

      bookScores[bookInfo.id] = score
    }

    // console.log({
    //   usedBooks,
    //   unusedBooks,
    //   completedBooks,
    //   readAuthors,
    //   startedSeries,
    //   favoriteGenres,
    //   maxLenngth: maxLength,
    //   bookScores,
    // })

    const candidateBooksSorted = unusedBooksNextParts.sort(
      (a, b) => bookScores[b.id] - bookScores[a.id],
    )

    const candidateBooksBest = candidateBooksSorted.slice(0, 6)
    const candidateBooksMaybe = candidateBooksSorted.slice(6, 50)
    shuffle(candidateBooksMaybe)

    const candidateBooks = [
      ...candidateBooksBest,
      ...candidateBooksMaybe.slice(0, 6),
    ]

    return getBooksData(candidateBooks)
  })

  return (
    <section class="recommended">
      <h2>Recommended</h2>
      <For each={recommendedBooks()}>
        {(book) => (
          <Book
            id={book.id}
            coverUrl={book.coverUrl}
            title={book.title}
            author={book.author}
            description={book.description}
            pages={book.pages}
            progress={book.progress}
            favorite={book.favorite}
            isNew={book.isNew}
            genres={book.genres}
          />
        )}
      </For>

      <footer>
        <a class="button primary" href="/books">
          Browse All Books
        </a>
      </footer>
    </section>
  )
}

export default Recommended
