import type { ChapterStats, StaticChapterInfo } from "../types/common"

type Args = {
  currentChapterId: string
  chaptersStats: { [index: string]: ChapterStats }
  chapters: StaticChapterInfo[]
  progress: number
}

export function getNextChapterId({
  currentChapterId,
  chaptersStats,
  chapters,
  progress,
}: Args) {
  if (progress === 0 || progress === 1) {
    const index = Math.max(
      0,
      chapters.findIndex((c) => !c.skip),
    )

    return chapters[index].id
  }

  const curr = chapters.findIndex((chapter) => chapter.id === currentChapterId)

  for (let i = curr; i < curr + chapters.length; i++) {
    const index = i % chapters.length
    const id = chapters[index].id

    if (
      !chapters[index].skip &&
      (!chaptersStats[id] || chaptersStats[id].progress < 1)
    ) {
      return chapters[index].id
    }
  }

  return chapters[0].id
}
