import { getDailyDate } from "../actions/getDailyDate"
import {
  calculateStat,
  summarizeBookStats,
  summarizeChapterStats,
  summarizeDailyStats,
} from "../components/pages/prompt/prompt-actions/summarizeStats"
import config from "../config"
import state from "../state/state"
import storage from "../storage/storage"
import {
  AverageStats,
  BasicStats,
  BookStats,
  ChapterStats,
  FinishedParagraphStats,
  MainStats,
  PBStats,
  StaticBookInfo,
  StatsSummary,
  TypedParagraphStats,
} from "../types/common"
import download from "./download"
import { getBookInfo } from "./getBookInfo"
import { compress, decompress } from "../utils/gzip.ts"
import { notify } from "../actions/notify.ts"

type ParagraphsStatsEntry = { [index: string]: FinishedParagraphStats }

type ParagraphsStatsStorage = {
  [id: string]: ParagraphsStatsEntry
}

type ChapterStatsEntry = { [chapterId: string]: ChapterStats }
type ChapterStatsStorage = { [bookId: string]: ChapterStatsEntry }
type BooksStatsStorage = { [bookId: string]: BookStats }
type DailyStatsStorage = { [day: string]: AverageStats }

async function getParagraphsData() {
  const keys = await storage.paragraphsStats.keys()
  const data: ParagraphsStatsStorage = {}

  for (const key of keys) {
    const entryData = await storage.paragraphsStats.get(key)

    if (entryData) {
      data[key] = entryData
    }
  }

  return data
}

function encodeParagraphStats(rawData: ParagraphsStatsEntry) {
  const data = Object.entries(rawData)

  const size = data.reduce(
    (len, [_, item]) => len + (item.type === "typed" ? 12 : 4),
    0,
  )

  const encoded = new Float64Array(size)
  let offset = 0

  for (const [index, entry] of data) {
    const isTyped = entry.type === "typed"
    encoded[offset] = Number(index)
    encoded[offset + 1] = isTyped ? 0 : 1

    if (isTyped) {
      encoded[offset + 2] = entry.wpm.value
      encoded[offset + 3] = entry.wpm.raw
      encoded[offset + 4] = entry.wpm.weight
      encoded[offset + 5] = entry.acc.value
      encoded[offset + 6] = entry.acc.weight
      encoded[offset + 7] = entry.con.value
      encoded[offset + 8] = entry.con.weight
      encoded[offset + 9] = entry.time
      encoded[offset + 10] = entry.timestamp
      encoded[offset + 11] = entry.length
    } else {
      encoded[offset + 2] = entry.timestamp
      encoded[offset + 3] = entry.length
    }

    offset += entry.type === "typed" ? 12 : 4
  }

  return encoded
}

function encodeParagraphsId(id: string) {
  return new TextEncoder().encode(id)
}

function concatBytes(items: Uint8Array[]) {
  const len = items.reduce((sum, item) => sum + item.byteLength, 0)
  const all = new Uint8Array(len)

  let offset = 0

  for (const item of items) {
    all.set(item, offset)
    offset += item.byteLength
  }

  return all
}

async function encodeSave() {
  const rawData = await getParagraphsData()

  const version = 1
  const chunks: Uint8Array[] = [new Uint8Array([version])]

  for (const [key, val] of Object.entries(rawData)) {
    const id = encodeParagraphsId(key)
    const stats = encodeParagraphStats(val)

    const statsSizeBytes = new Uint8Array(
      new Float64Array([stats.byteLength]).buffer,
    )
    const statsBytes = new Uint8Array(stats.buffer)
    const bytes = new Uint8Array([
      id.byteLength,
      ...id,
      ...statsSizeBytes,
      ...statsBytes,
    ])

    chunks.push(bytes)
  }

  const encoded = concatBytes(chunks)

  return encoded
}

function decodeEntry(data: Float64Array) {
  let offset = 0
  const items: [number, FinishedParagraphStats][] = []

  while (offset < data.length) {
    const paragraphIndex = data[offset]
    const type = data[offset + 1] === 0 ? "typed" : "read"

    if (type === "typed") {
      items.push([
        paragraphIndex,
        {
          type,
          wpm: {
            value: data[offset + 2],
            raw: data[offset + 3],
            weight: data[offset + 4],
          },
          acc: {
            value: data[offset + 5],
            weight: data[offset + 6],
          },
          con: {
            value: data[offset + 7],
            weight: data[offset + 8],
          },
          time: data[offset + 9],
          timestamp: data[offset + 10],
          length: data[offset + 11],
        },
      ])

      offset += 12
    } else {
      items.push([
        paragraphIndex,
        {
          type,
          timestamp: data[offset + 2],
          length: data[offset + 3],
        },
      ])

      offset += 4
    }
  }

  return items
}

async function decodeSave(blob: Blob) {
  const bytes = new Uint8Array(await blob.arrayBuffer())
  const version = bytes[0]
  const data: ParagraphsStatsStorage = {}
  let offset = 1

  if (version === 1) {
    while (offset < bytes.length) {
      const idSize = bytes[offset]
      offset++
      const id = new TextDecoder().decode(bytes.slice(offset, idSize + offset))
      offset += idSize
      const entrySize = new Float64Array(
        bytes.slice(offset, offset + 8).buffer,
      )[0]
      offset += 8
      const entry = decodeEntry(
        new Float64Array(bytes.slice(offset, offset + entrySize).buffer),
      )
      offset += entrySize

      data[id] = Object.fromEntries(entry)
    }
  }

  return data
}

async function mergeSaveWithCurrent(saved: ParagraphsStatsStorage) {
  const curr = await getParagraphsData()

  for (const [key, entries] of Object.entries(saved)) {
    for (const [index, entry] of Object.entries(entries)) {
      if (!curr[key]) {
        curr[key] = {}
      }

      if (!curr[key][index] || curr[key][index].timestamp < entry.timestamp) {
        curr[key][index] = entry
      }
    }
  }

  return curr
}

async function updateParagraphsStats(data: ParagraphsStatsStorage) {
  for (const [key, val] of Object.entries(data)) {
    await storage.paragraphsStats.set(key, val)
  }
}

async function recalculateChaptersStats(data: ParagraphsStatsStorage) {
  const bookInfos: { [key: string]: StaticBookInfo } = {}
  const chaptersStats: ChapterStatsStorage = {}

  for (const [key, entries] of Object.entries(data)) {
    const [bookId, chapterId] = key.split("__")
    const bookInfo = bookInfos[bookId] ?? (await getBookInfo(bookId))

    if (bookInfo === null) {
      continue
    }

    bookInfos[bookId] = bookInfo
    const chapterInfo = bookInfo.chapters.find(
      (chapterInfo) => chapterInfo.id === chapterId,
    )
    const chapterLength = chapterInfo?.length ?? 0
    const skip = chapterInfo?.skip ?? true
    const chapterStats = summarizeChapterStats(
      Object.values(entries),
      chapterLength,
      skip,
    )

    if (!chaptersStats[bookId]) {
      chaptersStats[bookId] = { [chapterId]: chapterStats }
    } else {
      chaptersStats[bookId][chapterId] = chapterStats
    }
  }

  return chaptersStats
}

async function updateChaptersStats(chaptersStats: ChapterStatsStorage) {
  for (const [key, val] of Object.entries(chaptersStats)) {
    await storage.chaptersStats.set(key, val)
  }
}

function recalculateBooksStats(chaptersStats: ChapterStatsStorage) {
  const booksStats: BooksStatsStorage = {}

  for (const [bookId, entries] of Object.entries(chaptersStats)) {
    const bookInfo = state.get.booksIndex.find(
      (bookInfo) => bookInfo.id === bookId,
    )

    const bookLength = bookInfo?.length ?? 0
    let lastTimestamp = 0
    let lastChapter: string = "000"
    const chaptersStatsVal: ChapterStats[] = []

    for (const [chapterId, chapterStats] of Object.entries(entries)) {
      if (chapterStats.timestamp > lastTimestamp) {
        lastTimestamp = chapterStats.timestamp
        lastChapter = chapterId
      }

      chaptersStatsVal.push(chapterStats)
    }

    const bookStats = summarizeBookStats(
      chaptersStatsVal,
      bookLength,
      lastChapter,
    )

    booksStats[bookId] = bookStats
  }

  return booksStats
}

async function updateBooksStats(booksStats: BooksStatsStorage) {
  for (const [key, val] of Object.entries(booksStats)) {
    storage.booksStats.set(key, val)
  }
}

function updateTopStats(pbStats: PBStats, stats: BasicStats, id: string) {
  if (stats.wpm.value > pbStats.wpm.value) {
    pbStats.wpm.value = stats.wpm.value
    pbStats.wpm.timestamp = stats.timestamp
    pbStats.wpm.id = id
  }

  if (stats.wpm.raw > pbStats.raw.value) {
    pbStats.raw.value = stats.wpm.raw
    pbStats.raw.timestamp = stats.timestamp
    pbStats.raw.id = id
  }

  if (stats.acc.value > pbStats.acc.value) {
    pbStats.acc.value = stats.acc.value
    pbStats.acc.timestamp = stats.timestamp
    pbStats.acc.id = id
  }

  if (stats.con.value > pbStats.con.value) {
    pbStats.con.value = stats.con.value
    pbStats.con.timestamp = stats.timestamp
    pbStats.con.id = id
  }
}

function recalculateParagraphsSummaries(
  paragraphsStats: ParagraphsStatsStorage,
) {
  const topParagraph = {
    wpm: { value: 0, timestamp: 0, id: "" },
    raw: { value: 0, timestamp: 0, id: "" },
    acc: { value: 0, timestamp: 0, id: "" },
    con: { value: 0, timestamp: 0, id: "" },
  }

  const flatParagraphsStats = Object.entries(paragraphsStats)
    .map(([chapterId, stats]) =>
      Object.entries(stats).map(([paragraphId, entry]) => {
        const id = `${chapterId}__${paragraphId}`

        return {
          id,
          day: getDailyDate(entry.timestamp),
          entry,
        }
      }),
    )
    .flat()
    .sort((a, b) => b.entry.timestamp - a.entry.timestamp)

  const recent = []

  const wpmVals = []
  const rawVals = []
  const wpmWeights = []
  const accVals = []
  const accWeights = []
  const conVals = []
  const conWeights = []

  const byDays: { [day: string]: TypedParagraphStats[] } = {}

  for (const item of flatParagraphsStats) {
    if (item.entry.type === "typed") {
      if (item.entry.length >= config.MIN_PB_PARAGRAPH_LENGTH) {
        updateTopStats(topParagraph, item.entry, item.id)
      }

      if (recent.length < 50) {
        recent.unshift({ id: item.id, ...item.entry })
      }

      wpmVals.push(item.entry.wpm.value)
      rawVals.push(item.entry.wpm.raw)
      wpmWeights.push(item.entry.wpm.weight)
      accVals.push(item.entry.acc.value)
      accWeights.push(item.entry.acc.weight)
      conVals.push(item.entry.con.value)
      conWeights.push(item.entry.con.weight)

      if (!byDays[item.day]) {
        byDays[item.day] = []
      }

      byDays[item.day].push(item.entry)
    }
  }

  const averageWpm = calculateStat(wpmVals, wpmWeights)
  const averageRaw = calculateStat(rawVals, wpmWeights)
  const averageAcc = calculateStat(accVals, accWeights)
  const averageCon = calculateStat(conVals, conWeights)

  const average: MainStats = {
    wpm: {
      value: averageWpm.value,
      raw: averageRaw.value,
      weight: averageWpm.weight,
    },
    acc: averageAcc,
    con: averageCon,
  }

  const daily: DailyStatsStorage = Object.fromEntries(
    Object.entries(byDays).map(([day, entries]) => [
      day,
      summarizeDailyStats(entries),
    ]),
  )

  return { topParagraph, recent, average, daily }
}

async function updateDailyStats(dailyStatsStorage: DailyStatsStorage) {
  for (const [key, val] of Object.entries(dailyStatsStorage)) {
    await storage.dailyStats.set(key, val)
  }
}

function recalculateTopChapter(stats: ChapterStatsStorage) {
  const topChapter = {
    wpm: { value: 0, timestamp: 0, id: "" },
    raw: { value: 0, timestamp: 0, id: "" },
    acc: { value: 0, timestamp: 0, id: "" },
    con: { value: 0, timestamp: 0, id: "" },
  }

  for (const [bookId, chaptersStats] of Object.entries(stats)) {
    for (const [chapterId, entry] of Object.entries(chaptersStats)) {
      if (entry.progress === 1 && entry.typed >= config.MIN_PB_CHAPTER_LENGTH) {
        const id = `${bookId}__${chapterId}`
        updateTopStats(topChapter, entry, id)
      }
    }
  }

  return topChapter
}

function recalculateTopBook(stats: BooksStatsStorage) {
  const topBook = {
    wpm: { value: 0, timestamp: 0, id: "" },
    raw: { value: 0, timestamp: 0, id: "" },
    acc: { value: 0, timestamp: 0, id: "" },
    con: { value: 0, timestamp: 0, id: "" },
  }

  for (const [bookId, entry] of Object.entries(stats)) {
    if (entry.progress === 1) {
      updateTopStats(topBook, entry, bookId)
    }
  }

  return topBook
}

async function updateStatsSummary(summary: StatsSummary) {
  return storage.general.set("statsSummary", summary)
}

async function restoreSave(data: ParagraphsStatsStorage) {
  const merged = await mergeSaveWithCurrent(data)
  await updateParagraphsStats(merged)
  const chaptersStats = await recalculateChaptersStats(merged)
  await updateChaptersStats(chaptersStats)
  const booksStats = recalculateBooksStats(chaptersStats)
  await updateBooksStats(booksStats)
  const summaries = recalculateParagraphsSummaries(merged)
  await updateDailyStats(summaries.daily)
  const topChapter = recalculateTopChapter(chaptersStats)
  const topBook = recalculateTopBook(booksStats)
  await updateStatsSummary({
    average: summaries.average,
    recent: summaries.recent,
    topParagraph: summaries.topParagraph,
    topChapter,
    topBook,
  })
}

export async function exportSave() {
  const data = await encodeSave()

  const compressed = await compress(data)
  const date = new Date().toISOString().slice(0, 10)

  const file = new File([compressed], `entertrained_${date}.save`, {
    type: "application/gzip",
  })

  download(file)
}

export async function importSave(file: File) {
  const blob = await decompress(file)
  const data = await decodeSave(blob)

  await restoreSave(data)
  await state.reload()
  notify({ title: "Imported successfully!", type: "ok", important: true })
}
