import "./QuickStats.css"
import { type Component, Show, createMemo, createResource } from "solid-js"
import StatsStrip from "../../common/stats-strip/StatsStrip"
import { summarizeChapterStats } from "../prompt/prompt-actions/summarizeStats"
import state from "../../../state/state"
import { formatNum, formatDuration } from "../../../utils/formatters"
import { getAccuracyThreshold } from "../../../actions/getAccuracyThreshold"
import storage from "../../../storage/storage"
import { getDailyDate } from "../../../actions/getDailyDate"
import config from "../../../config"
import { getConsistencyThreshold } from "../../../actions/getConsistencyThreshold"
import { getDailyProgress } from "../../../actions/getDailyProgress"

const QuickStats: Component = () => {
  const [dailyProgress] = createResource(async () => {
    const dailyStats = await storage.dailyStats.get(getDailyDate())

    return getDailyProgress(dailyStats)
  })

  const [books] = createResource(async () => {
    const keys = await storage.booksStats.keys()
    const entries = await Promise.all(
      keys.map((key) => storage.booksStats.get(key)),
    )

    return entries.map((entry, i) => ({ id: keys[i], ...entry }))
  })

  const typedAllTime = createMemo(() => {
    return books()?.reduce((sum, x) => sum + (x.typed ?? 0), 0) ?? 0
  })

  const recent = createMemo(() => {
    return summarizeChapterStats(state.get.statsSummary.recent, 0, false)
  })

  const statsNameSub = createMemo(() => {
    const len = state.get.statsSummary.recent.length
    return len === 1 ? "Last paragraph" : `Last ${len} paragraphs`
  })

  return (
    <Show when={state.get.statsSummary.recent.length > 0}>
      <section class="quick-stats">
        <h2>Quick Stats</h2>
        <StatsStrip
          data={[
            {
              name: "Pages Typed",
              nameSub: "All time",
              val: typedAllTime() / config.CHARACTERS_PER_PAGE,
              isPercent: false,
              precision: 0,
              tooltipVal: `${formatNum(
                typedAllTime() / 5,
                0,
              )} words, ${formatNum(typedAllTime(), 0)} characters`,
            },
            {
              name: "Speed",
              nameSub: statsNameSub(),
              val: recent().wpm.value ?? null,
              isPercent: false,
              precision: 2,
              unit: " wpm",
              tooltipTitle:
                "Words per minute, where one word equals five characters (including spaces)",
              tooltipVal: recent().wpm.raw
                ? `${formatNum(recent().wpm.raw ?? 0, 2)} raw`
                : undefined,
            },
            {
              name: "Accuracy",
              nameSub: statsNameSub(),
              val: recent().acc.value ?? null,
              isPercent: true,
              precision: 2,
              unit: " %",
              type: getAccuracyThreshold(recent().acc.value ?? 0),
              tooltipTitle: "Percentage of correct keystrokes",
              tooltipVal: "Target accuracy >= 98%",
            },
            {
              name: "Consistency",
              nameSub: statsNameSub(),
              val: recent().con.value ?? null,
              isPercent: true,
              precision: 2,
              unit: " %",
              type: getConsistencyThreshold(recent().con.value ?? 0),
              tooltipTitle: "Measures how consistent is you speed beteen words",
              tooltipVal: "Target consistency >= 80%",
            },
            {
              name: "Daily Goal",
              nameSub: "Type for 15 min",
              val: dailyProgress()?.progress ?? 0,
              isPercent: true,
              precision: 0,
              unit: " %",
              type: (dailyProgress()?.progress ?? 0) >= 1 ? "ok" : "warning",
              tooltipTitle: "Measures the completion of the daily goal",
              tooltipVal: `${formatDuration(
                dailyProgress()?.time ?? 0,
                true,
              )}, ${formatNum((dailyProgress()?.length ?? 0) / 5, 0)} words
              `,
              showZero: true,
            },
          ]}
          desktopColumns={5}
          mobileColumns={2}
        />
      </section>
    </Show>
  )
}

export default QuickStats
