import type { Component } from "solid-js"
import { toggleFavorite } from "../../../actions/toggleFavorite"
import { encodeParams } from "../../../utils/url"
import BookGenres from "../book-genres/BookGenres"
import BookStrip from "../book-strip/BookStrip"
import "./Book.css"
import Cover from "./Cover"

type Props = {
  coverUrl: string
  id: string
  title: string
  author: string
  description: string
  pages: number
  progress: number
  favorite: boolean
  isNew: boolean
  genres: string[]
  part?: number
}

const Book: Component<Props> = (props) => {
  return (
    <article
      classList={{
        book: true,
        done: props.progress === 1,
        "in-progress": props.progress > 0 && props.progress < 1,
      }}
      style={`--pages: ${props.pages}`}
    >
      <div class="cover-box">
        <Cover url={props.coverUrl} link={`/books/${props.id}`} />
      </div>
      <div class="scroll-box">
        <div class="info">
          <h3>
            <a href={`/books/${props.id}`}>{props.title}</a>
          </h3>
          <p class="author">
            by{" "}
            <a href={`/books?${encodeParams({ author: props.author })}`}>
              {props.author}
            </a>
          </p>
          <p class="description" title={props.description}>
            {props.description}
          </p>
          <BookGenres genres={props.genres} />
        </div>
      </div>
      <div class="top-box">
        <BookStrip
          pages={props.pages}
          progress={props.progress}
          isNew={props.isNew}
          part={props.part}
        />
        <button
          classList={{ favorite: true, active: props.favorite }}
          onClick={() => toggleFavorite(props.id)}
          title={props.favorite ? "Remove from favorites" : "Add to favorites"}
          type="button"
        >
          <i class={props.favorite ? "icon-heart" : "icon-heart-off"} />
        </button>
      </div>
    </article>
  )
}

export default Book
