import config from "../config"
import type { AverageStats } from "../types/common"

export function getDailyProgress(dailyStats: AverageStats | null) {
  return {
    progress: (dailyStats?.time ?? 0) / (1000 * 60 * config.DAILY_GOAL_MINUTES),
    length: dailyStats?.typed ?? 0,
    time: dailyStats?.time ?? 0,
  }
}
