import "./MobileMenu.css"
import { Component } from "solid-js"
import state from "../../../state/state"

type Props = {
  hidden: boolean
  close: () => void
}

const MobileMenu: Component<Props> = (props) => {
  return (
    <div class="mobile-menu" hidden={props.hidden}>
      <nav classList={{ profile: true }}>
        <ul>
          <li
            title="Toggle dark mode"
            onClick={() => {
              state.set(
                "settings",
                "ui",
                "darkmode",
                !state.get.settings.ui.darkmode,
              )
              props.close()
            }}
            classList={{ active: location.pathname === "/settings" }}
          >
            <i
              class={
                state.get.settings.ui.darkmode
                  ? "icon-dark-mode"
                  : "icon-light-mode"
              }
            />
          </li>
          <li title="Settings" class="settings-button">
            <a
              href="/settings"
              classList={{ active: location.pathname === "/settings" }}
              onClick={props.close}
            >
              <i class="icon-settings" />
            </a>
          </li>
          {/* <li title="Profile"> */}
          {/*   <a */}
          {/*     href="/profile" */}
          {/*     classList={{ active: location.pathname === "/profile" }} */}
          {/*     onClick={props.close} */}
          {/*   > */}
          {/*     <i class="icon-profile" /> <span>John Doe</span> */}
          {/*   </a> */}
          {/* </li> */}
        </ul>
      </nav>
      <nav classList={{ menu: true }}>
        <ul>
          <li>
            <a
              href="/"
              onClick={props.close}
              classList={{ active: location.pathname === "/" }}
            >
              <i class="icon-home" /> <span>Home</span>
            </a>
          </li>
          <li>
            <a
              href="/books"
              onClick={props.close}
              classList={{ active: location.pathname === "/books" }}
            >
              <i class="icon-books" /> <span>Books</span>
            </a>
          </li>
          <li>
            <a
              href="/stats"
              onClick={props.close}
              classList={{ active: location.pathname === "/stats" }}
            >
              <i class="icon-stats" /> <span>Stats</span>
            </a>
          </li>
        </ul>
      </nav>
    </div>
  )
}

export default MobileMenu
