import Chart, { type ChartConfiguration } from "chart.js/auto"
import config from "../../../../config"
import type { SimpleDataset } from "../../../../types/common"
import { formatNum } from "../../../../utils/formatters"
import { palettes } from "./chart-palettes.ts"

const sizes = {
  fontTick: config.IS_MOBILE ? 10 : 13,
  fontLabel: config.IS_MOBILE ? 11 : 14,
  pointRadius: config.IS_MOBILE ? 5 : 3,
  pointRadiusSmall: config.IS_MOBILE ? 2 : 2,
}

const fonts = {
  labels: "'Open Sans', sans-serif",
  ticks: "'Recursive Linear', monospace",
}

export class ActivityChart {
  #ctx: CanvasRenderingContext2D
  #dataLabel: string
  #chart: Chart | null = null
  #darkmode = true
  #data: {
    len: SimpleDataset
    time: SimpleDataset
    labels: (number | string)[]
  } = {
    len: [],
    time: [],
    labels: [],
  }

  constructor(ctx: CanvasRenderingContext2D, dataLabel: string) {
    this.#ctx = ctx
    this.#dataLabel = dataLabel
  }

  #prepareConfig(
    darkmode: boolean,
    labels: (number | string)[],
    len: number[],
    time: number[],
  ): ChartConfiguration {
    const paletteName = darkmode ? "dark" : "light"
    const colors = palettes[paletteName as keyof typeof palettes]

    return {
      type: "bar",
      data: {
        labels,
        datasets: [
          {
            label: "TIME",
            data: time.map((x) => x / (1000 * 60)),
            // borderColor: colors.accLine,
            backgroundColor: colors.accLine,
            borderWidth: 2,
            yAxisID: "y2",
            barThickness: "flex",
          },
          {
            label: "WORDS",
            data: len.map((x) => x / 5),
            // borderColor: colors.avgLine,
            backgroundColor: colors.avgLine,
            pointBackgroundColor: colors.avgLine,
            borderWidth: 2,
            yAxisID: "y1",
            pointRadius: 0,
            barThickness: "flex",
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        interaction: {
          intersect: false,
          mode: "index",
        },
        plugins: {
          legend: {
            display: false,
          },
          tooltip: {
            callbacks: {
              // @ts-ignore
              label: (context) => {
                const label = context.dataset.label
                const isTime = label === "TIME"
                const rawVal = context.parsed.y

                if (!label || !rawVal || label === "PB WPM") {
                  return false
                }

                const val = formatNum(context.parsed.y, 0)

                return ` ${label}: ${val}${isTime ? " min" : ""}`
              },
              title: (context: any) => {
                const label = context?.[0]?.label ?? ""
                return `At ${label}`
              },
            },
            usePointStyle: true,
            backgroundColor: colors.tooltipBg,
            bodyFont: {
              family: "Recursive Linear",
              size: sizes.fontLabel,
            },
            titleFont: {
              family: "Recursive Linear",
              size: sizes.fontLabel,
            },
            bodyColor: colors.tooltipFg,
            titleColor: colors.tooltipFg,
            titleMarginBottom: 12,
            borderWidth: 1,
            borderColor: colors.tooltipBorder,
            bodySpacing: 8,
            padding: 12,
            multiKeyBackground: "transparent",
          },
        },
        scales: {
          y1: {
            type: "linear",
            display: true,
            position: "right",
            beginAtZero: true,
            title: {
              display: true,
              text: "WORDS TYPED",
              color: colors.wpmLabel,
              font: {
                family: fonts.labels,
                size: sizes.fontLabel,
                weight: 600,
              },
            },
            ticks: {
              color: colors.wpmTick,
              font: {
                family: fonts.ticks,
                size: sizes.fontTick,
              },
            },
            grid: {
              color: colors.grid,
            },
          },
          y2: {
            type: "linear",
            display: true,
            position: "left",
            reverse: false,
            beginAtZero: true,
            title: {
              display: true,
              text: "TYPING TIME",
              color: colors.accLabel,
              font: {
                family: fonts.labels,
                size: sizes.fontLabel,
              },
            },
            ticks: {
              color: colors.accTick,
              font: {
                family: fonts.ticks,
                size: sizes.fontTick,
              },
              callback: (value) => value + " min",
            },
            grid: {
              display: false,
            },
          },
          x: {
            title: {
              display: true,
              text: this.#dataLabel,
              color: colors.mainLabel,
              font: {
                family: fonts.labels,
                size: sizes.fontLabel,
                weight: 600,
              },
            },
            ticks: {
              color: colors.mainTick,
              font: {
                family: fonts.ticks,
                size: sizes.fontTick,
              },
            },
            grid: {
              color: colors.grid,
            },
          },
        },
      },
    }
  }

  #prepareData(data: {
    len: SimpleDataset
    time: SimpleDataset
    labels: (number | string)[]
  }) {
    return { labels: data.labels, len: data.len, time: data.time }
  }

  toggleDarkmode(darkmode: boolean) {
    if (this.#darkmode === darkmode) {
      return
    }

    this.#darkmode = darkmode

    this.#reload()
  }

  update(
    data: {
      len: SimpleDataset
      time: SimpleDataset
      labels: (number | string)[]
    },
    dataLabel: string,
    _: any, // ignore third argument
    __: any, // ignore fourth argument
  ) {
    this.#data = data
    this.#dataLabel = dataLabel
    this.#reload()
  }

  #reload() {
    if (!this.#chart) {
      return
    }

    const { len, time, labels } = this.#prepareData(this.#data)

    const config = this.#prepareConfig(this.#darkmode, labels, len, time)

    this.#chart.data.datasets = config.data.datasets
    this.#chart.data.labels = config.data.labels
    // @ts-ignore
    this.#chart.options = config.options

    this.#chart.update()
  }

  load(
    darkmode: boolean,
    data: {
      len: SimpleDataset
      time: SimpleDataset
      labels: (number | string)[]
    },
  ) {
    this.#data = data
    this.#darkmode = darkmode

    const { len, time, labels } = this.#prepareData(data)

    this.#chart = new Chart(
      this.#ctx,
      this.#prepareConfig(darkmode, labels, len, time),
    )
  }

  destroy() {
    this.#chart?.destroy()
  }
}
