import { type Component, For } from "solid-js"
import { encodeParams } from "../../../utils/url"
import "./BookGenres.css"

type Props = {
  genres: string[]
  style?: string
}

const BookGenres: Component<Props> = (props) => {
  return (
    <ul class="book-genres" style={props.style}>
      <For each={props.genres}>
        {(genre) => (
          <li title="Show all books of this genre">
            <a href={"/books?" + encodeParams({ genre })}>{genre}</a>
          </li>
        )}
      </For>
    </ul>
  )
}

export default BookGenres
