import Chart, { type ChartConfiguration } from "chart.js/auto"
import { getAccuracyThreshold } from "../../../../actions/getAccuracyThreshold"
import config from "../../../../config"
import type { Dataset } from "../../../../types/common"
import { formatNum } from "../../../../utils/formatters"
import { calculateWeightedAverage } from "../../../../utils/math"
import { palettes } from "./chart-palettes.ts"

const sizes = {
  fontTick: config.IS_MOBILE ? 10 : 13,
  fontLabel: config.IS_MOBILE ? 11 : 14,
  pointRadius: config.IS_MOBILE ? 5 : 3,
  pointRadiusSmall: config.IS_MOBILE ? 2 : 2,
}

const fonts = {
  labels: "'Open Sans', sans-serif",
  ticks: "'Recursive Linear', monospace",
}

export class StatsChart {
  #ctx: CanvasRenderingContext2D
  #dataLabel: string
  #tooltipLabel: string | undefined
  #chart: Chart | null = null
  #darkmode = true
  #movingAverage = true
  #wpmPB = 0
  #data: {
    wpm: Dataset
    acc: Dataset
    con: Dataset
    labels: (number | string)[]
  } = {
    wpm: [],
    acc: [],
    con: [],
    labels: [],
  }

  constructor(
    ctx: CanvasRenderingContext2D,
    dataLabel: string,
    tooltipLabel?: string,
  ) {
    this.#ctx = ctx
    this.#dataLabel = dataLabel
    this.#tooltipLabel = tooltipLabel
  }

  #prepareConfig(
    darkmode: boolean,
    labels: (number | string)[],
    avg: number[],
    wpm: number[],
    acc: number[],
    con: number[],
  ): ChartConfiguration {
    const paletteName = darkmode ? "dark" : "light"
    const colors = palettes[paletteName as keyof typeof palettes]
    const drawPB = {
      id: "draw-pb",
      afterDatasetsDraw: (chart: Chart) => {
        const wpmPB = this.#wpmPB
        if (config.IS_MOBILE || wpmPB === 0) {
          return
        }

        const ctx = chart.ctx

        const meta = chart.getDatasetMeta(2)

        ctx.textBaseline = "middle"
        ctx.textAlign = "center"
        ctx.font = fonts.labels

        const text = `PB: ${wpmPB.toFixed(2)}`
        const padding = 4
        const { width } = ctx.measureText(text)

        ctx.fillStyle = colors.pbLabelBg
        const labelWidth = width + padding * 2
        const labelHeight = 24
        ctx.fillRect(
          ctx.canvas.width / 2 - labelWidth / 2,
          meta.data[0].y - labelHeight / 2,
          labelWidth,
          labelHeight,
        )

        ctx.fillStyle = colors.pbLabelFg
        ctx.fillText(text, ctx.canvas.width / 2, meta.data[0].y)
      },
    }

    return {
      type: "line",
      data: {
        labels,
        datasets: [
          {
            label: "AVG",
            data: avg,
            borderColor: colors.avgLine,
            pointBackgroundColor: colors.avgLine,
            borderWidth: 4,
            yAxisID: "y1",
            // @ts-ignore
            lineTension: 0.4,
            pointRadius: 0,
          },
          {
            label: "WPM",
            data: wpm,
            borderColor: colors.wpmLine,
            pointBackgroundColor: colors.wpmLine,
            borderWidth: 3,
            yAxisID: "y1",
            // @ts-ignore
            lineTension: 0.4,
            pointRadius: 0,
          },
          {
            label: "PB WPM",
            data: new Array(labels.length).fill(this.#wpmPB),
            borderColor: colors.pbLine,
            yAxisID: "y1",
            borderWidth: 1,
            pointRadius: 0,
            borderDash: [2, 2],
            hoverRadius: 0,
          },
          {
            label: "CON",
            data: con,
            borderColor: colors.accLine,
            pointBackgroundColor: colors.accLine,
            borderWidth: 1,
            yAxisID: "y2",
            // @ts-ignore
            lineTension: 0.4,
            pointRadius: 0,
            borderDash: [3, 3],
          },
          {
            label: "ACC",
            data: acc,
            borderColor: colors.accLine,
            borderWidth: 1,
            pointRadius: sizes.pointRadius,
            pointBorderWidth: 0,
            yAxisID: "y2",
            // @ts-ignore
            lineTension: 0.4,
            pointBackgroundColor: (context: any) => {
              const index = context.dataIndex
              const value = context.dataset.data[index]
              return colors[getAccuracyThreshold(value / 100)]
            },
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        interaction: {
          intersect: false,
          mode: "index",
        },
        plugins: {
          legend: {
            display: false,
          },
          tooltip: {
            callbacks: {
              // @ts-ignore
              label: (context) => {
                const label = context.dataset.label
                const isPercent = label === "ACC" || label === "CON"
                const rawVal = context.parsed.y

                if (!label || !rawVal || label === "PB WPM") {
                  return false
                }

                const val = formatNum(context.parsed.y, 1)

                return ` ${label}: ${val}${isPercent ? "%" : ""}`
              },
              title: (context: any) => {
                const label = context?.[0]?.label ?? ""

                const prefix = this.#movingAverage
                  ? `At ${
                      this.#tooltipLabel
                        ? this.#tooltipLabel.toLowerCase() + " "
                        : ""
                    }`
                  : this.#tooltipLabel
                    ? this.#tooltipLabel + " "
                    : ""
                return `${prefix}${label}`
              },
            },
            usePointStyle: true,
            backgroundColor: colors.tooltipBg,
            bodyFont: {
              family: "Recursive Linear",
              size: sizes.fontLabel,
            },
            titleFont: {
              family: "Recursive Linear",
              size: sizes.fontLabel,
            },
            bodyColor: colors.tooltipFg,
            titleColor: colors.tooltipFg,
            titleMarginBottom: 12,
            borderWidth: 1,
            borderColor: colors.tooltipBorder,
            bodySpacing: 8,
            padding: 12,
            multiKeyBackground: "transparent",
          },
        },
        scales: {
          y1: {
            type: "linear",
            display: true,
            position: "right",
            beginAtZero: true,
            title: {
              display: true,
              text: "WPM  +  AVERAGE WPM",
              color: colors.wpmLabel,
              font: {
                family: fonts.labels,
                size: sizes.fontLabel,
                weight: 600,
              },
            },
            ticks: {
              color: colors.wpmTick,
              font: {
                family: fonts.ticks,
                size: sizes.fontTick,
              },
            },
            grid: {
              color: colors.grid,
            },
          },
          y2: {
            type: "linear",
            display: true,
            position: "left",
            reverse: true,
            beginAtZero: true,
            title: {
              display: true,
              text: "ACCURACY  +  CONSISTENCY",
              color: colors.accLabel,
              font: {
                family: fonts.labels,
                size: sizes.fontLabel,
              },
            },
            ticks: {
              color: colors.accTick,
              font: {
                family: fonts.ticks,
                size: sizes.fontTick,
              },
              callback: (value) => value + "%",
            },
            grid: {
              display: false,
            },
          },
          x: {
            title: {
              display: true,
              text: this.#dataLabel,
              color: colors.mainLabel,
              font: {
                family: fonts.labels,
                size: sizes.fontLabel,
                weight: 600,
              },
            },
            ticks: {
              color: colors.mainTick,
              font: {
                family: fonts.ticks,
                size: sizes.fontTick,
              },
            },
            grid: {
              color: colors.grid,
            },
          },
        },
      },
      plugins: [drawPB],
    }
  }

  #prepareData(data: {
    wpm: Dataset
    acc: Dataset
    con: Dataset
    labels: (number | string)[]
  }) {
    const labels = data.labels
    const avg = this.#prepareMovingAverageStat(data.wpm)
    const wpm = data.wpm.map((x) => x.value)
    const acc = data.acc.map((x) => x.value * 100)
    const con = data.con.map((x) => x.value * 100)

    if (labels.length === 1) {
      return {
        labels: new Array(2).fill(labels[0]),
        avg: new Array(2).fill(avg[0]),
        wpm: new Array(2).fill(wpm[0]),
        acc: new Array(2).fill(acc[0]),
        con: new Array(2).fill(con[0]),
      }
    }

    return { labels, avg, wpm, acc, con }
  }

  #prepareMovingAverageStat(dataset: Dataset) {
    const values = dataset.map((x) => x.value)
    const weights = dataset.map((x) => x.weight ?? 0)

    const movingAverage = new Array(dataset.length)

    for (let i = 0; i < dataset.length; i++) {
      movingAverage[i] = calculateWeightedAverage(
        values.slice(0, i + 1),
        weights.slice(0, i + 1),
      )
    }

    return movingAverage
  }

  toggleDarkmode(darkmode: boolean) {
    if (this.#darkmode === darkmode) {
      return
    }

    this.#darkmode = darkmode

    this.#reload()
  }

  update(
    data: {
      wpm: Dataset
      acc: Dataset
      con: Dataset
      labels: (number | string)[]
    },
    dataLabel: string,
    tooltipLabel: string,
    wpmPB: number,
  ) {
    this.#data = data
    this.#dataLabel = dataLabel
    this.#tooltipLabel = tooltipLabel
    this.#wpmPB = wpmPB
    this.#reload()
  }

  #reload() {
    if (!this.#chart) {
      return
    }

    const { avg, wpm, acc, con, labels } = this.#prepareData(this.#data)

    const config = this.#prepareConfig(
      this.#darkmode,
      labels,
      avg,
      wpm,
      acc,
      con,
    )

    this.#chart.data.datasets = config.data.datasets
    this.#chart.data.labels = config.data.labels
    // @ts-ignore
    this.#chart.options = config.options

    this.#chart.update()
  }

  load(
    darkmode: boolean,
    wpmPB: number,
    data: {
      wpm: Dataset
      acc: Dataset
      con: Dataset
      labels: (number | string)[]
    },
    movingAverage: boolean,
  ) {
    this.#wpmPB = wpmPB
    this.#data = data
    this.#darkmode = darkmode
    this.#movingAverage = movingAverage

    const { avg, wpm, acc, con, labels } = this.#prepareData(data)

    this.#chart = new Chart(
      this.#ctx,
      this.#prepareConfig(darkmode, labels, avg, wpm, acc, con),
    )
  }

  destroy() {
    this.#chart?.destroy()
  }
}
