import config from "../config"
import type { BooksLiveIndex } from "../types/common"
import { isNew } from "../utils/dates"

export function getBooksData(booksIndex: BooksLiveIndex) {
  return booksIndex.map((entry) => ({
    id: entry.id,
    title: entry.title ?? "No Title",
    titleAlpha: entry.titleAlpha,
    author: entry.author ?? "Unknown",
    year: entry.year,
    createdAt: entry.createdAt,
    pages: Math.ceil(entry.length / config.CHARACTERS_PER_PAGE),
    description: entry.description ?? "No description",
    genres: entry.genres,
    coverUrl: `/books/${entry.id}/cover${
      devicePixelRatio > 1 ? "" : "-small"
    }.min.webp`,
    coverBigUrl: `/books/${entry.id}/cover.min.webp`,
    progress: entry.progress,
    favorite: entry.favorite,
    series: entry.series,
    part: entry.part,
    isNew: isNew(entry.createdAt),
  }))
}

export type BookData = ReturnType<typeof getBooksData>[number]
