import latinize from "latinize"
import { shouldIgnore, symbolReplacements } from "./charsets"

type Options = {
  replaceLetters: boolean
  ignoreUnknownChars: boolean
}

const defaultOptions: Options = {
  replaceLetters: false,
  ignoreUnknownChars: false,
}

export function cleanSymbols(text: string) {
  const cleaned: string[] = []

  for (const char of text) {
    const subSymbol = symbolReplacements[char]
    cleaned.push(subSymbol ?? char)
  }

  return cleaned.join("")
}

export const cleanText =
  (charset: Set<string>) =>
  (text: string, options: Partial<Options> = {}) => {
    const cfg = { ...defaultOptions, ...options }

    const base = text
      .replace(/\n+/g, " ")
      .replace(/\t+/g, " ")
      .replace(/^[* ]+$/, "***")
      .replace(/\s*\. \. \./g, "...")
      .replace(/\s--\s/g, " - ")
      .replace(/[,.?!][A-Z]/g, (v) => `${v[0]} ${v[1]}`) // fix no space between end of sentence and new sentence
      .replace(/\s[,.?!]/g, (v) => v[1]) // remove space before interpunction
      .trim()

    const cleaned: string[] = []

    for (const char of base) {
      if (charset.has(char)) {
        cleaned.push(char)
      } else if (!shouldIgnore(char)) {
        const subSymbol = symbolReplacements[char]
        const subLetter = latinize(char)

        cleaned.push(
          subSymbol ?? subLetter ?? (cfg.ignoreUnknownChars ? "" : char),
        )
      }
    }

    return cleaned
      .join("")
      .replace(/:\s-\s$/, ":")
      .replace(/\s+/g, " ")
      .trim()
  }
