import { useParams } from "@solidjs/router"
import { Component, createResource, Show } from "solid-js"
import { getBookInfo } from "../../../io/getBookInfo"
import { getAdditionalBookInfo } from "../../../io/getAdditionalBookInfo"
import { getBookCorrections } from "../../../io/getBookCorrections"
import Loading from "../../common/loading/Loading"

const BookEdit: Component = () => {
  const params = useParams()

  const [baseInfo] = createResource(async () => {
    return getBookInfo(params.id)
  })

  const [extraInfo] = createResource(baseInfo, () => {
    const info = baseInfo()
    return info ? getAdditionalBookInfo(info.title, info.author) : null
  })
  const [corrections] = createResource(baseInfo, () => {
    const info = baseInfo()
    return info ? getBookCorrections(info.id) : null
  })

  return (
    <Show when={baseInfo()} fallback={<Loading />}>
      <section class="edit">
        <pre>{JSON.stringify(baseInfo(), null, 2)}</pre>
        <pre>{JSON.stringify(extraInfo(), null, 2)}</pre>
        <pre>{JSON.stringify(corrections(), null, 2)}</pre>
      </section>
    </Show>
  )
}

export default BookEdit
