import { SetStoreFunction } from "solid-js/store"
import { LocalState } from "../types"
import { handleStandardInput } from "./handleStandardInput"

const delay = (ms: number) => new Promise((resolve) => setTimeout(resolve, ms))

type Options = {
  wpm: number
  delayBetweenParagraphs: number
  randomize: boolean
  emitEvents: boolean
}

const defaultOptions: Options = {
  wpm: 100,
  delayBetweenParagraphs: 200,
  randomize: false,
  emitEvents: false,
}

export async function simulateTyping(
  text: string,
  local: LocalState,
  setLocal: SetStoreFunction<LocalState>,
  options: Partial<Options> = {},
) {
  if (!text) {
    return
  }

  const { wpm, delayBetweenParagraphs, randomize, emitEvents } = {
    ...defaultOptions,
    ...options,
  }

  const lines = text.split(/\n+/).map((p) => [...p.split(""), "Enter"])
  const averageKeyDelay = 1000 / ((wpm * 5) / 60) - 2

  for (const line of lines) {
    const events = line.map((key) => new KeyboardEvent("keydown", { key }))

    for (let i = 0; i < events.length; i++) {
      const keyDelay = randomize
        ? averageKeyDelay * (Math.random() + 0.5)
        : averageKeyDelay

      await delay(keyDelay)
      if (emitEvents) {
        document.dispatchEvent(events[i])
      } else {
        handleStandardInput(events[i], local, setLocal)
      }
    }

    await delay(delayBetweenParagraphs)
  }
}
