import { useSearchParams } from "@solidjs/router"
import { type Component, For, Show, createMemo, onMount } from "solid-js"
import state from "../../../state/state"
import type { SortBy } from "../../../types/common"
import { debounce } from "../../../utils/debounce"
import ButtonToggle from "../../common/button-toggle/ButtonToggle"
import Select from "../../common/select/Select"
import "./Filters.css"
import config from "../../../config"

const setSearch = debounce(
  (
    val: string,
    setSearchParams: (x: { [key: string]: string | null }) => void,
  ) => {
    const phrase = val.toLowerCase().trim()
    setSearchParams({ search: phrase, page: null })
  },
  300,
)

function setOrderBy(
  val: string,
  setSearchParams: (x: { [key: string]: string | null }) => void,
) {
  state.set("settings", "ui", "sortBy", val as SortBy)
  setSearchParams({ order: val, page: null })
}

const Filters: Component = () => {
  const [searchParams, setSearchParams] = useSearchParams()
  const sortBy = createMemo(
    () => searchParams.order ?? state.get.settings.ui.sortBy,
  )
  const criteria = createMemo(() => {
    const items = []
    if (searchParams.author) {
      items.push({
        category: "Author",
        value: searchParams.author,
        onClick: () => setSearchParams({ author: null, page: null }),
      })
    }

    if (searchParams.series) {
      items.push({
        category: "Series",
        value: searchParams.series,
        onClick: () => setSearchParams({ series: null, page: null }),
      })
    }

    if (searchParams.genre) {
      items.push({
        category: "Genre",
        value: searchParams.genre,
        onClick: () => setSearchParams({ genre: null, page: null }),
      })
    }

    if (searchParams.type) {
      items.push({
        category: "Type",
        value: searchParams.type,
        onClick: () => setSearchParams({ type: null, page: null }),
      })
    }

    if (searchParams.search) {
      items.push({
        category: "Search",
        value: searchParams.search,
        onClick: () => setSearchParams({ search: null, page: null }),
      })
    }

    return items
  })

  let searchRef: HTMLInputElement | undefined
  onMount(() => {
    if (!searchRef || config.IS_MOBILE) {
      return
    }

    searchRef.focus()
  })

  return (
    <nav class="filters">
      <div class="tabs">
        <label>
          <span>Show </span>
          <ButtonToggle
            joined={true}
            items={[
              { name: "All", value: "all" },
              { name: "Favorite", value: "favorites" },
              { name: "In progress", value: "in-progress" },
              { name: "Done", value: "done" },
            ]}
            active={searchParams.tab ?? "all"}
            setValue={(val: string) =>
              setSearchParams({
                tab: val === "all" ? null : val,
                page: null,
                author: null,
                genre: null,
                type: null,
                search: null,
                series: null,
              })
            }
          />
        </label>
      </div>
      <div class="criteria">
        <Show when={criteria().length > 0}>
          <label>
            <span>Selected criteria</span>
            <div>
              <ul>
                <For each={criteria()}>
                  {(item) => (
                    <li onClick={item.onClick} title="Remove criteria">
                      {item.category}: <strong>{item.value}</strong>{" "}
                      <i class="icon-close" />
                    </li>
                  )}
                </For>
                <Show when={criteria().length > 1}>
                  <li
                    class="clear"
                    title="Remove all criteria"
                    onClick={() => {
                      setSearchParams({
                        author: null,
                        genre: null,
                        type: null,
                        search: null,
                        series: null,
                        page: null,
                      })
                    }}
                  >
                    CLEAR <i class="icon-close"></i>
                  </li>
                </Show>
              </ul>
            </div>
          </label>
        </Show>
      </div>
      <div class="options">
        <label>
          <span>Order by </span>
          <Select onChange={(e) => setOrderBy(e.target.value, setSearchParams)}>
            <Show when={searchParams.series}>
              <option value="author" selected={sortBy() === "part"}>
                Part
              </option>
            </Show>
            <option value="author" selected={sortBy() === "author"}>
              Author
            </option>
            <option value="title" selected={sortBy() === "title"}>
              Title
            </option>
            <option value="length" selected={sortBy() === "length"}>
              Length
            </option>
            {/* <option value="year" selected={current() === "year"}> */}
            {/*   Year */}
            {/* </option> */}
            <option value="added" selected={sortBy() === "added"}>
              Date added
            </option>
            <option value="random" selected={sortBy() === "random"}>
              Random
            </option>
          </Select>
        </label>
        <label>
          <span>Search </span>
          <input
            type="search"
            placeholder="Title, author or genre..."
            onInput={(e) => setSearch(e.target.value ?? "", setSearchParams)}
            value={searchParams.search ?? ""}
            ref={searchRef}
          />
        </label>
      </div>
    </nav>
  )
}

export default Filters
