import { type Component, Show } from "solid-js"
import { formatNum, formatPercentageNice } from "../../../utils/formatters"
import { encodeParams } from "../../../utils/url"
import "./BookStrip.css"
import config from "../../../config"

type Props = {
  pages: number
  progress: number
  isNew: boolean
  eta?: string
  wpm?: number
  part?: number
  style?: string
}

const BookStrip: Component<Props> = (props) => {
  return (
    <ul class="book-strip" style={props.style}>
      <Show when={props.part}>
        <li>
          <span>#{props.part}</span>
        </li>
      </Show>
      <li>
        <span
          title={`Page is equal to ${formatNum(
            config.CHARACTERS_PER_PAGE,
          )} characters (${formatNum(config.CHARACTERS_PER_PAGE / 5)} words)`}
        >
          {props.pages} page{props.pages !== 1 ? "s" : ""}
        </span>
      </li>
      <Show when={props.eta}>
        <li
          title={`Remaining typing time, estimated based on your recent typing speed (${formatNum(
            props.wpm ?? 60,
          )} wpm)`}
        >
          <span>~{props.eta}</span>
        </li>
      </Show>
      <Show when={props.isNew}>
        <li class="new" title="Show all recently added">
          <a href={"/books?" + encodeParams({ type: "new" })}>New!</a>
        </li>
      </Show>
      <Show when={props.progress === 1}>
        <li class="done" title="Show all finished">
          <a href={"/books?" + encodeParams({ tab: "done" })}>Done!</a>
        </li>
      </Show>
      <Show when={props.progress > 0 && props.progress < 1}>
        <li class="in-progress" title="Show all in progress">
          <a href={"/books?" + encodeParams({ tab: "in-progress" })}>
            {formatPercentageNice(props.progress)}% done
          </a>
        </li>
      </Show>
    </ul>
  )
}

export default BookStrip
