import { Epub } from "./libs/ebook/epub.ts"
import { serializeBook } from "./libs/ebook/serialize.ts"

async function getRawEpubPaths() {
  const res = await fetch("/raw-books/book-paths.json")
  return res.json() as Promise<string[]>
}

type Options = {
  limit: number
  onlyNew: boolean
}

const defaults = {
  limit: Infinity,
  onlyNew: false,
}

