async function prepareBooks(options: Partial<Options>) {
  const { limit } = { ...defaults, ...options }
  const bookPaths = (await getRawEpubPaths()).slice(0, limit)
  // .filter((x) => x.includes("little-fuzzy"))

  for (const bookPath of bookPaths) {
    try {
      console.log(bookPath)
      const res = await fetch(`/${bookPath}`, {
        cache: "no-cache",
      })

      if (!res.ok) {
        console.error("Cannot fetch")
        console.error(bookPath)
        continue
      }

      const data = await res.blob()
      const book = new Epub(data)
      const content = await book.load()

      if (content === null) {
        console.log("No content!")
        continue
      }

      const { all, cover } = serializeBook(content)

      // if (all.genres.includes("poetry") || all.genres.includes("drama")) {
      //   console.warn("Skipping drama and poetry")
      //   continue
      // }

      {
        const res = await fetch("http://localhost:1234", {
          method: "POST",
          body: JSON.stringify(all),
        })

        if (res.ok) {
          // console.log("Done info:", all.id)
        }
      }

      if (cover) {
        const headers = new Headers()
        headers.append("Content-Type", "image/png")
        headers.append("Content-Length", cover.size.toString())

        const res = await fetch(`http://localhost:1234/cover/${all.id}`, {
          method: "POST",
          body: cover,
          headers,
        })

        if (res.ok) {
          // console.log("Done cover:", all.id)
        }
      }
    } catch (e) {
      console.error(e)
      console.error("Path:", bookPath)
    }
    // console.log("---------------------------------------------")
  }

  console.log("Done!")
}

export default prepareBooks
