const defaultSession = {
  dailyDone: true,
  daily: {
    wpm: {
      value: 0,
      raw: 0,
      weight: 0,
    },
    acc: {
      value: 0,
      weight: 0,
    },
    con: {
      value: 0,
      weight: 0,
    },
    time: 0,
    timestamp: 0,
    typed: 0,
    read: 0,
  },
  notifications: [],
}

const defaultState: State = {
  loaded: false,
  statsSummary: structuredClone(defaultSummary),
  settings: structuredClone(defaultSettings),
  booksIndex: [],
  session: defaultSession,
}

async function loadSavedData(state: State, setState: SetStoreFunction<State>) {
  const settings = await storage.general.get("settings")
  const statsSummary = await storage.general.get("statsSummary")

  // daily
  const dailyStats = await storage.dailyStats.get(getDailyDate())

  if (dailyStats) {
    setState("session", "daily", dailyStats)
    const info = getDailyProgress(dailyStats)
    setState("session", "dailyDone", info.progress >= 1)
  }

  if (settings) {
    setState("settings", merge(defaultSettings, settings))
  }

  if (statsSummary) {
    setState("statsSummary", statsSummary)
  }

  const lastBooksIndexUpdate = await getBooksIndexLastUpdate()
  const savedBooksIndex = await storage.general.get("booksIndex")

  let booksIndex: null | BooksIndex = null

  if (!savedBooksIndex || lastBooksIndexUpdate > savedBooksIndex.lastUpdate) {
    const newBooksIndex = await getBooksIndex()

    if (newBooksIndex) {
      booksIndex = newBooksIndex
    }
  } else {
    booksIndex = savedBooksIndex
  }

  if (booksIndex) {
    const booksWithStats = new Set(await storage.booksStats.keys())
    const favorites = new Set((await storage.general.get("favorites")) ?? [])

    const booksLiveIndex: BooksLiveIndex = []

    for (const book of booksIndex.books) {
      let progress = 0
      let timestamp = 0
      const favorite = favorites.has(book.id)

      if (booksWithStats.has(book.id)) {
        const stats = await storage.booksStats.get(book.id)
        progress = stats?.progress ?? 0
        timestamp = stats?.timestamp ?? 0
      }

      booksLiveIndex.push({
        ...book,
        progress,
        favorite,
        timestamp,
      })
    }

    setState("booksIndex", booksLiveIndex)
    await storage.general.set("booksIndex", booksIndex)

    if (
      state.settings.ui.showHero &&
      (state.booksIndex.find((book) => book.id === "entertrained--first-steps")
        ?.progress ?? 0) > 0
    ) {
      setState("settings", "ui", "showHero", false)
    }
  }

  setState("loaded", true)
}

const state = createRoot(() => {
  const [state, setState] = createStore(defaultState)

  loadSavedData(state, setState)

  const settingsToWatch = (
    Object.keys(state.settings) as (keyof typeof state.settings)[]
  )
    .map((mainKey) =>
      Object.keys(state.settings[mainKey]).map((key) => {
        return () => (state.settings[mainKey] as { [key: string]: any })[key]
      }),
    )
    .flat()

  createEffect(
    on(settingsToWatch, () => {
      if (state.loaded) {
        const settings = { ...unwrap(state.settings) }
        storage.general.set("settings", settings)
      }
    }),
  )

  createEffect(() => {
    if (state.settings.ui.darkmode) {
      document.body.classList.add("dark")
    } else {
      document.body.classList.remove("dark")
    }
  })

  return {
    get: state,
    set: setState,
    reload: () => loadSavedData(state, setState),
  }
})

export default state

