import "./LongTermStats.css"
import {
  Component,
  createMemo,
  createResource,
  createSignal,
  Show,
} from "solid-js"
import state from "../../../state/state"
import storage from "../../../storage/storage"
import Chart from "../results/Chart"
import { formatDuration, formatNum } from "../../../utils/formatters"
import StatsStrip from "../../common/stats-strip/StatsStrip"
import { getAccuracyThreshold } from "../../../actions/getAccuracyThreshold"
import { getConsistencyThreshold } from "../../../actions/getConsistencyThreshold"
import { getDailyProgress } from "../../../actions/getDailyProgress"
import { AverageStats } from "../../../types/common"
import SmallButtonToggle from "../../common/small-button-toggle/SmallButtonToggle"
import { summarizeAverageStats } from "../prompt/prompt-actions/summarizeStats"
import config from "../../../config"

function getMonthlyStats(
  entries: {
    label: string
    data: AverageStats | null
  }[],
) {
  const monthlyEntries: { [key: string]: AverageStats[] } = {}

  for (const entry of entries) {
    if (entry.data) {
      const label = entry.label.slice(0, 7)
      if (!monthlyEntries[label]) {
        monthlyEntries[label] = []
      }

      monthlyEntries[label].push(entry.data)
    }
  }

  return Object.entries(monthlyEntries)
    .map(([label, entries]) => ({
      label,
      data: summarizeAverageStats(entries),
    }))
    .sort((a, b) => a.label.localeCompare(b.label))
}

const LongTermStats: Component = () => {
  const [tab, setTab] = createSignal("daily")

  const [dailyData] = createResource(async () => {
    const labels = (await storage.dailyStats.keys()).sort()

    const data = await Promise.all(
      labels.map((label) => storage.dailyStats.get(label)),
    )

    return data.map((entry, i) => ({ label: labels[i], data: entry }))
  })

  const [books] = createResource(async () => {
    const keys = await storage.booksStats.keys()
    const entries = await Promise.all(
      keys.map((key) => storage.booksStats.get(key)),
    )

    return entries.map((entry, i) => ({ id: keys[i], ...entry }))
  })

  const [chartData, { refetch }] = createResource(dailyData, (daily) => {
    const entries =
      tab() === "daily" ? daily.slice(-30) : getMonthlyStats(daily)
    const size = entries.length

    if (size === 0) {
      return undefined
    }

    const labels = entries.map((entry) => entry.label)

    const wpm = new Array(size)
    const acc = new Array(size)
    const con = new Array(size)
    const len = new Array(size)
    const time = new Array(size)

    for (let i = 0; i < size; i++) {
      wpm[i] = {
        value: entries[i].data?.wpm.value,
        weight: entries[i].data?.wpm.weight,
      }
      acc[i] = {
        value: entries[i].data?.acc.value,
        weight: entries[i].data?.acc.weight,
      }
      con[i] = {
        value: entries[i].data?.con.value,
        weight: entries[i].data?.con.weight,
      }
      len[i] = entries[i].data?.typed ?? 0
      time[i] = entries[i].data?.time ?? 0
    }

    return {
      stats: { labels, wpm, acc, con },
      activity: { labels, len, time },
    }
  })

  const [dailyAverageProgress] = createResource(dailyData, (daily) => {
    const progressByDays = daily.map(
      (entry) => getDailyProgress(entry.data).progress,
    )

    return progressByDays.length > 0
      ? progressByDays.reduce((sum, x) => sum + x, 0) / progressByDays.length
      : 0
  })

  const average = createMemo(() => {
    return state.get.statsSummary.average
  })

  const progressData = createMemo(() => {
    let booksCompleted = 0
    let charactersTyped = 0
    let charactersRead = 0
    let typingTime = 0

    for (const book of books() ?? []) {
      if (book.progress === 1) {
        booksCompleted++
      }
      charactersTyped += book.typed ?? 0
      charactersRead += book.read ?? 0
      typingTime += book.time ?? 0
    }

    const charactersTotal = charactersTyped + charactersRead

    return [
      {
        name: "Books Completed",
        nameSub: "All time",
        val: booksCompleted ?? null,
        isPercent: false,
        precision: 0,
        unit: "",
        showZero: true,
      },
      {
        name: "Total Pages",
        nameSub: "All time",
        val: charactersTotal / config.CHARACTERS_PER_PAGE,
        isPercent: false,
        precision: 0,
        unit: "",
        tooltipVal: `${formatNum(charactersTotal / 5, 0)} words`,
        showZero: true,
      },
      {
        name: "Pages Typed",
        nameSub: "All time",
        val: charactersTyped / config.CHARACTERS_PER_PAGE,
        isPercent: false,
        precision: 0,
        unit: "",
        tooltipVal: `${formatNum(charactersTyped / 5, 0)} words`,
        showZero: true,
      },
      {
        name: "Typing Time",
        nameSub: "All time",
        val: formatDuration(typingTime),
        isPercent: false,
        precision: 0,
        unit: "",
        tooltipVal: "hours : minutes : seconds",
      },
    ]
  })

  return (
    <section class="long-term-stats">
      <h2>Long-Term Stats</h2>
      <StatsStrip data={progressData()} desktopColumns={4} mobileColumns={2} />
      <StatsStrip
        data={[
          {
            name: "Speed",
            nameSub: "All time average",
            val: average().wpm.value ?? null,
            isPercent: false,
            precision: 2,
            unit: " wpm",
            tooltipTitle:
              "Words per minute, where one word equals five characters (including spaces)",
            tooltipVal: average().wpm.raw
              ? `${formatNum(average().wpm.raw ?? 0, 2)} raw`
              : undefined,
          },
          {
            name: "Accuracy",
            nameSub: "All time average",
            val: average().acc.value ?? null,
            isPercent: true,
            precision: 2,
            unit: " %",
            type: getAccuracyThreshold(average().acc.value ?? 0),
            tooltipTitle: "Percentage of correct keystrokes",
            tooltipVal: "Target accuracy >= 98%",
          },
          {
            name: "Consistency",
            nameSub: "All time average",
            val: average().con.value ?? null,
            isPercent: true,
            precision: 2,
            unit: " %",
            type: getConsistencyThreshold(average().con.value ?? 0),
            tooltipTitle: "Measures how consistent is you speed beteen words",
            tooltipVal: "Target consistency >= 80%",
          },
          {
            name: "Daily Goal",
            nameSub: "Average completion",
            val: dailyAverageProgress() ?? 0,
            isPercent: true,
            precision: 0,
            unit: " %",
            type: (dailyAverageProgress() ?? 0) >= 1 ? "ok" : "warning",
            tooltipTitle: "Measures the average completion of the daily goal",
          },
        ]}
        desktopColumns={4}
        mobileColumns={2}
      />
      <hr />
      <Show when={chartData()}>
        <SmallButtonToggle
          class="tabs-toggle"
          items={[
            {
              name: "Daily",
              value: "daily",
            },
            {
              name: "Monthly",
              value: "monthly",
            },
          ]}
          joined={true}
          active={tab()}
          setValue={(val) => {
            setTab(val)
            refetch()
          }}
        />
        <Chart
          data={chartData()!.stats}
          dataLabel={tab() === "daily" ? "DAY" : "MONTH"}
          wpmPB={0}
        />
        <Chart
          type="activity"
          data={chartData()!.activity}
          dataLabel={tab() === "daily" ? "DAY" : "MONTH"}
          wpmPB={0}
        />
      </Show>
    </section>
  )
}

export default LongTermStats
