import storage from "../storage/storage"
import { getBookContent } from "./getBookContent"

export async function getChapterContent(bookId: string, chapterId: string) {
  const id = `${bookId}__${chapterId}`
  let chapterContent = await storage.booksContent.get(id)

  if (!chapterContent) {
    const chapters = await getBookContent(bookId)
    chapterContent = chapters[chapterId]
  }

  return (chapterContent ?? "")
    .trim()
    .split("\n")
    .map((p) => p + "⏎")
}
