import { produce, unwrap } from "solid-js/store"
import config from "../../../../config"
import state from "../../../../state/state"
import storage from "../../../../storage/storage"
import type {
  BookStats,
  ChapterStats,
  FinishedParagraphStats,
} from "../../../../types/common"
import { mergeAverageStats, mergeMainStats } from "./summarizeStats"
import { getDailyDate } from "../../../../actions/getDailyDate"

export async function saveSummary(
  paragraphStats: FinishedParagraphStats,
  chapterStats: ChapterStats,
  bookStats: BookStats,
  path: string,
  bookId: string,
  paragraphNum: number,
) {
  const timestamp = paragraphStats.timestamp
  const paragraphId = `${path}__${paragraphNum}`
  const date = getDailyDate()
  const dailyStats = await storage.dailyStats.get(date)

  state.set(
    "statsSummary",
    produce((summary) => {
      if (
        paragraphStats.type === "typed" &&
        paragraphStats.length >= config.MIN_PB_PARAGRAPH_LENGTH
      ) {
        if (paragraphStats.wpm.value > summary.topParagraph.wpm.value) {
          summary.topParagraph.wpm.value = paragraphStats.wpm.value
          summary.topParagraph.wpm.timestamp = timestamp
          summary.topParagraph.wpm.id = paragraphId
        }

        if (paragraphStats.wpm.raw > summary.topParagraph.raw.value) {
          summary.topParagraph.raw.value = paragraphStats.wpm.raw
          summary.topParagraph.raw.timestamp = timestamp
          summary.topParagraph.raw.id = paragraphId
        }

        if (paragraphStats.acc.value > summary.topParagraph.acc.value) {
          summary.topParagraph.acc.value = paragraphStats.acc.value
          summary.topParagraph.acc.timestamp = timestamp
          summary.topParagraph.acc.id = paragraphId
        }

        if (paragraphStats.con.value > summary.topParagraph.con.value) {
          summary.topParagraph.con.value = paragraphStats.con.value
          summary.topParagraph.con.timestamp = timestamp
          summary.topParagraph.con.id = paragraphId
        }
      }

      if (paragraphStats.type === "typed") {
        const sameIdIndex = summary.recent.findIndex(
          (stats) => stats.id === paragraphId,
        )

        if (sameIdIndex !== -1) {
          summary.recent[sameIdIndex] = {
            id: paragraphId,
            ...paragraphStats,
          }
        } else {
          summary.recent.push({
            id: paragraphId,
            ...paragraphStats,
          })
        }

        if (summary.recent.length > config.MAX_RECENT_PARAGRAPHS) {
          summary.recent.shift()
        }

        const dailyData = mergeAverageStats(
          paragraphStats,
          dailyStats ?? undefined,
        )
        storage.dailyStats.set(date, dailyData)
        state.set("session", "daily", dailyData)
      }

      summary.average = mergeMainStats(paragraphStats, summary.average)

      if (
        chapterStats.progress === 1 &&
        chapterStats.typed > config.MIN_PB_CHAPTER_LENGTH
      ) {
        if (chapterStats.wpm.value > summary.topChapter.wpm.value) {
          summary.topChapter.wpm.value = chapterStats.wpm.value
          summary.topChapter.wpm.timestamp = timestamp
          summary.topChapter.wpm.id = path
        }

        if (chapterStats.wpm.raw > summary.topChapter.raw.value) {
          summary.topChapter.raw.value = chapterStats.wpm.raw
          summary.topChapter.raw.timestamp = timestamp
          summary.topChapter.raw.id = path
        }

        if (chapterStats.acc.value > summary.topChapter.acc.value) {
          summary.topChapter.acc.value = chapterStats.acc.value
          summary.topChapter.acc.timestamp = timestamp
          summary.topChapter.acc.id = path
        }

        if (chapterStats.con.value > summary.topChapter.con.value) {
          summary.topChapter.con.value = chapterStats.con.value
          summary.topChapter.con.timestamp = timestamp
          summary.topChapter.con.id = path
        }
      }

      if (bookStats.progress === 1) {
        if (bookStats.wpm.value > summary.topBook.wpm.value) {
          summary.topBook.wpm.value = bookStats.wpm.value
          summary.topBook.wpm.timestamp = timestamp
          summary.topBook.wpm.id = bookId
        }

        if (bookStats.wpm.raw > summary.topBook.raw.value) {
          summary.topBook.raw.value = bookStats.wpm.raw
          summary.topBook.raw.timestamp = timestamp
          summary.topBook.raw.id = bookId
        }

        if (bookStats.acc.value > summary.topBook.acc.value) {
          summary.topBook.acc.value = bookStats.acc.value
          summary.topBook.acc.timestamp = timestamp
          summary.topBook.acc.id = bookId
        }

        if (bookStats.con.value > summary.topBook.con.value) {
          summary.topBook.con.value = bookStats.con.value
          summary.topBook.con.timestamp = timestamp
          summary.topBook.con.id = bookId
        }
      }
    }),
  )

  await storage.general.set("statsSummary", unwrap(state.get.statsSummary))
}
