import { getChapterContent } from "../../../../actions/getChapterContent.ts"
import { getBookInfo } from "../../../../io/getBookInfo.ts"
import storage from "../../../../storage/storage.ts"
import { StaticBookInfo } from "../../../../types/common.ts"
import type { PromptData } from "../types.ts"

export async function getPromptData(id: string): Promise<PromptData> {
  const [bookId, chapterId] = id.split("__")

  const info = (await getBookInfo(bookId)) as StaticBookInfo
  const paragraphs = await getChapterContent(bookId, chapterId)
  const savedParagraphsStats = (await storage.paragraphsStats.get(id)) ?? {}
  const savedChaptersStats = (await storage.chaptersStats.get(bookId)) ?? {}
  const chapterProgress = savedChaptersStats[chapterId]?.progress ?? 0

  return {
    bookInfo: {
      id: bookId,
      language: info.language,
      title: info.title,
    },
    chapterInfo: info.chapters.find((chapter) => chapter.id === chapterId),
    chapterProgress,
    paragraphs,
    stats: savedParagraphsStats,
  }
}
