import { produce, type SetStoreFunction } from "solid-js/store"
import state from "../../../../state/state"
import type { LocalState } from "../types"
import { handleParagraphClick } from "./handleParagraphClick"
import { handleAfk } from "./handleAfk"

export function handleBackspace(
  e: KeyboardEvent,
  local: LocalState,
  setLocal: SetStoreFunction<LocalState>,
) {
  const isFirstParagraph = local.paragraphNum === 0
  const isFirstWord = local.wordNum === 0
  const isFirstChar = local.charNum === 0

  if (isFirstParagraph && isFirstWord && isFirstChar) {
    return
  }

  handleAfk(setLocal)

  const mod = e.ctrlKey || e.metaKey

  if (
    (mod && !state.get.settings.typingPrompt.backspaceWholeWord) ||
    (!mod && state.get.settings.typingPrompt.backspaceWholeWord)
  ) {
    // Delete whole word
    if (isFirstWord && isFirstChar) {
      const prevParagraphNum = local.paragraphNum - 1
      const isPrevRead = local.stats[prevParagraphNum].read

      // It unmarks the paragraph as read
      if (isPrevRead) {
        handleParagraphClick(prevParagraphNum, local, setLocal)
      }

      setLocal(
        produce((state) => {
          state.paragraphNum = prevParagraphNum
          state.wordNum = isPrevRead
            ? 0
            : local.typed[local.paragraphNum].length - 1
          state.charNum = 0
        }),
      )
      setLocal("stats", prevParagraphNum, "read", false)
    } else if (isFirstChar) {
      setLocal(
        produce((state) => {
          state.wordNum = local.wordNum - 1
          state.charNum = 0
        }),
      )
    } else {
      setLocal("charNum", 0)
    }

    setLocal("typed", local.paragraphNum, local.wordNum, (prev) =>
      [...prev].fill(null),
    )
  } else {
    // Delete single character
    if (isFirstWord && isFirstChar) {
      const prevParagraphNum = local.paragraphNum - 1
      const isPrevRead = local.stats[prevParagraphNum].read

      // It unmarks the paragraph as read
      if (isPrevRead) {
        handleParagraphClick(prevParagraphNum, local, setLocal)
      }

      setLocal(
        produce((state) => {
          state.paragraphNum = prevParagraphNum
          state.wordNum = isPrevRead
            ? 0
            : local.typed[local.paragraphNum].length - 1
          state.charNum = isPrevRead
            ? 0
            : local.typed[local.paragraphNum][local.wordNum].length - 1
        }),
      )
      setLocal("stats", prevParagraphNum, "read", false)
    } else if (isFirstChar) {
      setLocal(
        produce((state) => {
          state.wordNum = local.wordNum - 1
          state.charNum =
            local.typed[local.paragraphNum][local.wordNum].length - 1
        }),
      )
    } else {
      setLocal("charNum", local.charNum - 1)
    }

    setLocal("typed", local.paragraphNum, local.wordNum, local.charNum, null)
  }

  setLocal("prev", "")
  setLocal("prevOk", true)
}
