import type { LocalState } from "../types"
import { produce, type SetStoreFunction } from "solid-js/store"
import { saveParagraphs } from "./saveParagraph"
import { handleParagraphReset } from "./handleParagraphReset"

export async function handleParagraphClick(
  paragraphNum: number,
  local: LocalState,
  setLocal: SetStoreFunction<LocalState>,
) {
  if (local.id === null) {
    return
  }

  const nextParagraph = local.original[paragraphNum + 1]
  const isNextHr =
    nextParagraph &&
    nextParagraph.length === 1 &&
    nextParagraph[0].length === 4 &&
    nextParagraph[0].join("").startsWith("***")

  const changes = await saveParagraphs(
    local.id,
    local.skip,
    {
      num: paragraphNum,
      stats: {
        type: "read",
        timestamp: Date.now(),
        length: 0,
      },
      isNextHr,
    },
    local.length,
    local.paragraphsLengths,
  )

  if (changes.readAdded.has(paragraphNum)) {
    handleParagraphReset(local, setLocal)
  } else if (!changes.readDeleted.has(paragraphNum)) {
    // TODO Fix the case when you go back to typed paragraph,
    // backspace some or made other changes and the press Shoft+Enter
    // it should restore previous typed pargraph without problems
    // in reading mode, code below causes problems
    // setLocal(
    //   "typed",
    //   local.paragraphNum,
    //   structuredClone(unwrap(local.original[paragraphNum])),
    // )
  }

  console.log(local)

  setLocal(
    "stats",
    produce((state) => {
      for (const index of changes.readAdded) {
        state[index].read = true
      }
      for (const index of changes.readDeleted) {
        state[index].read = false
      }
    }),
  )

  if (local.typed.length - 1 === changes.lastIndex) {
    location.replace(`/results/${local.id}`)
    return
  }

  setLocal(
    produce((state) => {
      state.paragraphNum = changes.lastIndex + 1 // HANDLE END OF CHAPTER!
      state.wordNum = 0
      state.charNum = 0
    }),
  )
}
