import "./Toast.css"
import { Component, For, Show } from "solid-js"
import { Portal } from "solid-js/web"

export type ToastItem = {
  id: Symbol
  title: string
  details?: string
  type?: "error" | "warning" | "ok" | "special"
  important: boolean
  onClose: () => void
}

type Props = {
  items: ToastItem[]
  closeAll: () => void
}

const Toast: Component<Props> = (props) => {
  return (
    <Portal>
      <Show when={props.items.length > 0}>
        <ul class="toast">
          <Show when={props.items.length > 1}>
            <li class="close-all" onClick={props.closeAll}>
              CLOSE ALL
            </li>
          </Show>
          <For each={props.items}>
            {(item) => (
              <li
                classList={{
                  [item.type ?? "default"]: Boolean(item.type),
                  important: item.important,
                }}
                onClick={() => {
                  if (item.onClose) {
                    item.onClose()
                  }
                }}
              >
                <h3>{item.title}</h3>
                <Show when={item.details}>
                  <p>{item.details}</p>
                </Show>
                <i class="icon-close"></i>
              </li>
            )}
          </For>
        </ul>
      </Show>
    </Portal>
  )
}

export default Toast
