import { type Component, For, Show, createMemo } from "solid-js"
import { formatNumNice, formatPercentageNice } from "../../../utils/formatters"
import "./StatsStrip.css"

type Props = {
  data: {
    name: string
    nameSub?: string
    val: (number | string)[] | number | string | null
    isPercent: boolean
    precision: number
    unit?: string
    type?: string
    extra?: string
    tooltipTitle?: string
    tooltipVal?: string
    showZero?: boolean
  }[]
  desktopColumns?: number
  mobileColumns?: number
}

const StatsStrip: Component<Props> = (props) => {
  const style = createMemo(() => {
    const styles = []

    if (props.desktopColumns) {
      styles.push(`--desktop-columns: ${props.desktopColumns}`)
    }

    if (props.mobileColumns) {
      styles.push(`--mobile-columns: ${props.mobileColumns}`)
    }

    return styles.join("; ")
  })
  return (
    <ul class="stats-strip" style={style()}>
      <For each={props.data}>
        {(item) => {
          const val = () => {
            if (item.val === null) {
              return <strong>-</strong>
            }

            if (Array.isArray(item.val)) {
              return (
                <For each={item.val}>
                  {(chunk, i) => {
                    return i() % 2 === 0 ? (
                      <strong> {chunk}</strong>
                    ) : (
                      <span> {chunk}</span>
                    )
                  }}
                </For>
              )
            }

            if (typeof item.val === "string") {
              return <strong>{item.val}</strong>
            }

            if (item.val === 0 && item.showZero) {
              return <strong>0</strong>
            }

            if (item.isPercent) {
              return (
                <strong>
                  {formatPercentageNice(item.val ?? 0, item.precision)}
                </strong>
              )
            }

            return (
              <strong>{formatNumNice(item.val ?? 0, item.precision)}</strong>
            )
          }

          return (
            <li>
              <figure>
                <figcaption title={item.tooltipTitle ?? ""}>
                  {item.name}
                </figcaption>
                <Show when={item.nameSub}>
                  <p class="sub">{item.nameSub}</p>
                </Show>
                <p
                  title={item.tooltipVal ?? ""}
                  classList={{
                    val: true,
                    [item.type ?? "normal"]:
                      item.type !== undefined && item.val !== 0,
                  }}
                >
                  {val()}
                  <Show when={item.val !== null && item.unit}>
                    {item.val !== 0 || item.showZero ? item.unit : ""}
                  </Show>
                </p>
                <Show when={item.extra}>
                  <p class="extra">{item.extra}</p>
                </Show>
              </figure>
            </li>
          )
        }}
      </For>
    </ul>
  )
}

export default StatsStrip
