import { fetchJSON } from "../libs/api-helpers/fetchJSON"

type AdditionalInfo = {
  title: string
  author: string
  year: number
  subject: string[]
  place: string[]
  person: string[]
  cover: string | null
  pagesMedian: number
  ratingsCount: number
  rating: number | null
  ratingSort: number | null
  popularity: number
}

type Doc = { ratings_count?: number }

const FIELDS = [
  "title",
  "author_name",
  "place",
  "person",
  "first_publish_year",
  "subject",
  "cover_i",
  "number_of_pages_median",
  "ratings_count",
  "ratings_average",
  "ratings_sortable",
  "readinglog_count",
  "want_to_read_count",
].join(",")

async function fetchExact(title: string, author: string | null) {
  const params = new URLSearchParams()

  if (title) {
    params.set("title", title.toLowerCase())
  }

  if (author) {
    params.set("author", author.toLowerCase())
  }

  params.set("fields", FIELDS)
  params.set("sort", "readinglog")
  params.set("limit", "2")

  const data = await fetchJSON(`https://openlibrary.org/search.json?${params}`)

  return (
    data?.docs
      .sort((a: Doc, b: Doc) => (b.ratings_count ?? 0) - (a.ratings_count ?? 0))
      .filter((doc: Doc) => (doc.ratings_count ?? 0) > 0)[0] ?? null
  )
}

async function fetchClosest(title: string, author: string | null) {
  const params = new URLSearchParams()

  if (title) {
    params.set("q", title.toLowerCase())
  }

  if (author) {
    params.set("author", author.toLowerCase())
  }

  params.set("fields", FIELDS)
  params.set("sort", "readinglog")
  params.set("limit", "2")

  const data = await fetchJSON(`https://openlibrary.org/search.json?${params}`)

  return (
    data?.docs
      .sort((a: Doc, b: Doc) => (b.ratings_count ?? 0) - (a.ratings_count ?? 0))
      .filter((doc: Doc) => (doc.ratings_count ?? 0) > 0)[0] ?? null
  )
}

export async function getAdditionalBookInfo(
  title: string | null,
  author: string | null,
) {
  if (!title) {
    return null
  }

  const bookInfo =
    (await fetchExact(title, author)) ?? (await fetchClosest(title, author))

  if (!bookInfo) {
    return null
  }

  const authorResult = bookInfo.author_name?.[0] ?? null
  const info: AdditionalInfo = {
    title: bookInfo.title,
    author: authorResult,
    year: bookInfo.first_publish_year ?? null,
    subject: bookInfo.subject ?? [],
    place: bookInfo.place ?? [],
    person: (bookInfo.person ?? []).filter(
      (x: string) => !x.includes(authorResult),
    ),
    cover: bookInfo.cover_i
      ? `https://covers.openlibrary.org/b/id/${bookInfo.cover_i}-L.jpg`
      : null,
    pagesMedian: bookInfo.number_of_pages_median ?? null,
    ratingsCount: bookInfo.ratings_count ?? null,
    rating: bookInfo.ratings_average ? bookInfo.ratings_average / 5 : null,
    ratingSort: bookInfo.ratings_sortable
      ? bookInfo.ratings_sortable / 5
      : null,
    popularity:
      (bookInfo.readinglog_count ?? 0) + (bookInfo.want_to_read_count ?? 0),
  }

  console.log(info)

  return info
}
