import { type Component, Show, createMemo } from "solid-js"
import "./Home.css"
import QuickBooks from "./QuickBooks"
import Recommended from "./Recommended"
import QuickStats from "./QuickStats"
import config from "../../../config"
import Hero from "./Hero"
import state from "../../../state/state"
import { getBooksData } from "../../../actions/getBooksData"

const Home: Component = () => {
  const booksInProgress = createMemo(() => {
    return getBooksData(
      state.get.booksIndex
        .filter((book) => book.progress > 0 && book.progress < 1)
        .sort((a, b) => b.timestamp - a.timestamp),
    )
  })

  return (
    <main class="home">
      <Hero lastBookData={booksInProgress()[0]} />
      <Show when={!config.IS_MOBILE}>
        <QuickStats />
      </Show>
      <QuickBooks booksInProgress={booksInProgress()} />
      <Recommended />
    </main>
  )
}

export default Home
