import type {
  BookStats,
  ChapterStats,
  AverageStats,
  FinishedParagraphStats,
  ReadParagraphStats,
  TypedParagraphStats,
  MainStats,
} from "../../../../types/common"
import { calculateWeightedAverage } from "../../../../utils/math"

export type ParagraphsStats = (TypedParagraphStats | ReadParagraphStats)[]

export function calculateStat(values: number[], weights: number[]) {
  const nonZeroWeights = weights.filter((weight) => weight > 0).length

  return nonZeroWeights === 0
    ? { value: 0, weight: 0 }
    : {
        value: calculateWeightedAverage(values, weights),
        weight: weights.reduce((a, b) => a + b, 0),
      }
}

export function summarizeChapterStats(
  pStats: ParagraphsStats,
  chapterLength: number,
  skip: boolean,
): ChapterStats {
  const typedParagraphsCount = pStats.filter(
    (entry) => entry.type === "typed",
  ).length

  const wpms = new Array(typedParagraphsCount)
  const rawWpms = new Array(typedParagraphsCount)
  const wpmsWeights = new Array(typedParagraphsCount)
  const accuracies = new Array(typedParagraphsCount)
  const accuraciesWeights = new Array(typedParagraphsCount)
  const consustencies = new Array(typedParagraphsCount)
  const consistenciesWeights = new Array(typedParagraphsCount)

  let time = 0
  let read = 0
  let typed = 0
  let timestamp = 0
  let i = 0

  for (const stats of pStats) {
    if (stats.type === "typed") {
      wpms[i] = stats.wpm.value
      rawWpms[i] = stats.wpm.raw
      wpmsWeights[i] = stats.wpm.weight
      accuracies[i] = stats.acc.value
      accuraciesWeights[i] = stats.acc.weight
      consustencies[i] = stats.con.value
      consistenciesWeights[i] = stats.con.weight
      typed += stats.length
      time += stats.time
      i++
    } else {
      read += stats.length
    }

    timestamp = Math.max(timestamp, stats.timestamp)
  }

  const total = read + typed
  const wpm = calculateStat(wpms, wpmsWeights)
  const rawWpm = calculateStat(rawWpms, wpmsWeights)
  const acc = calculateStat(accuracies, accuraciesWeights)
  const con = calculateStat(consustencies, consistenciesWeights)

  return {
    wpm: {
      value: wpm.value,
      raw: rawWpm.value,
      weight: wpm.weight,
    },
    acc,
    con,
    time,
    timestamp,
    read,
    typed,
    progress: total / chapterLength,
    skip,
  }
}

