export function summarizeAverageStats(aStats: AverageStats[]): AverageStats {
  const len = aStats.length

  const wpms = new Array(len)
  const rawWpms = new Array(len)
  const wpmsWeights = new Array(len)
  const accuracies = new Array(len)
  const accuraciesWeights = new Array(len)
  const consustencies = new Array(len)
  const consistenciesWeights = new Array(len)

  let time = 0
  let read = 0
  let typed = 0
  let timestamp = 0

  for (let i = 0; i < len; i++) {
    wpms[i] = aStats[i].wpm.value
    rawWpms[i] = aStats[i].wpm.raw
    wpmsWeights[i] = aStats[i].wpm.weight
    accuracies[i] = aStats[i].acc.value
    accuraciesWeights[i] = aStats[i].acc.weight
    consustencies[i] = aStats[i].con.value
    consistenciesWeights[i] = aStats[i].con.weight
    typed += aStats[i].typed
    read += aStats[i].read
    time += aStats[i].time
    timestamp = Math.max(timestamp, aStats[i].timestamp)
  }

  const wpm = calculateStat(wpms, wpmsWeights)
  const rawWpm = calculateStat(rawWpms, wpmsWeights)
  const acc = calculateStat(accuracies, accuraciesWeights)
  const con = calculateStat(consustencies, consistenciesWeights)

  return {
    wpm: {
      value: wpm.value,
      raw: rawWpm.value,
      weight: wpm.weight,
    },
    acc,
    con,
    time,
    timestamp,
    read,
    typed,
  }
}

export function summarizeBookStats(
  cStats: ChapterStats[],
  bookLength: number,
  lastChapter: string,
): BookStats {
  const len = cStats.length

  const wpms = new Array(len)
  const rawWpms = new Array(len)
  const wpmsWeights = new Array(len)
  const accuracies = new Array(len)
  const accuraciesWeights = new Array(len)
  const consustencies = new Array(len)
  const consistenciesWeights = new Array(len)

  let time = 0
  let read = 0
  let typed = 0
  let timestamp = 0
  let total = 0

  for (let i = 0; i < len; i++) {
    wpms[i] = cStats[i].wpm.value
    rawWpms[i] = cStats[i].wpm.raw
    wpmsWeights[i] = cStats[i].wpm.weight
    accuracies[i] = cStats[i].acc.value
    accuraciesWeights[i] = cStats[i].acc.weight
    consustencies[i] = cStats[i].con.value
    consistenciesWeights[i] = cStats[i].con.weight
    typed += cStats[i].typed
    read += cStats[i].read
    time += cStats[i].time
    timestamp = Math.max(timestamp, cStats[i].timestamp)

    if (!cStats[i].skip) {
      total += cStats[i].typed + cStats[i].read
    }
  }

  const wpm = calculateStat(wpms, wpmsWeights)
  const rawWpm = calculateStat(rawWpms, wpmsWeights)
  const acc = calculateStat(accuracies, accuraciesWeights)
  const con = calculateStat(consustencies, consistenciesWeights)

  return {
    wpm: {
      value: wpm.value,
      raw: rawWpm.value,
      weight: wpm.weight,
    },
    acc,
    con,
    time,
    timestamp,
    read,
    typed,
    progress: total / bookLength,
    lastChapter,
  }
}

export function mergeAverageStats(
  paragraphStats: FinishedParagraphStats,
  averageStats?: AverageStats,
): AverageStats {
  if (!averageStats) {
    return paragraphStats.type === "typed"
      ? {
          wpm: paragraphStats.wpm,
          acc: paragraphStats.acc,
          con: paragraphStats.con,
          time: paragraphStats.time,
          typed: paragraphStats.length,
          read: 0,
          timestamp: paragraphStats.timestamp,
        }
      : {
          wpm: { value: 0, raw: 0, weight: 0 },
          acc: { value: 0, weight: 0 },
          con: { value: 0, weight: 0 },
          time: 0,
          typed: 0,
          read: paragraphStats.length,
          timestamp: paragraphStats.timestamp,
        }
  } else {
    const wpm =
      paragraphStats.type === "typed"
        ? calculateStat(
            [averageStats.wpm.value, paragraphStats.wpm.value],
            [averageStats.wpm.weight, paragraphStats.wpm.weight],
          )
        : averageStats.wpm
    const raw =
      paragraphStats.type === "typed"
        ? calculateStat(
            [averageStats.wpm.raw, paragraphStats.wpm.raw],
            [averageStats.wpm.weight, paragraphStats.wpm.weight],
          )
        : { value: averageStats.wpm.raw }
    const acc =
      paragraphStats.type === "typed"
        ? calculateStat(
            [averageStats.acc.value, paragraphStats.acc.value],
            [averageStats.acc.weight, paragraphStats.acc.weight],
          )
        : averageStats.acc
    const con =
      paragraphStats.type === "typed"
        ? calculateStat(
            [averageStats.con.value, paragraphStats.con.value],
            [averageStats.con.weight, paragraphStats.con.weight],
          )
        : averageStats.con

    const time =
      paragraphStats.type === "typed"
        ? averageStats.time + paragraphStats.time
        : averageStats.time

    const typed =
      paragraphStats.type === "typed"
        ? averageStats.typed + paragraphStats.length
        : averageStats.typed

    const read =
      paragraphStats.type === "read"
        ? averageStats.read + paragraphStats.length
        : averageStats.read

    return {
      wpm: {
        value: wpm.value,
        raw: raw.value,
        weight: wpm.weight,
      },
      acc,
      con,
      time,
      typed,
      read,
      timestamp: paragraphStats.timestamp,
    }
  }
}

export function mergeMainStats(
  paragraphStats: FinishedParagraphStats,
  mainStats?: MainStats,
): MainStats {
  if (!mainStats) {
    return paragraphStats.type === "typed"
      ? {
          wpm: paragraphStats.wpm,
          acc: paragraphStats.acc,
          con: paragraphStats.con,
        }
      : {
          wpm: { value: 0, raw: 0, weight: 0 },
          acc: { value: 0, weight: 0 },
          con: { value: 0, weight: 0 },
        }
  } else {
    const wpm =
      paragraphStats.type === "typed"
        ? calculateStat(
            [mainStats.wpm.value, paragraphStats.wpm.value],
            [mainStats.wpm.weight, paragraphStats.wpm.weight],
          )
        : mainStats.wpm
    const raw =
      paragraphStats.type === "typed"
        ? calculateStat(
            [mainStats.wpm.raw, paragraphStats.wpm.raw],
            [mainStats.wpm.weight, paragraphStats.wpm.weight],
          )
        : { value: mainStats.wpm.raw }
    const acc =
      paragraphStats.type === "typed"
        ? calculateStat(
            [mainStats.acc.value, paragraphStats.acc.value],
            [mainStats.acc.weight, paragraphStats.acc.weight],
          )
        : mainStats.acc
    const con =
      paragraphStats.type === "typed"
        ? calculateStat(
            [mainStats.con.value, paragraphStats.con.value],
            [mainStats.con.weight, paragraphStats.con.weight],
          )
        : mainStats.con

    return {
      wpm: {
        value: wpm.value,
        raw: raw.value,
        weight: wpm.weight,
      },
      acc,
      con,
    }
  }
}
