import "./TopScores.css"
import { Component, createMemo, createSignal } from "solid-js"
import StatsStrip from "../../common/stats-strip/StatsStrip"
import state from "../../../state/state"
import { formatNum } from "../../../utils/formatters"
import { getAccuracyThreshold } from "../../../actions/getAccuracyThreshold"
import { getConsistencyThreshold } from "../../../actions/getConsistencyThreshold"
import SmallButtonToggle from "../../common/small-button-toggle/SmallButtonToggle"

const TopScores: Component = () => {
  const [tab, setTab] = createSignal("paragraph")

  const data = createMemo(() => {
    const nameSub = `Single ${tab()}`
    const stats =
      tab() === "chapter"
        ? state.get.statsSummary.topChapter
        : tab() === "book"
          ? state.get.statsSummary.topBook
          : state.get.statsSummary.topParagraph

    return [
      {
        name: "Top Speed",
        nameSub,
        val: stats.wpm.value ?? null,
        isPercent: false,
        precision: 2,
        unit: " wpm",
        tooltipTitle:
          "Words per minute, where one word equals five characters (including spaces)",
        tooltipVal: stats.raw.value
          ? `${formatNum(stats.raw.value ?? 0, 2)} raw`
          : undefined,
      },
      {
        name: "Top Accuracy",
        nameSub,
        val: stats.acc.value ?? null,
        isPercent: true,
        precision: 2,
        unit: " %",
        type: getAccuracyThreshold(stats.acc.value ?? 0),
        tooltipTitle: "Percentage of correct keystrokes",
        tooltipVal: "Target accuracy >= 98%",
      },
      {
        name: "Top Consistency",
        nameSub,
        val: stats.con.value ?? null,
        isPercent: true,
        precision: 2,
        unit: " %",
        type: getConsistencyThreshold(stats.con.value ?? 0),
        tooltipTitle: "Measures how consistent is you speed beteen words",
        tooltipVal: "Target consistency >= 80%",
      },
    ]
  })

  return (
    <section class="top-scores">
      <h2>Top Scores</h2>
      <SmallButtonToggle
        class="tabs-toggle"
        items={[
          {
            name: "Paragraph",
            value: "paragraph",
          },
          {
            name: "Chapter",
            value: "chapter",
          },
          {
            name: "Book",
            value: "book",
          },
        ]}
        joined={true}
        active={tab()}
        setValue={(val) => {
          setTab(val)
        }}
      />
      <StatsStrip data={data()} desktopColumns={3} mobileColumns={1} />
    </section>
  )
}

export default TopScores
