import { useNavigate, useParams, useSearchParams } from "@solidjs/router"
import {
  type Component,
  Show,
  createMemo,
  createResource,
  onCleanup,
  onMount,
  createSignal,
} from "solid-js"
import { getAccuracyThreshold } from "../../../actions/getAccuracyThreshold"
import { getNextChapterId } from "../../../actions/getNextChapterId"
import { getBookInfo } from "../../../io/getBookInfo"
import storage from "../../../storage/storage"
import { formatDuration, formatNum } from "../../../utils/formatters"
import { encodeParams } from "../../../utils/url"
import Separator from "../../common/separator/Separator"
import StatsStrip from "../../common/stats-strip/StatsStrip"
import Chart from "./Chart"
import Confetti from "./Confetti"
import "./Results.css"
import { TypedParagraphStats } from "../../../types/common"
import state from "../../../state/state"
import { getConsistencyThreshold } from "../../../actions/getConsistencyThreshold"
import SmallButtonToggle from "../../common/small-button-toggle/SmallButtonToggle"
import config from "../../../config"
import { notify } from "../../../actions/notify"
import Loading from "../../common/loading/Loading"

async function fetchData(id: string) {
  const [bookId, chapterId] = id.split("__")

  const chaptersStats = await storage.chaptersStats.get(bookId)
  const paragraphsStats = (await storage.paragraphsStats.get(id)) ?? {}
  const bookStats = await storage.booksStats.get(bookId)
  const bookInfo = await getBookInfo(bookId)

  if (!bookInfo) {
    return null
  }

  const topChapter = state.get.statsSummary.topChapter

  if (topChapter.wpm.id === id) {
    notify({
      title: "Top Speed!",
      details: "This is your fastest chapter.",
      type: "ok",
      important: true,
      closeDelay: 10000,
    })
  }

  if (topChapter.acc.id === id) {
    notify({
      title: "Top Accuracy!",
      details: "This is your most accurate chapter.",
      type: "ok",
      important: true,
      closeDelay: 10000,
    })
  }

  if (topChapter.con.id === id) {
    notify({
      title: "Top Consistency!",
      details: "This is your most consistent chapter.",
      type: "ok",
      important: true,
      closeDelay: 10000,
    })
  }

  const isBookComplete = bookStats?.progress === 1

  const nextChapterId = getNextChapterId({
    currentChapterId: chapterId,
    chaptersStats: chaptersStats ?? {},
    chapters: bookInfo.chapters,
    progress: bookStats?.progress ?? 0,
  })

  const nextChapterInfo = bookInfo.chapters.find(
    (chapter) => chapter.id === nextChapterId,
  )
  const nextChapter = {
    path: `${bookInfo.id}__${nextChapterId}`,
    ...nextChapterInfo,
  }

  return {
    id,
    isBookComplete,
    nextChapter,
    bookInfo,
    bookId,
    chapterId,
    bookStats: bookStats && bookStats.time > 0 ? bookStats : undefined,
    chaptersStats,
    paragraphsStats: Object.entries(paragraphsStats)
      .filter(([_, entry]) => entry.type === "typed")
      .map(
        ([index, entry]) =>
          [Number(index), entry] as [number, TypedParagraphStats],
      ),
  }
}

const Results: Component = () => {
  const navigate = useNavigate()
  const [tab, setTab] = createSignal("chapter")
  const params = useParams()
  const [searchParams] = useSearchParams()
  const [data] = createResource(params.id, fetchData)

  const libLink = "/books"
  const nextLink = createMemo(() => `/prompt/${data()?.nextChapter.path}`)
  const tocLink = createMemo(() => `/books/${data()?.bookInfo.id}#toc`)

  function handleShortcuts(e: KeyboardEvent) {
    if (e.key === "Enter") {
      if (data()?.isBookComplete === undefined) {
        return
      }

      navigate(data()?.isBookComplete ? libLink : nextLink())
      return
    }

    if (e.key === "Backspace") {
      navigate(tocLink())
      return
    }
  }

  onMount(() => {
    document.addEventListener("keydown", handleShortcuts)
  })

  onCleanup(() => {
    document.removeEventListener("keydown", handleShortcuts)
  })

  const [chartChapterData] = createResource(data, (data) => {
    const typedEntries = data.paragraphsStats

    if (typedEntries.length === 0) {
      return null
    }

    typedEntries.sort((a, b) => a[0] - b[0])

    const entries = typedEntries.map((entry) => entry[1])
    const labels = typedEntries.map((entry) => entry[0] + 1)

    return {
      wpm: entries.map((entry) => ({
        value: entry.wpm.value,
        weight: entry.wpm.weight,
      })),
      acc: entries.map((entry) => entry.acc),
      con: entries.map((entry) => entry.con),
      labels,
    }
  })

  const fastestParagraph = createMemo(() => {
    const paragraphsStats = data()?.paragraphsStats
    if (!paragraphsStats) {
      return null
    }

    const typedStats = paragraphsStats.map((entry) => entry[1])
    const wpms = typedStats.map((stats) => stats.wpm.value ?? 0)

    return Math.max(...wpms)
  })

  const fastestChapter = createMemo(() => {
    const chaptersStats = data()?.chaptersStats
    if (!chaptersStats) {
      return null
    }

    const wpms = Object.values(chaptersStats).map(
      (stats) => stats.wpm.value ?? 0,
    )

    return Math.max(...wpms)
  })

  const [chapterStats] = createResource(data, (data) => {
    const stats = data.chaptersStats?.[data.chapterId]
    return stats && stats.time > 0 ? stats : undefined
  })

  const [chartBookData] = createResource(data, (data) => {
    if (!data.chaptersStats) {
      return null
    }

    const typedEntries = Object.entries(data.chaptersStats)
      .filter(([_, entry]) => entry.time > 0)
      .sort((a, b) => Number(a[0]) - Number(b[0]))

    const entries = typedEntries.map((entry) => entry[1])
    const labels = typedEntries.map((entry) => Number(entry[0]) + 1)

    return {
      wpm: entries.map((entry) => entry.wpm),
      acc: entries.map((entry) => entry.acc),
      con: entries.map((entry) => entry.con),
      labels,
    }
  })

  const statsData = createMemo(() => {
    const wpm =
      tab() === "chapter"
        ? chapterStats()?.wpm?.value ?? null
        : data()?.bookStats?.wpm?.value ?? null

    const raw =
      tab() === "chapter"
        ? chapterStats()?.wpm?.raw ?? null
        : data()?.bookStats?.wpm?.raw ?? null

    const acc =
      tab() === "chapter"
        ? chapterStats()?.acc?.value ?? null
        : data()?.bookStats?.acc?.value ?? null

    const con =
      tab() === "chapter"
        ? chapterStats()?.con?.value ?? null
        : data()?.bookStats?.con?.value ?? null

    const time =
      tab() === "chapter" ? chapterStats()?.time : data()?.bookStats?.time
    const typed =
      tab() === "chapter" ? chapterStats()?.typed : data()?.bookStats?.typed
    const fastest = tab() === "chapter" ? fastestParagraph() : fastestChapter()
    const fastestLabel = tab() === "chapter" ? "paragraph" : "chapter"

    return [
      {
        name: "Speed",
        val: wpm,
        isPercent: false,
        precision: 2,
        unit: " wpm",
        tooltipTitle:
          "Words per minute, where one word equals five characters (including spaces)",
        tooltipVal: raw ? `${formatNum(raw ?? 0, 1)} raw` : undefined,
      },
      {
        name: "Accuracy",
        val: acc,
        isPercent: true,
        precision: 2,
        unit: " %",
        type: getAccuracyThreshold(acc ?? 0),
        tooltipTitle: "Percentage of correct keystrokes",
        tooltipVal: "Target accuracy >= 98%",
      },
      {
        name: "Consistency",
        val: con,
        isPercent: true,
        precision: 2,
        unit: " %",
        type: getConsistencyThreshold(con ?? 0),
        tooltipTitle: "Measures how consistent is you speed between words",
        tooltipVal: "Target consistency >= 80%",
      },
      {
        name: "Typing Time",
        val: time !== undefined ? formatDuration(time) : null,
        isPercent: true,
        precision: 1,
        tooltipVal: "hours : minutes : seconds",
      },
      {
        name: "Words typed",
        val: (typed ?? 0) / 5,
        isPercent: false,
        precision: 0,
      },
      {
        name: `Fastest ${fastestLabel}`,
        val: fastest,
        isPercent: false,
        precision: 2,
        unit: " wpm",
      },
    ]
  })

  const chartData = createMemo(() => {
    return tab() === "chapter" ? chartChapterData() : chartBookData()
  })

  return (
    <Show when={data()} fallback={<Loading />}>
      <Separator />
      <section class="results">
        <Show when={!data()?.isBookComplete}>
          <section class="next">
            <h2>
              {data()?.nextChapter.id === data()?.chapterId
                ? "Current"
                : "Next"}{" "}
              chapter
            </h2>
            <p>{data()?.nextChapter.title}</p>
            <nav>
              <a
                class="button secondary"
                href={tocLink()}
                data-hint={config.IS_MOBILE ? "" : "Backspace"}
              >
                CONTENTS
              </a>
              <a
                class="button primary"
                href={nextLink()}
                data-hint={config.IS_MOBILE ? "" : "Enter"}
              >
                CONTINUE
              </a>
            </nav>
          </section>
        </Show>
        <Show when={data()?.isBookComplete}>
          <section class="complete">
            <h2>Book completed!</h2>
            <h3>{data()?.bookInfo.title}</h3>
            <p>
              by{" "}
              <a
                href={
                  "/books?" +
                  encodeParams({ author: data()?.bookInfo.author ?? "" })
                }
              >
                {data()?.bookInfo.author}
              </a>
            </p>
            <nav>
              <a
                class="button secondary"
                href={tocLink()}
                data-hint={config.IS_MOBILE ? "" : "Backspace"}
              >
                CONTENTS
              </a>
              <a
                class="button primary"
                href="/books"
                data-hint={config.IS_MOBILE ? "" : "Enter"}
              >
                LIBRARY
              </a>
            </nav>
          </section>
        </Show>

        <Show when={chartData()}>
          <div class="stats-box">
            <SmallButtonToggle
              class="tabs-toggle"
              items={[
                {
                  name: "Chapter Stats",
                  value: "chapter",
                },
                {
                  name: "Book Stats",
                  value: "book",
                },
              ]}
              joined={true}
              active={tab()}
              setValue={(val) => {
                setTab(val)
              }}
            />
            <StatsStrip data={statsData()} desktopColumns={6} />
          </div>
          <Chart
            data={chartData()!}
            wpmPB={
              tab() === "chapter"
                ? state.get.statsSummary.topChapter.wpm.value
                : state.get.statsSummary.topBook.wpm.value
            }
            dataLabel={tab() === "chapter" ? "PARAGRAPHS" : "CHAPTERS"}
            tooltipLabel={tab() === "chapter" ? "Paragraph" : "Chapter"}
          />
        </Show>
      </section>
      <Show when={data()?.isBookComplete && !searchParams.show}>
        <Confetti />
      </Show>
    </Show>
  )
}

export default Results
