import { calculateVariationCoefficient } from "../../../../utils/math.ts"
import { WordStats } from "../types.ts"
import { calculateWpm } from "./calculateWpm.ts"

export function calculateWordsWpms(wordsStats: WordStats[]) {
  const wpms = []

  for (const stats of wordsStats) {
    const start = stats.times[0]
    const end = stats.times[stats.length - 1]

    if (!start || !end) {
      continue
    }

    const wordTime = end - start
    wpms.push(calculateWpm(wordTime, stats.length - 1))
  }

  return wpms
}

export function calculateParagraphConsistency(
  wordsStats: WordStats[],
  charCount: number,
  prevConsistency: { value: number; weight: number } | null,
) {
  // It is not worth the additional complexity to recalculate this
  // minor stat on paragraph reentry, so we just return the old one
  if (prevConsistency) {
    return { ...prevConsistency }
  }

  const wpms = calculateWordsWpms(wordsStats)
  const val = 1 - calculateVariationCoefficient(wpms)

  return {
    value: Math.max(0, val),
    weight: charCount,
  }
}
