import { createStore } from "solid-js/store"
import { getFont } from "../../../actions/getFont"
import config from "../../../config"
import { layouts } from "../../../data/desktopKeyboardLayouts"
import state from "../../../state/state"
import "./ScreenKeyboardDesktop.css"
import {
  Component,
  For,
  Show,
  createMemo,
  createSignal,
  onCleanup,
  onMount,
} from "solid-js"

const MOD_NONE = new Set<string>()
const MOD_SHIFT = new Set(["shift"])
const MOD_RSHIFT = new Set(["rshift"])
const MOD_ALT = new Set(["alt"])
const MOD_RSHIFT_ALT = new Set(["rshift", "alt"])
const MOD_SHIFT_ALT = new Set(["shift", "alt"])

type KeyDetails = {
  value: string
  label: string
  accent: boolean
  code?: string
  icon?: string
  className?: string
  special?: boolean
  toRight?: boolean
  holdable?: boolean
  mod?: string
  notChar?: boolean
}

type KeysDetails = { [key: string]: KeyDetails }

function getSpecialKeys(template: string): KeysDetails {
  return {
    ISO: {
      code: "",
      value: "",
      label: "ISO",
      accent: true,
      className: "iso",
      special: true,
      notChar: true,
    },
    CapsLock: {
      code: "CapsLock",
      value: "CapsLock",
      label: config.IS_APPLE ? "caps lock" : "CapsLock",
      accent: true,
      className: "caps",
      special: true,
      notChar: true,
    },
    ShiftLeft: {
      code: "ShiftLeft",
      value: "Shift",
      label: config.IS_APPLE ? "shift" : "Shift",
      accent: true,
      className: "shift-left",
      special: true,
      holdable: true,
      mod: "shift",
      notChar: true,
    },
    ShiftRight: {
      code: "ShiftRight",
      value: "Shift",
      label: config.IS_APPLE ? "shift" : "Shift",
      accent: true,
      className: "shift-right",
      special: true,
      toRight: config.IS_APPLE,
      holdable: true,
      mod: "rshift",
      notChar: true,
    },
    Backspace: {
      code: "Backspace",
      value: "Backspace",
      label: config.IS_APPLE ? "delete" : "Backspace",
      accent: true,
      className: "backspace",
      special: true,
      toRight: config.IS_APPLE,
      notChar: true,
    },
    Space: {
      value: " ",
      label: " ",
      accent: false,
      className: "space",
      special: true,
    },
    Tab: {
      code: "Tab",
      value: "Tab",
      label: config.IS_APPLE ? "tab" : "Tab",
      accent: true,
      className: "tab",
      special: true,
      notChar: true,
    },
    Fn: {
      value: "Fn",
      label: config.IS_APPLE ? "fn" : "Fn",
      accent: true,
      className: "fn",
      special: true,
      holdable: true,
      notChar: true,
    },
    Enter: {
      value: "Enter",
      label: config.IS_APPLE
        ? template === "type-b"
          ? "ret"
          : "return"
        : "Enter",
      accent: true,
      className: "enter",
      special: true,
      toRight: config.IS_APPLE,
    },
    Enter1: {
      value: "Enter",
      label: config.IS_APPLE
        ? template === "type-b"
          ? "ret"
          : "return"
        : "Enter",
      accent: true,
      className: "enter-1",
      special: true,
      toRight: config.IS_APPLE,
    },
    Enter2: {
      value: "Enter",
      label: "",
      accent: true,
      icon: "none",
      className: "enter-2",
      special: true,
      toRight: config.IS_APPLE,
    },
    CtrlLeft: {
      code: "ControlLeft",
      value: "Ctrl",
      label: "Ctrl",
      accent: true,
      className: "ctrl-left",
      special: true,
      holdable: true,
      notChar: true,
    },
    CtrlRight: {
      code: "ControlRight",
      value: "Ctrl",
      label: "Ctrl",
      accent: true,
      className: "ctrl-right",
      special: true,
      holdable: true,
      notChar: true,
    },
    Alt: {
      code: "AltLeft",
      value: "Alt",
      label: config.IS_APPLE ? "option" : "Alt",
      accent: true,
      className: "alt",
      icon: config.IS_APPLE ? "option" : undefined,
      special: !config.IS_APPLE,
      holdable: true,
      notChar: true,
    },
    AltGraph: {
      code: "AltRight",
      value: "AltGraph",
      label: config.IS_APPLE ? "option" : "Alt",
      accent: true,
      className: "alt-gr",
      icon: config.IS_APPLE ? "option" : undefined,
      special: !config.IS_APPLE,
      holdable: true,
      mod: "alt",
      notChar: true,
    },
    Meta: {
      code: "MetaLeft",
      value: "Meta",
      label: config.IS_APPLE ? "command" : "Super",
      accent: true,
      icon: config.IS_APPLE
        ? "meta-macos"
        : config.OS === "Windows"
          ? "meta-windows"
          : undefined,
      className: "meta",
      special: !config.IS_APPLE && !(config.OS === "Windows"),
      holdable: true,
      notChar: true,
    },
    MetaRight: {
      code: "MetaRight",
      value: "Meta",
      label: config.IS_APPLE ? "command" : "Super",
      accent: true,
      icon: config.IS_APPLE
        ? "meta-macos"
        : config.OS === "Windows"
          ? "meta-windows"
          : undefined,
      className: "meta-right",
      special: !config.IS_APPLE && !(config.OS === "Windows"),
      holdable: true,
      notChar: true,
    },
    Backslash: {
      value: "\\",
      label: "\\",
      accent: false,
      className: "backslash",
    },
    Pipe: {
      value: "|",
      label: "|",
      accent: false,
      className: "backslash",
    },
    ArrowLeft: {
      code: "ArrowLeft",
      value: "ArrowLeft",
      label: "",
      accent: true,
      icon: "arrow-left",
      className: "arrow-left",
      notChar: true,
    },
    ArrowRight: {
      code: "ArrowRight",
      value: "ArrowRight",
      label: "",
      accent: true,
      icon: "arrow-right",
      className: "arrow-right",
      notChar: true,
    },
    ArrowUp: {
      code: "ArrowUp",
      value: "ArrowUp",
      label: "",
      accent: true,
      icon: "arrow-up",
      className: "arrow-up",
      notChar: true,
    },
    ArrowDown: {
      code: "ArrowDown",
      value: "ArrowDown",
      label: "",
      accent: true,
      icon: "arrow-down",
      className: "arrow-down",
      notChar: true,
    },
  }
}

function layoutToDetails(layout: string[][], template: string) {
  const specialKeys = getSpecialKeys(template)

  return layout.map((row, i) =>
    row.map((key): KeyDetails => {
      if (key.startsWith("Blank")) {
        const span = Number(key.split(":")[1])

        return {
          value: "",
          label: "",
          accent: false,
          className: `blank-${span}`,
        }
      }

      return (
        specialKeys[key] ?? {
          value: key,
          label: key,
          accent: i === 0 || i === layout.length - 1,
        }
      )
    }),
  )
}

type Props = {
  handleTyping: (e: KeyboardEvent) => void
  expectedChar?: string
  prev: string
  prevOk: boolean
  charset: Set<string>
}

const defaultLocal = {
  CapsLock: false,
  ShiftLeft: false,
  ShiftRight: false,
  AltLeft: false,
  AltRight: false,
  ControlLeft: false,
  ControlRight: false,
  MetaLeft: false,
  MetaRight: false,
  Backspace: false,
  ArrowLeft: false,
  ArrowRight: false,
  ArrowUp: false,
  ArrowDown: false,
  Tab: false,
  KeyH: false,
}

const ScreenKeyboardDesktop: Component<Props> = (props) => {
  const [caps, setCaps] = createSignal(false)
  const [current, setCurrent] = createSignal<string | null>(null)
  const [local, setLocal] = createStore({ ...defaultLocal })
  const modKeys = new Set(Object.keys(local))

  const handleMod = (isDown: boolean) => (e: KeyboardEvent) => {
    if (modKeys.has(e.code)) {
      setLocal(e.code as keyof typeof local, isDown)
      return true
    }
    return false
  }

  const handleModPress = handleMod(true)
  const handleModRelease = handleMod(false)

  const handleKeyDown = (e: KeyboardEvent) => {
    if (!handleModPress(e)) {
      setCurrent(e.key)
    }

    const currentCaps = e.getModifierState && e.getModifierState("CapsLock")

    if (local.CapsLock) {
      setCaps(!currentCaps)
    } else if (currentCaps !== caps()) {
      setCaps(currentCaps)
    }
  }

  const handleKeyUp = (e: KeyboardEvent) => {
    if (!handleModRelease(e)) {
      setCurrent(null)
    }
  }

  const handleBlur = () => {
    setLocal({ ...defaultLocal })
  }

  onMount(() => {
    document.addEventListener("keydown", handleKeyDown)
    document.addEventListener("keyup", handleKeyUp)
    window.addEventListener("blur", handleBlur)
  })

  onCleanup(() => {
    document.removeEventListener("keydown", handleModPress)
    document.removeEventListener("keyup", handleModRelease)
    window.removeEventListener("blur", handleBlur)
  })

  const showNext = createMemo(
    () =>
      state.get.settings.typingPrompt.keyboardIndicator === "next" ||
      state.get.settings.typingPrompt.keyboardIndicator === "both",
  )

  const showLast = createMemo(
    () =>
      state.get.settings.typingPrompt.keyboardIndicator === "last" ||
      state.get.settings.typingPrompt.keyboardIndicator === "both",
  )

  const osType = config.IS_APPLE ? "mac" : "pc"

  const layout = createMemo(() => {
    const layoutData =
      layouts[osType][state.get.settings.typingPrompt.keyboardLayout]
    const main = layoutToDetails(layoutData.main, layoutData.template)
    const shift = layoutToDetails(layoutData.shift, layoutData.template)
    const alt = layoutToDetails(layoutData.alt, layoutData.template)
    const shiftAlt = layoutToDetails(layoutData.shiftAlt, layoutData.template)
    const template = layoutData.template

    const chars = {
      main: new Set(main.flat().map((key) => key.value)),
      shift: new Set(shift.flat().map((key) => key.value)),
      rShift: layoutData.rShift,
      alt: new Set(alt.flat().map((key) => key.value)),
      shiftAlt: new Set(shiftAlt.flat().map((key) => key.value)),
      rShiftAlt: layoutData.rShiftAlt,
    }

    return {
      template,
      bumps: layoutData.bumps,
      layers: { main, shift, alt, "shift-alt": shiftAlt },
      chars,
    }
  })

  const toggleCase = (char: string) => {
    const isLowerCase = char.toLowerCase() === char
    return isLowerCase ? char.toUpperCase() : char.toLowerCase()
  }

  const expectedChar = createMemo(() => {
    if (!props.expectedChar) {
      return { layer: "null", mods: MOD_NONE, val: "" }
    }

    if (!props.charset.has(props.expectedChar ?? "")) {
      return { layer: "any", mods: MOD_NONE, val: " " }
    }

    const chars = layout().chars
    const char = caps() ? toggleCase(props.expectedChar) : props.expectedChar

    if (!char || chars.main.has(char)) {
      return { layer: "main", mods: MOD_NONE, val: char }
    }

    if (chars.rShift.has(char)) {
      return { layer: "shift", mods: MOD_RSHIFT, val: char }
    }

    if (chars.shift.has(char)) {
      return { layer: "shift", mods: MOD_SHIFT, val: char }
    }

    if (chars.alt.has(char)) {
      return { layer: "alt", mods: MOD_ALT, val: char }
    }

    if (chars.rShiftAlt.has(char)) {
      return { layer: "shift-alt", mods: MOD_RSHIFT_ALT, val: char }
    }

    if (chars.shiftAlt.has(char)) {
      return { layer: "shift-alt", mods: MOD_SHIFT_ALT, val: char }
    }

    return { layer: "main", mods: MOD_NONE, val: char }
  })

  const isNext = (key: KeyDetails) => {
    if (expectedChar().layer === "null") {
      return false
    }

    if (expectedChar().layer === "any") {
      return key.label === expectedChar().val
    }

    if (expectedChar().layer === layer()) {
      return expectedChar().val === key.label
    }

    if (key.mod !== undefined && expectedChar().mods.has(key.mod)) {
      return true
    }

    return false
  }

  const font = createMemo(() => {
    return getFont(state.get.settings.typingPrompt.keyBoardFont)
  })

  const layer = createMemo(() => {
    if (local.AltRight && (local.ShiftLeft || local.ShiftRight)) {
      return "shift-alt"
    }

    if (local.ShiftLeft || local.ShiftRight) {
      return "shift"
    }

    if (local.AltRight) {
      return "alt"
    }

    return "main"
  })

  return (
    <section
      classList={{
        "screen-keyboard-desktop": true,
        [`keycaps-${state.get.settings.typingPrompt.keyboardKeycaps}`]: true,
        "caps-active": caps(),
      }}
      style={{
        "--key-thickness":
          state.get.settings.typingPrompt.keyboardThickness ?? "2px",
        "--font": font().font,
        "--font-scale": font().size,
      }}
    >
      <div
        classList={{
          keys: true,
          [layout().template]: true,
          [layer()]: true,
          mac: config.IS_APPLE,
          lower: caps() && (local.ShiftLeft || local.ShiftRight),
          upper: caps() && !(local.ShiftLeft || local.ShiftRight),
        }}
      >
        <For each={layout().layers.main}>
          {(row, y) => (
            <For each={row}>
              {(_, x) => {
                const key = createMemo(() => layout().layers[layer()][y()][x()])
                const className = key().className ?? ""

                return (
                  <button
                    disabled={true}
                    classList={{
                      accent: key().accent,
                      next: showNext() && isNext(key()),
                      ok:
                        showLast() &&
                        current() !== null &&
                        current() === key().label &&
                        props.prevOk,
                      error:
                        showLast() &&
                        current() !== null &&
                        current() === key().label &&
                        !props.prevOk,
                      [className]: Boolean(key().className),
                      special: key().special,
                      bump: layout().bumps.has(`${y()}:${x()}`),
                      "to-right": key().toRight,
                      warning:
                        key().code !== undefined &&
                        local[key().code as keyof typeof local] === true,
                    }}
                  >
                    <div>
                      <Show when={key().icon} fallback={key().label}>
                        <i class={`icon-${key().icon}`} />
                      </Show>
                    </div>
                  </button>
                )
              }}
            </For>
          )}
        </For>
      </div>
    </section>
  )
}

export default ScreenKeyboardDesktop
