import { useSearchParams } from "@solidjs/router"
import { type Component, For, createMemo } from "solid-js"
import state from "../../../state/state"
import { getUtcDays } from "../../../utils/dates"
import Book from "../../common/book/Book"
import Pagination from "../../common/pagination/Pagination"
import "./Books.css"
import Filters from "./Filters"
import { getBooksData } from "../../../actions/getBooksData"
import { shuffle } from "../../../utils/array"

const Books: Component = () => {
  const [searchParams] = useSearchParams()

  const books = createMemo(() => {
    const genre = searchParams.genre
    const author = searchParams.author
    const type = searchParams.type
    const phrase = searchParams.search
    const tab = searchParams.tab ?? "all"
    const series = searchParams.series

    const data = getBooksData(state.get.booksIndex).filter((book) => {
      const matchesSearch = phrase
        ? book.author.toLowerCase().includes(phrase) ||
          book.title.toLowerCase().includes(phrase) ||
          book.genres.some((genre) => genre.includes(phrase)) ||
          book.series?.toLowerCase().includes(phrase)
        : true

      const matchesAuthor = author ? book.author === author : true
      const matchesGenre = genre ? book.genres.includes(genre) : true
      type === "in-progress" ? book.progress > 0 && book.progress < 1 : true
      const matchesNew = type === "new" ? book.isNew : true
      const matchSeries = series ? book.series === series : true
      const matchTab =
        tab === "favorites"
          ? book.favorite
          : tab === "done"
            ? book.progress === 1
            : tab === "in-progress"
              ? book.progress > 0 && book.progress < 1
              : true

      return (
        matchesSearch &&
        matchesAuthor &&
        matchesGenre &&
        matchesNew &&
        matchTab &&
        matchSeries
      )
    })

    const sortBy = searchParams.order ?? state.get.settings.ui.sortBy

    if (sortBy === "random") {
      shuffle(data)
    } else {
      data.sort((a, b) => {
        if (sortBy === "part") {
          return (
            a.part - b.part ||
            a.author.localeCompare(b.author) ||
            a.titleAlpha.localeCompare(b.titleAlpha)
          )
        }

        if (sortBy === "length") {
          return (
            a.pages - b.pages ||
            a.author.localeCompare(b.author) ||
            a.titleAlpha.localeCompare(b.titleAlpha)
          )
        }

        if (sortBy === "title") {
          return (
            a.titleAlpha.localeCompare(b.titleAlpha) ||
            a.author.localeCompare(b.author)
          )
        }

        if (sortBy === "year") {
          return (
            a.year - b.year ||
            a.part - b.part ||
            a.author.localeCompare(b.author) ||
            a.titleAlpha.localeCompare(b.titleAlpha)
          )
        }

        if (sortBy === "added") {
          return (
            getUtcDays(b.createdAt - a.createdAt) ||
            a.part - b.part ||
            a.author.localeCompare(b.author) ||
            a.titleAlpha.localeCompare(b.titleAlpha)
          )
        }

        return (
          a.author.localeCompare(b.author) ||
          a.part - b.part ||
          a.titleAlpha.localeCompare(b.titleAlpha)
        )
      })
    }

    return data
  })

  const booksOnPage = createMemo(() => {
    const limit = state.get.settings.ui.booksPerPage
    const currentPage = searchParams.page ? Number(searchParams.page) : 1
    const start = (currentPage - 1) * limit
    const end = start + limit
    return books().slice(start, end)
  })

  const totalPages = createMemo(() => {
    return Math.ceil(books().length / state.get.settings.ui.booksPerPage)
  })

  return (
    <section class="books">
      <Filters />
      <Pagination of={totalPages()} shortcuts={false} />
      <p class="results">
        Found <strong>{books().length}</strong> book
        {books().length !== 1 ? "s" : ""}
      </p>
      <main>
        <For each={booksOnPage()}>
          {(book) => (
            <Book
              id={book.id}
              coverUrl={book.coverUrl}
              title={book.title}
              author={book.author}
              description={book.description}
              pages={book.pages}
              progress={book.progress}
              favorite={book.favorite}
              isNew={book.isNew}
              genres={book.genres}
              part={searchParams.series ? book.part : undefined}
            />
          )}
        </For>
      </main>
      <Pagination of={totalPages()} shortcuts={false} down={true} />
    </section>
  )
}

export default Books
