import { type Component, Show, createMemo, createSignal } from "solid-js"
import { getAccuracyThreshold } from "../../../actions/getAccuracyThreshold.ts"
import state from "../../../state/state.ts"
import {
  formatNum,
  formatPercentage,
  formatPercentageNice,
} from "../../../utils/formatters.ts"
import "./Statusbar.css"
import config from "../../../config.ts"
import MobileMenu from "../../common/header/MobileMenu.tsx"

type Props = {
  bookId: string | null
  bookTitle: string | null
  chapterTitle: string | null
  acc: number | null
  wpm: number | null
  paused: boolean
  page: number
  pages: number
  progress: number
  dailyProgress: {
    progress: number
    length: number
    time: number
  }
  saved: boolean
  cantTypeAlert: boolean
  scrollToActive: () => void
  resetUnfinishedParagraph: () => void
  invisible: boolean
}

const Statusbar: Component<Props> = (props) => {
  const [menu, setMenu] = createSignal(false)

  const liveAcc = createMemo(() =>
    props.acc !== null
      ? formatNum(props.acc * 100, config.IS_MOBILE ? 0 : 2)
      : "-",
  )

  const liveWpm = createMemo(() =>
    props.wpm !== null ? formatNum(props.wpm, config.IS_MOBILE ? 0 : 2) : "-",
  )

  const liveProgress = createMemo(() =>
    formatPercentageNice(props.progress, config.IS_MOBILE ? 0 : 1),
  )

  return (
    <section classList={{ statusbar: true, invisible: props.invisible }}>
      <p class="slug">
        <Show when={props.bookTitle}>
          <a href={`/books/${props.bookId}`} class="mobile">
            <i class="icon-big icon-back"></i>
          </a>
        </Show>
        <Show when={props.bookTitle}>
          <a href={`/books/${props.bookId}`} class="desktop">
            {props.bookTitle}
          </a>
          <span class="desktop"> / </span>
        </Show>
        <Show when={props.chapterTitle}>
          <strong class="desktop">{props.chapterTitle}</strong>
        </Show>
      </p>
      <p class="stats">
        <Show
          when={
            (props.paused && !state.get.settings.ui.readingMode) || props.saved
          }
        >
          <span
            classList={{
              alert: true,
              warning: props.paused && !props.saved,
              ok: props.saved,
            }}
          >
            <i class={`icon-${props.saved ? "save" : "pause"}`} />
            <strong>{props.saved ? "SAVED" : "PAUSED"}</strong>
          </span>
        </Show>
        <span title="Progress">
          <strong class="percent">{liveProgress()}</strong> done
        </span>
        <Show when={!state.get.settings.ui.readingMode}>
          <span title="Words per minute">
            <strong>{liveWpm()}</strong> wpm
          </span>
          <span
            title={`Accuracy (${((props.acc ?? 0) * 100).toFixed(3)}%)`}
            classList={{
              [getAccuracyThreshold(props.acc ?? 0)]: props.acc !== null,
            }}
          >
            <strong class="percent">{liveAcc()}</strong> acc
          </span>
        </Show>
        <Show when={state.get.settings.ui.readingMode}>
          <span
            classList={{
              mode: true,
              flash: props.cantTypeAlert,
            }}
            title="Toggle the button on the right to go back to typing."
          >
            READING MODE
          </span>
        </Show>
        <button
          class="mode"
          onClick={() => {
            state.set("settings", "ui", "readingMode", (prev) => !prev)
            if (state.get.settings.ui.readingMode) {
              props.resetUnfinishedParagraph()
            }
            setTimeout(() => props.scrollToActive(), 100)
          }}
          title={
            state.get.settings.ui.readingMode
              ? "Click to switch to typing mode"
              : "Click to switch to reading mode"
          }
        >
          <i
            class={`icon-big icon-reading-${
              state.get.settings.ui.readingMode ? "on" : "off"
            }`}
          />
        </button>
        <button class="nav menu" onClick={() => setMenu(!menu())} title="Menu">
          <i class={menu() ? "icon-close" : "icon-menu"} />
        </button>
      </p>
      <MobileMenu hidden={!menu()} close={() => setMenu(false)} />
      <div class="bars">
        <Show when={!menu() && !state.get.settings.ui.readingMode}>
          <div
            class="progressbar daily"
            data-info={`Daily goal progress: ${formatPercentage(
              props.dailyProgress.progress,
              1,
            )}, ${formatNum(props.dailyProgress.time / 1000 / 60, 0)} of ${
              config.DAILY_GOAL_MINUTES
            } min`}
          >
            <div
              style={`width: ${
                Math.min(1, props.dailyProgress.progress) * 100
              }%`}
            ></div>
          </div>
        </Show>
        <Show when={!menu()}>
          <div
            class="progressbar"
            data-info={`Chapter progress: ${formatPercentage(
              props.progress,
              1,
            )}, page ${props.page} of ${props.pages}`}
          >
            <div style={`width: ${props.progress * 100}%`}></div>
          </div>
        </Show>
      </div>
    </section>
  )
}

export default Statusbar
