import { getNextChapterId } from "../../../actions/getNextChapterId"
import config from "../../../config"
import { getBookInfo } from "../../../io/getBookInfo"
import storage from "../../../storage/storage"

export async function fetchBookDetails(id: string) {
  const data = await getBookInfo(id)

  if (data === null) {
    return null
  }

  const extra = null //await getAdditionalBookInfo(data.title, data.author)

  const chaptersStats = await storage.chaptersStats.get(id)
  const bookStats = await storage.booksStats.get(id)

  const nextChapterId = getNextChapterId({
    currentChapterId: bookStats?.lastChapter ?? "",
    chaptersStats: chaptersStats ?? {},
    chapters: data.chapters,
    progress: bookStats?.progress ?? 0,
  })

  const chapters = data.chapters.map((chapter) => {
    const chapterStats = chaptersStats && chaptersStats[chapter.id]

    return {
      ...chapter,
      wpm: chapterStats?.wpm.value ?? null,
      acc: chapterStats?.acc.value ?? null,
      consistency: chapterStats?.con.value ?? null,
      progress: chapterStats?.progress ?? 0,
      typedLength: chapterStats?.typed ?? 0,
    }
  })

  const totalChars = data.chapters
    .filter((chapter) => !chapter.skip)
    .map((chapter) => chapter.length)
    .reduce((sum, x) => sum + x, 0)

  const totalPages = Math.ceil(totalChars / config.CHARACTERS_PER_PAGE)

  const typedLength = bookStats?.typed ?? 0
  const pages = typedLength / config.CHARACTERS_PER_PAGE

  return {
    ...data,
    pages,
    totalPages,
    totalChars,
    chapters,
    wpm: bookStats?.wpm.value ?? null,
    raw: bookStats?.wpm.raw ?? null,
    acc: bookStats?.acc.value ?? null,
    con: bookStats?.con.value ?? null,
    time: bookStats?.time,
    progress: bookStats?.progress ?? 0,
    typedLength,
    nextChapterId,
    extra,
  }
}
