import type { ParentComponent } from "solid-js"
import "./Select.css"

type Props = {
  onChange(
    e: Event & {
      target: HTMLSelectElement
    },
  ): void
}

const Select: ParentComponent<Props> = (props) => {
  return (
    <div class="select">
      <select onChange={props.onChange}>{props.children}</select>
    </div>
  )
}

export default Select
