import { produce } from "solid-js/store"
import state from "../state/state"

export type ToastData = {
  title: string
  details?: string
  type?: "error" | "warning" | "ok" | "special"
  manualClose?: boolean
  closeDelay?: number
  important?: boolean
}

export function notify(toastData: ToastData) {
  const id = Symbol()

  state.set(
    "session",
    "notifications",
    produce((temp) => {
      temp.unshift({
        id,
        ...toastData,
        onClose: () => {
          state.set("session", "notifications", (prev) => {
            return prev.filter((item) => item.id !== id)
          })
        },
        important: Boolean(toastData.important),
      })
    }),
  )

  if (!toastData.manualClose) {
    setTimeout(() => {
      state.set("session", "notifications", (prev) => {
        return prev.filter((item) => item.id !== id)
      })
    }, toastData.closeDelay ?? 4000)
  }
}
