import { useParams } from "@solidjs/router"
import {
  For,
  Show,
  createEffect,
  createMemo,
  createResource,
  createSignal,
  on,
  onCleanup,
  onMount,
} from "solid-js"
import { createStore, produce } from "solid-js/store"
import "./Prompt.css"

import config from "../../../config.ts"
import state from "../../../state/state.ts"
import { formatNum } from "../../../utils/formatters.ts"
import { getPromptData } from "./prompt-actions/getPromptData.ts"
import { EXTRA_KEYS } from "./prompt-util/KEY_NAMES.ts"
import { initScroll, scrollToWord } from "./prompt-util/scrollToWord.ts"
import type { LocalState } from "./types.ts"

import Statusbar from "./Statusbar.tsx"
import { handleBackspace } from "./prompt-actions/handleBackspace.ts"
import { handleParagraphReset } from "./prompt-actions/handleParagraphReset.ts"
import { handleStandardInput } from "./prompt-actions/handleStandardInput.ts"
import { loadPromptContent } from "./prompt-actions/loadPromptContent.ts"
import { updateAverageAccuracy } from "./prompt-actions/updateAverageAccuracy.ts"
import { updateAverageWpm } from "./prompt-actions/updateAverageWpm.ts"
import { updateProgress } from "./prompt-actions/updateProgress.ts"
import { getScreenSplitOffsets } from "./prompt-util/getScreenSplitOffsets.ts"
import { getFont } from "../../../actions/getFont.ts"
import { handleParagraphClick } from "./prompt-actions/handleParagraphClick.ts"
import { showCantTypeAlert } from "./prompt-util/setCantTypeAlert.ts"
// import { simulateTyping } from "./prompt-actions/simulateTyping.ts"
import ScreenKeyboard from "../../common/screen-keyboard/ScreenKeyboard.tsx"
import Header from "../../common/header/Header.tsx"
import { getDailyProgress } from "../../../actions/getDailyProgress.ts"
import ScreenKeyboardDesktop from "../../common/screen-keyboard/ScreenKeyboardDesktop.tsx"
import { notify } from "../../../actions/notify.ts"
import Loading from "../../common/loading/Loading.tsx"

function Prompt() {
  const params = useParams()
  let screenKeyboardPrompt: HTMLTextAreaElement | undefined
  const [blur, setBlur] = createSignal(false)

  const [local, setLocal] = createStore<LocalState>({
    id: null,
    skip: false,
    length: 0,
    lengthCompleted: 0,
    charset: new Set(),
    hideCursor: false,
    done: false,
    paused: true,
    saved: true,
    original: [],
    typed: [],
    paragraphsLengths: [],
    incrementalLength: [],
    screenSplits: [],
    splitStart: 0,
    splitEnd: 0,
    stats: [],
    pageStats: { acc: null, wpm: null, inputTimes: [] },
    paragraphNum: 0,
    wordNum: 0,
    charNum: -1,
    prev: "",
    prevOk: true,
  })
  const [cantType, setCantType] = createSignal(false)
  const [promptData] = createResource(params.id, getPromptData)

  const [caret, setCaret] = createSignal({ x: 0, y: 0, h: 0, w: 0 })

  const isParagraphChange = createMemo(() => {
    return (
      local.original.length === 0 ||
      (local.wordNum === 0 && local.charNum === 0) ||
      (local.wordNum === local.original[local.paragraphNum].length - 1 &&
        local.charNum ===
          local.original[local.paragraphNum][local.wordNum].length - 1)
    )
  })

  const caretSpeed = createMemo(() =>
    isParagraphChange()
      ? "instant"
      : state.get.settings.typingPrompt.caretSpeed,
  )

  const getCaretPosition = () => {
    const current = document.querySelector(".current") as HTMLSpanElement | null
    setCaret({
      x: current?.offsetLeft ?? 0,
      y: current?.offsetTop ?? 0,
      h: current?.offsetHeight ?? 0,
      w: current?.offsetWidth ?? 0,
    })
  }

  const bootstrapCaret = (step: number = 0) => {
    if (!state.get.settings.ui.readingMode && step <= 10) {
      getCaretPosition()
      setTimeout(bootstrapCaret, step * 100, step + 1)
    }
  }

  createEffect(
    on(
      [() => local.charNum, () => local.wordNum, () => local.paragraphNum],
      getCaretPosition,
    ),
  )

  const paragraphNumPB = createMemo(() => {
    const [bookId, chapterId, paragraphId] =
      state.get.statsSummary.topParagraph.wpm.id.split("__")

    return `${bookId}__${chapterId}` === local.id ? Number(paragraphId) : -1
  })

  const showCantType = showCantTypeAlert(setCantType)

  const setAutotypeText = () => {
    const text = local.original
      .slice(local.paragraphNum, local.splitEnd)
      .map((p) => p.flat().join(""))
      .join("\n\n")

    return text
  }

  const wordLabel = createMemo(() => {
    return `${local.paragraphNum}:${local.wordNum}`
  })

  createEffect(
    on(promptData, () => {
      loadPromptContent(promptData, setLocal, params.id)
    }),
  )

  // createEffect(
  //   on(
  //     () => local.splitEnd,
  //     () => {
  //       if (!state.get.settings.ui.readingMode) {
  //         const text = setAutotypeText()
  //         simulateTyping(text, local, setLocal, {
  //           wpm: 100,
  //           randomize: true,
  //           emitEvents: true,
  //         })
  //       }
  //     },
  //   ),
  // )

  createEffect(on(wordLabel, updateAverageWpm(local, setLocal)))
  createEffect(on(wordLabel, updateAverageAccuracy(local, setLocal)))
  createEffect(on(wordLabel, updateProgress(local, setLocal)))

  createEffect(on(() => local.splitEnd, setAutotypeText))

  createEffect(
    on(
      () => local.paragraphNum,
      () => {
        const splitOffsets = getScreenSplitOffsets(
          local.screenSplits,
          local.paragraphNum,
        )

        if (local.splitStart !== splitOffsets.start) {
          setLocal(
            produce((state) => {
              state.splitStart = splitOffsets.start
              state.splitEnd = splitOffsets.end
            }),
          )

          if (
            !state.get.settings.ui.readingMode &&
            local.paragraphNum >= local.splitStart &&
            local.paragraphNum < local.splitEnd
          ) {
            initScroll(screenKeyboardPrompt)
          } else {
            document
              .querySelector(".prompt-box")
              ?.scroll({ top: 0, behavior: "instant" })
          }

          bootstrapCaret()
        }
      },
    ),
  )

  const prevPage = () => {
    if (local.splitStart === local.screenSplits[0]) {
      return
    }

    const splitOffsets = getScreenSplitOffsets(
      local.screenSplits,
      local.splitStart - 1,
    )

    setLocal(
      produce((state) => {
        state.splitStart = splitOffsets.start
        state.splitEnd = splitOffsets.end
      }),
    )

    document
      .querySelector(".prompt-box")
      ?.scroll({ top: 0, behavior: "instant" })
  }

  const nextPage = () => {
    if (local.splitEnd === local.screenSplits.at(-1)) {
      return
    }

    const splitOffsets = getScreenSplitOffsets(
      local.screenSplits,
      local.splitEnd,
    )

    setLocal(
      produce((state) => {
        state.splitStart = splitOffsets.start
        state.splitEnd = splitOffsets.end
      }),
    )

    document
      .querySelector(".prompt-box")
      ?.scroll({ top: 0, behavior: "instant" })
  }

  const handleTyping = (e: KeyboardEvent) => {
    if (!e.key || e.key === "Undefined") {
      return
    }

    const isParagraphEnd = () =>
      local.original[local.paragraphNum][local.wordNum][local.charNum] ===
      config.ENTER_SYMBOL

    // Mark paragraph as read in typing mode, when the user types Shift+Enter
    // (but not if it's an accidental Shift+Enter at the very end of a paragraph)
    if (
      (e.key === "Enter" && e.shiftKey && !isParagraphEnd()) ||
      (e.key === "Enter" && state.get.settings.ui.readingMode)
    ) {
      e.preventDefault()
      handleParagraphClick(local.paragraphNum, local, setLocal)

      return
    }

    if (e.key === "ArrowRight") {
      nextPage()
      return
    }

    if (e.key === "ArrowLeft") {
      prevPage()
      return
    }

    if (
      e.key !== "Backspace" &&
      (EXTRA_KEYS.has(e.key) || e.metaKey || e.ctrlKey)
    ) {
      return
    }

    e.preventDefault()

    if (state.get.settings.ui.readingMode) {
      showCantType()
      return
    }

    if (e.key === "Tab") {
      handleParagraphReset(local, setLocal)
      return
    }

    if (e.key === "Backspace") {
      handleBackspace(e, local, setLocal)
      getCaretPosition()
      return
    }

    handleStandardInput(e, local, setLocal)
  }

  const handleMouseMove = () => setLocal("hideCursor", false)

  const handleBlur = () => {
    if (state.get.settings.typingPrompt.focusWarning) {
      setBlur(true)
    }
  }
  const handleFocus = () => {
    if (state.get.settings.typingPrompt.focusWarning) {
      setBlur(false)
    }
  }

  onMount(() => {
    if (!config.IS_MOBILE) {
      document.addEventListener("keydown", handleTyping)
      window.addEventListener("mousemove", handleMouseMove)
      window.addEventListener("resize", getCaretPosition)
      window.addEventListener("focus", handleFocus)
      window.addEventListener("blur", handleBlur)
    }
    bootstrapCaret()
  })

  onCleanup(() => {
    if (!config.IS_MOBILE) {
      document.removeEventListener("keydown", handleTyping)
      window.removeEventListener("mousemove", handleMouseMove)
      window.removeEventListener("resize", getCaretPosition)
      window.removeEventListener("focus", handleFocus)
      window.removeEventListener("blur", handleBlur)
    }
  })

  // Scroll lines automatically
  createEffect(
    on(
      [() => local.paragraphNum, () => local.wordNum],
      (_, __, prev: number | undefined) => {
        return scrollToWord(prev, screenKeyboardPrompt)
      },
    ),
  )

  createEffect(
    on(
      () => state.get.settings.ui.readingMode,
      () => bootstrapCaret(),
    ),
  )

  const pages = createMemo(() =>
    Math.ceil(local.length / config.CHARACTERS_PER_PAGE),
  )

  const page = createMemo(
    () => Math.floor(local.lengthCompleted / config.CHARACTERS_PER_PAGE) + 1,
  )

  const progress = createMemo(() => local.lengthCompleted / local.length)

  const dailyProgress = createMemo(() => {
    return getDailyProgress(state.get.session.daily)
  })

  createEffect(() => {
    if (dailyProgress().progress >= 1 && !state.get.session.dailyDone) {
      notify({
        title: "Daily goal completed!",
        details: "Type for 15 minutes.",
        type: "ok",
        important: true,
      })
      state.set("session", "dailyDone", true)
    }
  })

  const fontTyping = createMemo(() => {
    return getFont(state.get.settings.typingPrompt.font)
  })

  const fontReading = createMemo(() => {
    return getFont(state.get.settings.readingPrompt.font)
  })

  const width = createMemo(() => {
    return state.get.settings.ui.readingMode
      ? state.get.settings.readingPrompt.lineWidth
      : state.get.settings.typingPrompt.lineWidth
  })

  const touch = { x: 0, y: 0, down: false }

  return (
    <Show when={promptData()} fallback={<Loading />}>
      <div
        classList={{
          "prompt-grid": true,
          nocursor: local.hideCursor,
        }}
      >
        <Header zone={local.hideCursor} />
        <Statusbar
          bookId={promptData()!.bookInfo.id}
          bookTitle={promptData()!.bookInfo.title}
          chapterTitle={promptData()!.chapterInfo?.title ?? ""}
          wpm={local.pageStats.wpm}
          acc={local.pageStats.acc}
          paused={local.paused}
          page={page()}
          pages={pages()}
          progress={progress()}
          dailyProgress={dailyProgress()}
          saved={local.saved}
          cantTypeAlert={cantType()}
          scrollToActive={() => initScroll(screenKeyboardPrompt)}
          resetUnfinishedParagraph={() => {
            handleParagraphReset(local, setLocal)
          }}
          invisible={
            state.get.settings.typingPrompt.zenMode && local.hideCursor
          }
        />
        <div
          classList={{ "prompt-box": true, error: !local.prevOk }}
          onTouchStart={(e) => {
            touch.x = e.touches[0].clientX
            touch.y = e.touches[0].clientY
            touch.down = true
          }}
          onTouchMove={(e) => {
            const diffX = touch.x - e.touches[0].clientX
            const diffY = touch.y - e.touches[0].clientY
            if (
              touch.down &&
              Math.abs(diffX) / 2 > Math.abs(diffY) &&
              Math.abs(diffX) > 50
            ) {
              if (diffX < 0) {
                prevPage()
              } else {
                nextPage()
              }
              touch.down = false
            }
          }}
        >
          <nav>
            <button
              class="prev"
              disabled={local.splitStart === 0}
              title="Previous page"
              onClick={prevPage}
            >
              <i class="icon-prev" />
            </button>
            <button
              class="next"
              disabled={local.splitEnd === local.screenSplits.at(-1)}
              title="Next page"
              onClick={nextPage}
            >
              <i class="icon-next" />
            </button>
          </nav>

          <Show when={blur() && !state.get.settings.ui.readingMode}>
            <div class="blur-info">
              <p>
                The window is out of focus, click anywhere on the page to start
                typing.
              </p>
            </div>
          </Show>
          <section
            classList={{
              prompt: true,
              "reading-mode": state.get.settings.ui.readingMode,
              indent: state.get.settings.ui.readingMode
                ? state.get.settings.readingPrompt.indentParagraphs
                : state.get.settings.typingPrompt.indentParagraphs,
              justify:
                (state.get.settings.ui.readingMode &&
                  state.get.settings.readingPrompt.justify) ||
                (!state.get.settings.ui.readingMode &&
                  state.get.settings.typingPrompt.justify),
              [`width-${width()}`]: true,
              [`caret-${state.get.settings.typingPrompt.caret}`]:
                !state.get.settings.ui.readingMode,
              [`caret-${caretSpeed()}`]: true,
              [`caret-${
                state.get.settings.typingPrompt.caretEasing ?? "swing"
              }`]: true,
              paused: local.paused,
              blur: blur(),
            }}
            style={{
              "--font-typing": fontTyping().font,
              "--font-typing-size": fontTyping().size,
              "--font-typing-scale": state.get.settings.typingPrompt.fontScale,
              "--line-typing-height":
                state.get.settings.typingPrompt.lineHeight,
              "--paragraphs-typing-gap":
                state.get.settings.typingPrompt.paragraphsGap,
              "--font-reading": fontReading().font,
              "--font-reading-size": fontReading().size,
              "--font-reading-scale":
                state.get.settings.readingPrompt.fontScale,
              "--line-reading-height":
                state.get.settings.readingPrompt.lineHeight,
              "--paragraphs-reading-gap":
                state.get.settings.readingPrompt.paragraphsGap,
              "--caret-height": `${caret().h}px`,
            }}
            onClick={() => {
              if (
                screenKeyboardPrompt &&
                config.IS_MOBILE &&
                !state.get.settings.ui.readingMode
              ) {
                if (
                  !state.get.settings.ui.readingMode &&
                  !state.get.settings.typingPrompt.screenKeyboard
                ) {
                  screenKeyboardPrompt.focus({ preventScroll: true })
                }

                if (config.IS_MOBILE && window.visualViewport) {
                  window.visualViewport?.addEventListener(
                    "resize",
                    () => {
                      initScroll(screenKeyboardPrompt)
                    },
                    { once: true },
                  )
                } else {
                  initScroll(screenKeyboardPrompt)
                }
              }
            }}
          >
            <textarea
              spellcheck={false}
              autocomplete="off"
              /* @ts-ignore */
              autocorrect="off"
              autocapitalize="off"
              ref={screenKeyboardPrompt!}
              onKeyUp={(e) => {
                if (e.currentTarget.value === "") {
                  const event = new KeyboardEvent("keydown", {
                    key: "Backspace",
                  })
                  handleTyping(event)
                }
              }}
              onFocus={(e) => {
                e.preventDefault()
              }}
              onInput={(e) => {
                const val = e.target.value ?? ""

                const char = val[val.length - 1]
                let key

                switch (char) {
                  case undefined:
                    return
                  case "\n":
                  case "\r":
                    key = "Enter"
                    break
                  default:
                    key = char
                }

                const event = new KeyboardEvent("keydown", { key })
                handleTyping(event)
                e.target.value = "_"
              }}
            />
            <div class="paragraphs">
              <Show
                when={
                  local.splitStart === 0 && promptData()!.chapterInfo?.title
                }
              >
                <div class="p-box">
                  <h2>{promptData()!.chapterInfo?.title}</h2>
                </div>
              </Show>
              <For
                each={local.original.slice(local.splitStart, local.splitEnd)}
              >
                {(paragraph, paragraphNum) => {
                  const offset = createMemo(
                    () => paragraphNum() + local.splitStart,
                  )

                  const pStats = createMemo(() => {
                    const wpm = local.stats?.[offset()]?.wpm
                    const acc = local.stats?.[offset()]?.acc

                    if (!wpm || !acc) {
                      return null
                    }

                    return {
                      wpm: formatNum(wpm.value),
                      acc: formatNum(acc.value * 100),
                    }
                  })

                  return (
                    <div class="p-box">
                      <p
                        classList={{
                          read: local.stats?.[offset()]?.read === true,
                          hr:
                            state.get.settings.ui.readingMode &&
                            paragraph.length === 1 &&
                            paragraph[0].length === 4 &&
                            paragraph[0].join("").startsWith("***"),
                        }}
                        onClick={async (e) => {
                          if (!state.get.settings.ui.readingMode) {
                            return
                          }

                          e.preventDefault()
                          await handleParagraphClick(offset(), local, setLocal)
                        }}
                      >
                        <Show
                          when={
                            !state.get.settings.ui.readingMode &&
                            local.paragraphNum === offset()
                          }
                        >
                          <i
                            classList={{ caret: true }}
                            style={{
                              "--left": `${caret().x}px`,
                              "--top:": `${caret().y}px`,
                              "--width:": `${caret().w}px`,
                            }}
                          />
                        </Show>
                        <For each={paragraph}>
                          {(word, wordNum) => {
                            const currentWord = () =>
                              local.typed[offset()][wordNum()]
                            const expectedWord = () =>
                              local.original[offset()][wordNum()]
                            const isInaccurate = () =>
                              !currentWord().includes(null) &&
                              currentWord().join("") !== expectedWord().join("")

                            const isActive = () =>
                              offset() === local.paragraphNum &&
                              wordNum() === local.wordNum

                            return (
                              <span
                                classList={{
                                  word: true,
                                  inaccurate: isInaccurate(),
                                  active: isActive(),
                                }}
                              >
                                {
                                  <For each={word}>
                                    {(letter, charNum) => {
                                      const currentChar = () =>
                                        local.typed[offset()][wordNum()][
                                          charNum()
                                        ]
                                      const expectedChar = () =>
                                        local.original[offset()][wordNum()][
                                          charNum()
                                        ]
                                      const isCorrect = () =>
                                        currentChar() === expectedChar()

                                      const getTypedChar = () => {
                                        let typedChar =
                                          local.typed[offset()][wordNum()][
                                            charNum()
                                          ]

                                        if (
                                          typedChar === " " &&
                                          letter !== " "
                                        ) {
                                          typedChar = "_"
                                        }

                                        return typedChar ?? letter
                                      }

                                      return (
                                        <span
                                          classList={{
                                            letter: true,
                                            current:
                                              offset() === local.paragraphNum &&
                                              wordNum() === local.wordNum &&
                                              charNum() === local.charNum,
                                            ok: isCorrect(),
                                            error:
                                              currentChar() !== null &&
                                              !isCorrect(),
                                            special:
                                              letter === config.ENTER_SYMBOL,
                                          }}
                                        >
                                          {letter === config.ENTER_SYMBOL
                                            ? ""
                                            : state.get.settings.typingPrompt
                                                  .showTypos
                                              ? getTypedChar()
                                              : letter}
                                        </span>
                                      )
                                    }}
                                  </For>
                                }
                              </span>
                            )
                          }}
                        </For>
                      </p>
                      <Show when={!state.get.settings.ui.readingMode}>
                        <div
                          classList={{
                            "p-stats": true,
                            invisible:
                              state.get.settings.typingPrompt.zenMode &&
                              local.hideCursor,
                          }}
                        >
                          <Show when={pStats()}>
                            <span>
                              <i class="icon-speed" />
                              {pStats()!.wpm}
                            </span>
                            <span>
                              <i class="icon-accuracy" />
                              {pStats()!.acc}
                            </span>
                          </Show>
                        </div>
                        <div class="p-extra">
                          <div>
                            <Show when={paragraphNumPB() === offset()}>
                              <i
                                class="icon-pb pb"
                                title="Fastest paragraph!"
                              />
                            </Show>{" "}
                            <i class="icon-extra more" />
                          </div>
                        </div>
                      </Show>
                    </div>
                  )
                }}
              </For>
            </div>
          </section>
        </div>
        <Show
          when={
            config.IS_MOBILE &&
            state.get.settings.typingPrompt.screenKeyboard &&
            !state.get.settings.ui.readingMode
          }
        >
          <ScreenKeyboard
            handleTyping={handleTyping}
            expectedChar={
              local.original[local.paragraphNum]?.[local.wordNum]?.[
                local.charNum
              ]
            }
            prev={local.prev}
            prevOk={local.prevOk}
          />
        </Show>
        <Show
          when={
            !config.IS_MOBILE &&
            state.get.settings.typingPrompt.screenKeyboard &&
            !state.get.settings.ui.readingMode
          }
        >
          <ScreenKeyboardDesktop
            handleTyping={handleTyping}
            expectedChar={
              local.original[local.paragraphNum]?.[local.wordNum]?.[
                local.charNum
              ]
            }
            prev={local.prev}
            prevOk={local.prevOk}
            charset={local.charset}
          />
        </Show>
        <Show when={!state.get.settings.ui.readingMode}>
          <button
            classList={{
              zen: true,
              active: state.get.settings.typingPrompt.zenMode,
              invisible: local.hideCursor,
            }}
            title="Click to toggle Zen Mode - hides all statistics during typing"
            onClick={() => {
              state.set("settings", "typingPrompt", "zenMode", (prev) => !prev)
            }}
          >
            <i class="icon-zen" />
            <p>Zen {state.get.settings.typingPrompt.zenMode ? "ON" : "OFF"}</p>
          </button>
        </Show>
      </div>
    </Show>
  )
}

export default Prompt
