import { useLocation, useParams } from "@solidjs/router"
import {
  type Component,
  For,
  Show,
  createEffect,
  createMemo,
  createResource,
  createSignal,
  onMount,
} from "solid-js"
import { getAccuracyThreshold } from "../../../actions/getAccuracyThreshold"
import { toggleFavorite } from "../../../actions/toggleFavorite"
import config from "../../../config"
import state from "../../../state/state"
import { isNew } from "../../../utils/dates"
import {
  formatNum,
  formatOrdinalNum,
  formatPercentage,
  formatDuration,
} from "../../../utils/formatters"
import { encodeParams } from "../../../utils/url"
import BookGenres from "../../common/book-genres/BookGenres"
import BookStrip from "../../common/book-strip/BookStrip"
import Cover from "../../common/book/Cover"
import StatsStrip from "../../common/stats-strip/StatsStrip"
import "./BookDetails.css"
import { getConsistencyThreshold } from "../../../actions/getConsistencyThreshold"
import { summarizeChapterStats } from "../prompt/prompt-actions/summarizeStats"
import { fetchBookDetails } from "./fetchBookDetails"
import Loading from "../../common/loading/Loading"

type Props = {
  value: number | string
  unit: string
  type?: string
  class?: string
  title?: string
}

const MiniStat: Component<Props> = (props) => {
  return (
    <div
      classList={{
        ministat: true,
        [props.type ?? "normal"]: props.type !== undefined,
        [props.class ?? "class"]: props.class !== undefined,
      }}
      title={props.title}
    >
      <strong>{props.value}</strong>
      <small>{props.unit}</small>
    </div>
  )
}

const BookDetails: Component = () => {
  const location = useLocation()
  const params = useParams()
  const [data] = createResource(params.id, fetchBookDetails)
  const [more, setMore] = createSignal(false)

  const favorite = createMemo(() => {
    return (
      state.get.booksIndex.find((book) => book.id === data()?.id)?.favorite ??
      false
    )
  })

  const recent = createMemo(() => {
    return summarizeChapterStats(state.get.statsSummary.recent, 0, false)
  })

  const totalTypingTime = createMemo(() => {
    const averageWpm = recent().wpm.value || 60
    const timeMs = ((data()?.totalChars ?? 0) / 5 / averageWpm) * 60 * 1000
    return formatDuration(timeMs, true) as string
  })

  onMount(() => {
    createEffect(() => {
      if (location.hash && data()) {
        document.querySelector(location.hash)?.scrollIntoView()
      }
    })
  })

  const nextChapter = createMemo(() => {
    const chaptetId = data()?.nextChapterId ?? ""

    return {
      link: `/prompt/${params.id}__${chaptetId}`,
      text:
        data()?.progress === 1
          ? "TRY AGAIN"
          : data()?.progress === 0
            ? "START"
            : "CONTINUE",
    }
  })

  const description = createMemo(() => {
    const paragraphs = data()?.longDescription ?? []

    if (paragraphs.length === 0) {
      return ["No description."]
    }

    if (paragraphs.length === 1 || more()) {
      return paragraphs
    }

    return paragraphs.slice(0, 1)
  })

  const statsStripData = createMemo(() => {
    if (!data()?.time) {
      return null
    }

    const acc = data()?.acc ?? null
    const con = data()?.con ?? null
    const duration = data()?.time ? formatDuration(data()?.time ?? 0) : null

    return [
      {
        name: "Speed",
        val: data()?.wpm ?? null,
        isPercent: false,
        precision: 2,
        unit: " wpm",
        tooltipTitle:
          "Words per minute, where one word equals five characters (including spaces)",
        tooltipVal: data()?.raw
          ? `${formatNum(data()!.raw ?? 0, 2)} raw`
          : undefined,
      },
      {
        name: "Accuracy",
        val: acc,
        isPercent: true,
        precision: 2,
        unit: " %",
        type: acc !== null ? getAccuracyThreshold(acc) : undefined,
        tooltipTitle: "Percentage of correct keystrokes",
        tooltipVal: "Target accuracy >= 98%",
      },
      {
        name: "Consistency",
        val: con,
        isPercent: true,
        precision: 2,
        unit: " %",
        type: con !== null ? getConsistencyThreshold(con) : undefined,
        tooltipTitle: "Measures how consistent is the time between keystrokes",
        tooltipVal: "Target consistency >= 80%",
      },
      {
        name: "Typing Time",
        val: duration,
        isPercent: false,
        precision: 0,
        tooltipVal: "hours : minutes : seconds",
        tooltipTitle: "Total time spent typing",
      },
    ]
  })

  const sourceText = createMemo(() => {
    const value = data()?.source?.value ?? "unknown"
    if (data()?.source?.isUrl) {
      try {
        return new URL(value).origin
      } catch {}
    }

    return data()?.source?.value
  })

  return (
    <Show when={data()} fallback={<Loading />}>
      <div class="book-details">
        <section class="info" style={`--pages: ${data()?.totalPages}`}>
          <nav>
            <div>
              <button
                classList={{ favorite: true, active: favorite() }}
                onClick={() => toggleFavorite(data()?.id ?? "")}
                title={
                  favorite() ? "Remove from favorites" : "Add to favorites"
                }
                type="button"
              >
                <i class={favorite() ? "icon-heart" : "icon-heart-off"} />
              </button>
              <a class="button primary type" href={nextChapter().link}>
                {nextChapter().text}
              </a>
            </div>
            {/* <div> */}
            {/*   <button class="edit primary warning">EDIT</button> */}
            {/* </div> */}
          </nav>
          <h2>{data()?.title}</h2>
          <Cover url={`/books/${params.id}/cover.min.webp`} />
          <Show when={data()?.series}>
            <p class="series">
              <strong>{formatOrdinalNum(data()?.part ?? 0)}</strong> book of{" "}
              {/^(a |an |the )/i.test(data()?.series ?? "") ? " " : "the "}
              <a
                title="Show all books in the series"
                href={`/books?${encodeParams({
                  series: data()?.series ?? "",
                  order: "part",
                })}`}
              >
                {data()?.series}
              </a>{" "}
              series
            </p>
          </Show>
          <p class="author">
            by{" "}
            <a
              title="Show all books by the author"
              href={`/books?${encodeParams({
                author: data()?.author ?? "",
              })}`}
            >
              {data()?.author}
            </a>
          </p>
          <BookStrip
            pages={data()?.totalPages ?? 0}
            eta={totalTypingTime()}
            progress={data()?.progress ?? 0}
            isNew={isNew(data()?.createdAt ?? 0)}
            style="--padding-v: var(--space-xs); --padding-h: var(--space-s); --font-size: var(--font-m); --margin: var(--space-s)"
            wpm={recent().wpm.value}
          />
          <BookGenres
            genres={data()?.genres ?? []}
            style="--padding-v: var(--space-xs); --padding-h: var(--space-s); --font-size: var(--font-s); --margin: var(--space-m)"
          />
          <p class="source">
            Source:{" "}
            <Show when={data()?.source?.isUrl} fallback={sourceText()}>
              <a href={data()?.source?.value} target="_blank" rel="noreferrer">
                {sourceText()}
              </a>
            </Show>
          </p>
          <Show when={statsStripData()}>
            <StatsStrip data={statsStripData()!} />
          </Show>
          <p class="short-description">{data()?.description}</p>
          <article class="description">
            <p>***</p>
            <For each={description()}>{(par) => <p>{par}</p>}</For>
          </article>
          <Show when={more()}>
            <p class="rights">
              <strong>Rights: </strong>
              {data()?.rights ?? "no info"}
            </p>
          </Show>
          <p class="more">
            <a
              href="#"
              onClick={(e) => {
                e.preventDefault()
                setMore(!more())
                if (!more()) {
                  window.scrollTo({ top: 0, behavior: "instant" })
                }
              }}
            >
              [{more() ? "less" : "more"}]
            </a>
          </p>
        </section>
        <hr />
        <section class="chapters" id="toc">
          <h2>Chapters</h2>
          <ul>
            <For each={data()?.chapters}>
              {(chapter) => {
                const pages = createMemo(() =>
                  Math.ceil(chapter.length / config.CHARACTERS_PER_PAGE),
                )

                const words = createMemo(() => Math.ceil(chapter.length / 5))

                const status = createMemo(() => {
                  if (chapter.progress === 1) {
                    return { val: "100%", unit: "done", type: "ok" }
                  }

                  if (chapter.progress > 0) {
                    return {
                      val: formatPercentage(chapter.progress),
                      unit: "done",
                      type: "warning",
                    }
                  }

                  if (chapter.skip) {
                    return { val: "SKIP", unit: "THIS", type: "" }
                  }

                  return { val: "", unit: "", type: "" }
                })

                const typingTime = createMemo(() => {
                  const averageWpm = recent().wpm.value || 60
                  const timeMs = (words() / averageWpm) * 60 * 1000
                  return formatDuration(timeMs, true)
                })

                return (
                  <li
                    classList={{
                      skipped: chapter.skip && chapter.progress === 0,
                      done: chapter.progress === 1,
                      "in-progress":
                        chapter.progress > 0 && chapter.progress < 1,
                    }}
                  >
                    <a
                      href={`/prompt/${data()?.id}__${chapter.id}`}
                      class="chapter-link"
                    >
                      <p
                        classList={{
                          "chapter-title": true,
                          "full-size": chapter.progress > 0,
                        }}
                      >
                        {chapter.title}
                      </p>
                    </a>
                    <div class="chapter-right">
                      <Show when={chapter.progress > 0 && chapter.wpm}>
                        <a
                          href={`/results/${data()?.id}__${
                            chapter.id
                          }?show=chapter`}
                        >
                          <MiniStat value={"FULL"} unit="stats" />
                        </a>
                      </Show>
                      <Show when={chapter.wpm}>
                        <MiniStat
                          value={formatNum(chapter.wpm ?? 0)}
                          unit="wpm"
                        />
                      </Show>
                      <Show when={chapter.acc}>
                        <MiniStat
                          value={formatPercentage(chapter.acc ?? 0)}
                          unit="acc"
                          type={getAccuracyThreshold(chapter.acc ?? 0)}
                        />
                      </Show>
                      <Show when={status().val}>
                        <MiniStat
                          value={status().val}
                          unit={status().unit}
                          type={status().type}
                          class={
                            chapter.progress === 0
                              ? "condensed-status"
                              : undefined
                          }
                        />
                      </Show>
                      <MiniStat
                        value={pages()}
                        unit={`page${pages() !== 1 ? "s" : ""}`}
                        class={
                          chapter.progress === 0
                            ? "condensed-page-count"
                            : undefined
                        }
                        title={`${formatNum(words())} words | ~${typingTime()}`}
                      />
                      {/* <span class="chapter-skip"> */}
                      {/*   <label> */}
                      {/*     <i */}
                      {/*       class={`icon-toggle-${chapter.skip ? "off" : "on"}`} */}
                      {/*     /> */}
                      {/*     <input type="checkbox" /> */}
                      {/*   </label> */}
                      {/* </span> */}
                    </div>
                  </li>
                )
              }}
            </For>
          </ul>
        </section>
      </div>
    </Show>
  )
}

export default BookDetails
