import { type Component, createEffect, onCleanup, onMount, on } from "solid-js"
import state from "../../../state/state"
import type { Dataset, SimpleDataset } from "../../../types/common"
import "./Chart.css"
import { StatsChart } from "./results-util/StatsChart"
import { ActivityChart } from "./results-util/ActivityChart"

type StatsData = {
  wpm: Dataset
  acc: Dataset
  con: Dataset
  labels: (number | string)[]
}

type ActivityData = {
  len: SimpleDataset
  time: SimpleDataset
  labels: (number | string)[]
}

type Props = {
  type?: "activity" | "stats"
  data: StatsData | ActivityData
  wpmPB: number
  dataLabel: string
  tooltipLabel?: string
}

const Chart: Component<Props> = (props) => {
  let canvasRef: HTMLCanvasElement | undefined
  let chart: StatsChart | ActivityChart

  onMount(() => {
    const ctx = canvasRef?.getContext("2d")

    if (ctx) {
      if (props.type === "activity") {
        chart = new ActivityChart(ctx, props.dataLabel)
        chart.load(state.get.settings.ui.darkmode, props.data as ActivityData)
      } else {
        chart = new StatsChart(ctx, props.dataLabel, props.tooltipLabel)
        chart.load(
          state.get.settings.ui.darkmode,
          props.wpmPB,
          props.data as StatsData,
          true,
        )
      }
    }
  })

  createEffect(
    on(
      () => props.data,
      () => {
        chart.update(
          props.data as any,
          props.dataLabel,
          props.tooltipLabel,
          props.wpmPB,
        )
      },
      { defer: true },
    ),
  )

  onCleanup(() => {
    if (chart) {
      chart.destroy()
    }
  })

  createEffect(() => {
    if (chart) {
      chart.toggleDarkmode(state.get.settings.ui.darkmode)
    }
  })

  return (
    <div class="chart">
      <canvas ref={canvasRef}></canvas>
    </div>
  )
}

export default Chart
