import "./Settings.css"
import {
  createMemo,
  type Component,
  createSignal,
  For,
  Show,
  Switch,
  Match,
} from "solid-js"
import ButtonToggle from "../../common/button-toggle/ButtonToggle"
import state from "../../../state/state"
import {
  Caret,
  CaretSpeed,
  CaretEasing,
  KeyboardIndicator,
  KeyboardKeycaps,
} from "../../../types/common"
import { fontsByType } from "./fonts"
import { getFont } from "../../../actions/getFont"
import config from "../../../config"
import ScreenKeyboard from "../../common/screen-keyboard/ScreenKeyboard"
import ScreenKeyboardDesktop from "../../common/screen-keyboard/ScreenKeyboardDesktop"
import {
  DesktopLayout,
  layouts as desktopLayouts,
} from "../../../data/desktopKeyboardLayouts"
import {
  MobileLayout,
  layouts as mobileLayouts,
} from "../../../data/mobileKeyboardLayouts"
import { getCharset } from "../../../libs/charsets"
import ButtonMulti from "../../common/button-toggle/ButtonMulti"
import { themes } from "../../../data/themes"
import { exportSave, importSave } from "../../../io/save"
import Confirm from "../../common/confirm/Confirm"
import { resetProgress, resetSettings } from "../../../io/reset"
// import { FontsDevPreview } from "./FontsDevPreview"

const previewContents = [
  (type: "typing" | "reading") => [
    `This is a preview of the ${type} prompt. Click to toggle between different texts.`,
  ],
  () => ["The quick brown fox jumps over the lazy dog."],
  () => [
    "Aa Bb Cc Dd Ee Ff Gg Hh Ii Jj Kk Ll Mm Nn Oo Pp Qq Rr Ss Tt Uu Vv Ww Xx Yy Zz 0123456789?!",
  ],
  () => [
    `Alice was beginning to get very tired of sitting by her sister on the bank, and of having nothing to do: once or twice she had peeped into the book her sister was reading, but it had no pictures or conversations in it, "and what is the use of a book," thought Alice "without pictures or conversations?"`,
    `So she was considering in her own mind (as well as she could, for the hot day made her feel very sleepy and stupid), whether the pleasure of making a daisy-chain would be worth the trouble of getting up and picking the daisies, when suddenly a White Rabbit with pink eyes ran close by her."`,
  ],
]

const Settings: Component = () => {
  const [previewTypingIndex, setPreviewTypingIndex] = createSignal(0)
  const [previewReadingIndex, setPreviewReadingIndex] = createSignal(0)
  const [confirm, setConfirm] = createSignal<string | null>(null)
  const [loading, setLoading] = createSignal({
    export: false,
    import: false,
    deleteProgress: false,
    resetSettings: true,
  })

  const genres = createMemo(() => {
    return [
      ...new Set(
        state.get.booksIndex.map((bookInfo) => bookInfo.genres).flat(),
      ),
    ]
      .sort()
      .map((genre) => ({
        name: genre,
        value: genre,
      }))
  })

  const fontTyping = createMemo(() => {
    return getFont(state.get.settings.typingPrompt.font)
  })

  const fontReading = createMemo(() => {
    return getFont(state.get.settings.readingPrompt.font)
  })

  const keyboardLayouts = createMemo(() => {
    const layouts = config.IS_MOBILE
      ? mobileLayouts
      : desktopLayouts[config.IS_APPLE ? "mac" : "pc"]

    return Object.entries(layouts)
      .filter((layout) => !layout[1].hidden)
      .map((layout) => ({
        name: () => <span>{layout[1].name}</span>,
        value: layout[0],
      }))
  })

  const themeOptions = createMemo(() => {
    return themes.map((theme) => ({
      name: () => (
        <div>
          {theme.name}
          <small>{theme.details ?? "standard"}</small>
          <div classList={{ "theme-preview": true, [theme.value]: true }}>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
            <div></div>
          </div>
        </div>
      ),
      value: theme.value,
    }))
  })

  return (
    <main class="settings">
      {/* <FontsDevPreview /> */}
      <details>
        <summary>
          <h2 class="icon-next">Typing Mode</h2>
        </summary>
        <figure>
          <div class="prompt-box">
            <div
              classList={{
                prompt: true,
                indent: state.get.settings.typingPrompt.indentParagraphs,
                justify: state.get.settings.typingPrompt.justify,
                [`width-${state.get.settings.typingPrompt.lineWidth}`]: true,
              }}
              style={{
                "--font-typing": fontTyping().font,
                "--font-typing-size": fontTyping().size,
                "--font-typing-scale":
                  state.get.settings.typingPrompt.fontScale,
                "--line-typing-height":
                  state.get.settings.typingPrompt.lineHeight,
                "--paragraphs-typing-gap":
                  state.get.settings.typingPrompt.paragraphsGap,
              }}
              onClick={() =>
                setPreviewTypingIndex(
                  (prev) => (prev + 1) % previewContents.length,
                )
              }
            >
              <div class="paragraphs">
                <For each={previewContents[previewTypingIndex()]("typing")}>
                  {(paragraph) => (
                    <div class="p-box">
                      <p>{paragraph}</p>
                    </div>
                  )}
                </For>
              </div>
            </div>
          </div>
        </figure>
        <article class="setting">
          <header>
            <h3>Font Size</h3>
          </header>
          <ButtonToggle
            style="min-width: 2.4em;"
            items={[
              { name: "1", value: "0.875" },
              { name: "2", value: "1" },
              { name: "3", value: "1.25" },
              { name: "4", value: "1.5" },
              { name: "5", value: "1.75" },
              { name: "6", value: "2" },
              { name: "7", value: "2.25" },
              { name: "8", value: "2.5" },
            ]}
            active={state.get.settings.typingPrompt.fontScale}
            setValue={(val) => {
              state.set("settings", "typingPrompt", "fontScale", val)
            }}
          />
        </article>
        <article class="setting">
          <header>
            <h3>Line Height</h3>
          </header>
          <ButtonToggle
            style="min-width: 4em;"
            items={[
              { name: "XS", value: "1.2" },
              { name: "S", value: "1.3" },
              { name: "M", value: "1.5" },
              { name: "L", value: "1.8" },
              { name: "XL", value: "2.2" },
            ]}
            active={state.get.settings.typingPrompt.lineHeight}
            setValue={(val) => {
              state.set("settings", "typingPrompt", "lineHeight", val)
            }}
          />
        </article>
        <Show when={!config.IS_MOBILE}>
          <article class="setting">
            <header>
              <h3>Line Width</h3>
            </header>
            <ButtonToggle
              style="min-width: 4em;"
              items={[
                {
                  name: "Auto",
                  value: "auto",
                  tooltip: "The width depends on the selected font.",
                },
                { name: "Min", value: "min" },
                { name: "Mid", value: "mid" },
                { name: "Max", value: "max" },
              ]}
              active={state.get.settings.typingPrompt.lineWidth}
              setValue={(val) => {
                state.set("settings", "typingPrompt", "lineWidth", val)
              }}
            />
          </article>
        </Show>
        <article class="setting">
          <header>
            <h3>Paragraphs Gap</h3>
          </header>
          <ButtonToggle
            style="min-width: 4em;"
            items={[
              { name: "None", value: "0" },
              { name: "S", value: "0.25" },
              { name: "M", value: "0.5" },
              { name: "L", value: "0.75" },
            ]}
            active={state.get.settings.typingPrompt.paragraphsGap}
            setValue={(val) => {
              state.set("settings", "typingPrompt", "paragraphsGap", val)
            }}
          />
        </article>
        <article class="setting">
          <header>
            <h3>Indent Paragraphs</h3>
          </header>
          <ButtonToggle
            style="min-width: 6em;"
            items={[
              { name: "Yes", value: "1" },
              { name: "No", value: "0" },
            ]}
            active={
              state.get.settings.typingPrompt.indentParagraphs ? "1" : "0"
            }
            setValue={(val) => {
              state.set(
                "settings",
                "typingPrompt",
                "indentParagraphs",
                val === "1",
              )
            }}
          />
        </article>
        <article class="setting">
          <header>
            <h3>Justify Text</h3>
          </header>
          <ButtonToggle
            style="min-width: 6em;"
            items={[
              { name: "Yes", value: "1" },
              { name: "No", value: "0" },
            ]}
            active={state.get.settings.typingPrompt.justify ? "1" : "0"}
            setValue={(val) => {
              state.set("settings", "typingPrompt", "justify", val === "1")
            }}
          />
        </article>
        <article class="setting wide">
          <header>
            <h3>Font Style</h3>
            <p>
              Monospace fonts are recommended for the optimal typing experience!
            </p>
          </header>
          <For each={fontsByType}>
            {(fonts) => (
              <ButtonToggle
                class="font"
                items={fonts.map((font) => ({
                  name: () => (
                    <span>
                      {font.name}
                      <small
                        style={{
                          "font-weight":
                            font.type === "monospace" ? "600" : "400",
                          color:
                            font.type === "monospace"
                              ? "var(--color-fg-ok)"
                              : "inherit",
                        }}
                      >
                        {font.type}
                      </small>
                    </span>
                  ),
                  value: font.value,
                }))}
                active={state.get.settings.typingPrompt.font}
                setValue={(val) => {
                  state.set("settings", "typingPrompt", "font", val)
                }}
              />
            )}
          </For>
        </article>
        <article class="mono">
          <header>
            <h3>Caret Style</h3>
          </header>
          <ButtonToggle
            style={{
              "min-width": "6rem",
              "font-family": fontTyping().font ?? '"Open Sans", sans-serif',
              "font-size": `calc(1rem * ${fontTyping().size})`,
            }}
            items={[
              {
                name: () => (
                  <span>
                    <i class="caret-line">L</i>ine
                  </span>
                ),
                value: "line",
              },
              {
                name: () => (
                  <span>
                    <i class="caret-floor">F</i>loor
                  </span>
                ),
                value: "floor",
              },
              {
                name: () => (
                  <span>
                    <i class="caret-block">B</i>lock
                  </span>
                ),
                value: "block",
              },
            ]}
            active={state.get.settings.typingPrompt.caret}
            setValue={(val) => {
              state.set("settings", "typingPrompt", "caret", val as Caret)
            }}
          />
        </article>
        <article>
          <header>
            <h3>Caret Speed</h3>
          </header>
          <ButtonToggle
            style={{
              "min-width": "6rem",
            }}
            items={[
              { name: "Slow", value: "slow" },
              { name: "Medium", value: "medium" },
              { name: "Fast", value: "fast" },
              { name: "Turbo", value: "vary-fast" },
              { name: "Instant", value: "instant" },
            ]}
            active={state.get.settings.typingPrompt.caretSpeed}
            setValue={(val) => {
              state.set(
                "settings",
                "typingPrompt",
                "caretSpeed",
                val as CaretSpeed,
              )
            }}
          />
        </article>
        <article>
          <header>
            <h3>Caret Animation</h3>
            <p>
              It controls how the caret accelerates and decelerates. You can
              preview different behaviors{" "}
              <a href="https://codepen.io/caderek/full/qBGmJJP" target="_blank">
                here
              </a>
              .
            </p>
          </header>
          <ButtonToggle
            style={{
              "min-width": "6rem",
            }}
            items={[
              { name: "Linear", value: "linear" },
              { name: "Swing", value: "swing" },
              { name: "Ease", value: "ease" },
              { name: "Ease-in", value: "ease-in" },
              { name: "Ease-out", value: "ease-out" },
            ]}
            active={state.get.settings.typingPrompt.caretEasing}
            setValue={(val) => {
              state.set(
                "settings",
                "typingPrompt",
                "caretEasing",
                val as CaretEasing,
              )
            }}
          />
        </article>
        <article class="setting">
          <header>
            <h3>Typos Indication</h3>
            <p>
              First option is recommended, as it's closer to the real-world
              typing. If you are not using a monospace font, the second option
              prevents layout shifts.
            </p>
          </header>
          <ButtonToggle
            style="min-width: 10em;"
            items={[
              {
                name: "Replace",
                value: "replace",
              },
              {
                name: "Highlight Only",
                value: "highlight-ony",
              },
            ]}
            active={
              state.get.settings.typingPrompt.showTypos
                ? "replace"
                : "highlight-ony"
            }
            setValue={(val) => {
              state.set(
                "settings",
                "typingPrompt",
                "showTypos",
                val === "replace",
              )
            }}
          />
        </article>
        <article class="setting">
          <header>
            <h3>Backspace Behavior</h3>
            <p>
              Selected option will be used for <code>Backspace</code>, other
              option will be used for{" "}
              <code>
                {config.IS_MOBILE ? "Shift" : config.IS_APPLE ? "Cmd" : "Ctrl"}
                +Backspace
              </code>
              .
            </p>
          </header>
          <ButtonToggle
            style="min-width: 10em;"
            items={[
              {
                name: "Delete Character",
                value: "char",
              },
              {
                name: "Delete Word",
                value: "word",
              },
            ]}
            active={
              state.get.settings.typingPrompt.backspaceWholeWord
                ? "word"
                : "char"
            }
            setValue={(val) => {
              state.set(
                "settings",
                "typingPrompt",
                "backspaceWholeWord",
                val === "word",
              )
            }}
          />
        </article>
        <article class="setting">
          <header>
            <h3>Out-of-focus Warning</h3>
            <p>
              Shows the warning overlay when the window is out of focus, and you
              can't type.
            </p>
          </header>
          <ButtonToggle
            style="min-width: 6em;"
            items={[
              { name: "Yes", value: "1" },
              { name: "No", value: "0" },
            ]}
            active={state.get.settings.typingPrompt.focusWarning ? "1" : "0"}
            setValue={(val) => {
              state.set("settings", "typingPrompt", "focusWarning", val === "1")
            }}
          />
        </article>
      </details>
      <details>
        <summary>
          <h2 class="icon-next">Reading Mode</h2>
        </summary>
        <figure>
          <div class="prompt-box">
            <div
              classList={{
                prompt: true,
                "reading-mode": true,
                indent: state.get.settings.readingPrompt.indentParagraphs,
                justify: state.get.settings.readingPrompt.justify,
                [`width-${state.get.settings.readingPrompt.lineWidth}`]: true,
              }}
              style={{
                "--font-reading": fontReading().font,
                "--font-reading-size": fontReading().size,
                "--font-reading-scale":
                  state.get.settings.readingPrompt.fontScale,
                "--line-reading-height":
                  state.get.settings.readingPrompt.lineHeight,
                "--paragraphs-reading-gap":
                  state.get.settings.readingPrompt.paragraphsGap,
              }}
              onClick={() =>
                setPreviewReadingIndex(
                  (prev) => (prev + 1) % previewContents.length,
                )
              }
            >
              <div class="paragraphs">
                <For each={previewContents[previewReadingIndex()]("reading")}>
                  {(paragraph) => (
                    <div class="p-box">
                      <p>{paragraph}</p>
                    </div>
                  )}
                </For>
              </div>
            </div>
          </div>
        </figure>
        <article class="setting">
          <header>
            <h3>Font Size</h3>
          </header>
          <ButtonToggle
            style="min-width: 4em;"
            items={[
              { name: "XS", value: "0.875" },
              { name: "S", value: "1" },
              { name: "M", value: "1.25" },
              { name: "L", value: "1.5" },
              { name: "XL", value: "2" },
            ]}
            active={state.get.settings.readingPrompt.fontScale}
            setValue={(val) => {
              state.set("settings", "readingPrompt", "fontScale", val)
            }}
          />
        </article>
        <article class="setting">
          <header>
            <h3>Line Height</h3>
          </header>
          <ButtonToggle
            style="min-width: 4em;"
            items={[
              { name: "XS", value: "1.1" },
              { name: "S", value: "1.3" },
              { name: "M", value: "1.5" },
              { name: "L", value: "1.8" },
              { name: "XL", value: "2.2" },
            ]}
            active={state.get.settings.readingPrompt.lineHeight}
            setValue={(val) => {
              state.set("settings", "readingPrompt", "lineHeight", val)
            }}
          />
        </article>
        <Show when={!config.IS_MOBILE}>
          <article class="setting">
            <header>
              <h3>Line Width</h3>
            </header>
            <ButtonToggle
              style="min-width: 4em;"
              items={[
                {
                  name: "Auto",
                  value: "auto",
                  tooltip: "The width depends on the selected font.",
                },
                { name: "Min", value: "min" },
                { name: "Mid", value: "mid" },
                { name: "Max", value: "max" },
              ]}
              active={state.get.settings.readingPrompt.lineWidth}
              setValue={(val) => {
                state.set("settings", "readingPrompt", "lineWidth", val)
              }}
            />
          </article>
        </Show>
        <article class="setting">
          <header>
            <h3>Paragraphs Gap</h3>
          </header>
          <ButtonToggle
            style="min-width: 4em;"
            items={[
              { name: "None", value: "0" },
              { name: "S", value: "0.25" },
              { name: "M", value: "0.5" },
              { name: "L", value: "0.75" },
            ]}
            active={state.get.settings.readingPrompt.paragraphsGap}
            setValue={(val) => {
              state.set("settings", "readingPrompt", "paragraphsGap", val)
            }}
          />
        </article>
        <article class="setting">
          <header>
            <h3>Indent Paragraphs</h3>
          </header>
          <ButtonToggle
            style="min-width: 6em;"
            items={[
              { name: "Yes", value: "1" },
              { name: "No", value: "0" },
            ]}
            active={
              state.get.settings.readingPrompt.indentParagraphs ? "1" : "0"
            }
            setValue={(val) => {
              state.set(
                "settings",
                "readingPrompt",
                "indentParagraphs",
                val === "1",
              )
            }}
          />
        </article>
        <article class="setting">
          <header>
            <h3>Justify Text</h3>
          </header>
          <ButtonToggle
            style="min-width: 6em;"
            items={[
              { name: "Yes", value: "1" },
              { name: "No", value: "0" },
            ]}
            active={state.get.settings.readingPrompt.justify ? "1" : "0"}
            setValue={(val) => {
              state.set("settings", "readingPrompt", "justify", val === "1")
            }}
          />
        </article>
        <article class="setting wide">
          <header>
            <h3>Font Style</h3>
          </header>
          <For each={fontsByType}>
            {(fonts) => (
              <ButtonToggle
                class="font"
                items={fonts.map((font) => ({
                  name: () => (
                    <span>
                      {font.name}
                      <small>{font.type}</small>
                    </span>
                  ),
                  value: font.value,
                }))}
                active={state.get.settings.readingPrompt.font}
                setValue={(val) => {
                  state.set("settings", "readingPrompt", "font", val)
                }}
              />
            )}
          </For>
        </article>
      </details>
      <details>
        <summary>
          <h2 class="icon-next">Screen Keyboard</h2>
        </summary>

        <Switch>
          <Match
            when={
              state.get.settings.typingPrompt.screenKeyboard && config.IS_MOBILE
            }
          >
            <figure>
              <ScreenKeyboard
                handleTyping={() => {}}
                expectedChar={"a"}
                prev={""}
                prevOk={true}
              />
            </figure>
          </Match>
          <Match
            when={
              state.get.settings.typingPrompt.screenKeyboard &&
              !config.IS_MOBILE
            }
          >
            <figure>
              <ScreenKeyboardDesktop
                handleTyping={() => {}}
                expectedChar={"a"}
                prev={""}
                prevOk={true}
                charset={getCharset("en")}
              />
            </figure>
          </Match>
        </Switch>
        <article class="setting">
          <header>
            <h3>
              {config.IS_MOBILE ? "Keyboard Type" : "Show Screen Keyboard"}
            </h3>
          </header>
          <ButtonToggle
            style={{ "min-width": config.IS_MOBILE ? "8em" : "6em" }}
            items={[
              { name: config.IS_MOBILE ? "Dedicated" : "Yes", value: "1" },
              { name: config.IS_MOBILE ? "Built-in" : "No", value: "0" },
            ]}
            active={state.get.settings.typingPrompt.screenKeyboard ? "1" : "0"}
            setValue={(val) => {
              state.set(
                "settings",
                "typingPrompt",
                "screenKeyboard",
                val === "1",
              )
            }}
          />
        </article>
        <Show
          when={
            config.IS_MOBILE &&
            !config.IS_APPLE &&
            state.get.settings.typingPrompt.screenKeyboard
          }
        >
          <article class="setting">
            <header>
              <h3>Haptic Feedback</h3>
            </header>
            <ButtonToggle
              style="min-width: 4.8em;"
              items={[
                { name: "None", value: "0" },
                { name: "Low", value: "20" },
                { name: "Medium", value: "40" },
                { name: "High", value: "60" },
              ]}
              active={String(state.get.settings.typingPrompt.keyboardHaptics)}
              setValue={(val) => {
                const force = Number(val)
                state.set("settings", "typingPrompt", "keyboardHaptics", force)

                if (force > 0) {
                  navigator?.vibrate(force)
                }
              }}
            />
          </article>
        </Show>
        <Show when={state.get.settings.typingPrompt.screenKeyboard}>
          <article class="setting">
            <header>
              <h3>Key Thickness</h3>
            </header>
            <ButtonToggle
              style="min-width: 4.8em;"
              items={[
                { name: "None", value: "0px" },
                { name: "Low", value: "2px" },
                { name: "Medium", value: "4px" },
                { name: "High", value: "6px" },
              ]}
              active={state.get.settings.typingPrompt.keyboardThickness}
              setValue={(val) => {
                state.set("settings", "typingPrompt", "keyboardThickness", val)
              }}
            />
          </article>
        </Show>
        <Show when={state.get.settings.typingPrompt.screenKeyboard}>
          <article class="setting">
            <header>
              <h3>Key Style</h3>
            </header>
            <ButtonToggle
              style="min-width: 6.7em;"
              items={[
                { name: "Standard", value: "standard" },
                { name: "Semi-Blank", value: "semi-blank" },
                { name: "Blank", value: "blank" },
              ]}
              active={state.get.settings.typingPrompt.keyboardKeycaps}
              setValue={(val) => {
                state.set(
                  "settings",
                  "typingPrompt",
                  "keyboardKeycaps",
                  val as KeyboardKeycaps,
                )
              }}
            />
          </article>
        </Show>
        <Show when={state.get.settings.typingPrompt.screenKeyboard}>
          <article class="setting">
            <header>
              <h3>Key Indicators</h3>
              <p>
                <code>Live</code> highlights the currently pressed characters,{" "}
                <code>Next</code> marks the next characters that the user should
                type.
              </p>
            </header>
            <ButtonToggle
              style="min-width: 5em;"
              items={[
                { name: "None", value: "none" },
                { name: "Live", value: "last" },
                { name: "Next", value: "next" },
                { name: "Both", value: "both" },
              ]}
              active={state.get.settings.typingPrompt.keyboardIndicator}
              setValue={(val) => {
                state.set(
                  "settings",
                  "typingPrompt",
                  "keyboardIndicator",
                  val as KeyboardIndicator,
                )
              }}
            />
          </article>
        </Show>
        <Show when={state.get.settings.typingPrompt.screenKeyboard}>
          <article class="setting wide">
            <header>
              <h3>Keyboard Layout</h3>
              <Show when={!config.IS_MOBILE}>
                <p>
                  This option does not remap your keybord, you still have to
                  change the layout in your system settings.
                </p>
              </Show>
            </header>
            <Switch>
              <Match when={config.IS_MOBILE}>
                <ButtonToggle
                  class="font"
                  items={keyboardLayouts()}
                  active={state.get.settings.typingPrompt.keyboardLayoutMobile}
                  setValue={(val) => {
                    state.set(
                      "settings",
                      "typingPrompt",
                      "keyboardLayoutMobile",
                      val as MobileLayout,
                    )
                  }}
                />
              </Match>
              <Match when={!config.IS_MOBILE}>
                <ButtonToggle
                  class="font"
                  items={keyboardLayouts()}
                  active={state.get.settings.typingPrompt.keyboardLayout}
                  setValue={(val) => {
                    state.set(
                      "settings",
                      "typingPrompt",
                      "keyboardLayout",
                      val as DesktopLayout,
                    )
                  }}
                />
              </Match>
            </Switch>
          </article>
        </Show>
        <Show when={state.get.settings.typingPrompt.screenKeyboard}>
          <article class="setting wide">
            <header>
              <h3>Keyboard Font</h3>
            </header>
            <For each={fontsByType}>
              {(fonts) => (
                <ButtonToggle
                  class="font"
                  items={fonts.map((font) => ({
                    name: () => (
                      <span>
                        {font.name}
                        <small>{font.type}</small>
                      </span>
                    ),
                    value: font.value,
                  }))}
                  active={state.get.settings.typingPrompt.keyBoardFont}
                  setValue={(val) => {
                    state.set("settings", "typingPrompt", "keyBoardFont", val)
                  }}
                />
              )}
            </For>
          </article>
        </Show>
      </details>
      <details>
        <summary>
          <h2 class="icon-next">Themes</h2>
        </summary>
        <hr />
        <article class="setting wide">
          <header>
            <p>
              Every theme has two versions - light and dark. More themes are
              coming soon.
            </p>
          </header>
          <ButtonToggle
            class="font"
            items={themeOptions()}
            active={state.get.settings.ui.theme}
            setValue={(val) => state.set("settings", "ui", "theme", val)}
          />
        </article>
      </details>
      <details>
        <summary>
          <h2 class="icon-next">Recommendations</h2>
        </summary>
        <hr />
        <article class="setting wide">
          <header>
            <h3>Preferred Length</h3>
          </header>
          <ButtonToggle
            style="min-width: 5em;"
            items={[
              { name: "100 pages", value: "100" },
              { name: "200 pages", value: "200" },
              { name: "300 pages", value: "300" },
              { name: "400 pages", value: "400" },
              { name: "500 pages", value: "500" },
              { name: "All", value: "0" },
            ]}
            active={String(state.get.settings.ui.favoriteMaxLength)}
            setValue={(val) => {
              state.set("settings", "ui", "favoriteMaxLength", Number(val))
            }}
          />
        </article>
        <article class="setting wide">
          <header>
            <h3>Favorite genres</h3>
          </header>
          <ButtonMulti
            class="font"
            items={genres()}
            active={state.get.settings.ui.favoriteGenres}
            setValue={(val) => {
              state.set("settings", "ui", "favoriteGenres", (prev) => {
                if (prev.includes(val)) {
                  return prev.filter((genre) => genre !== val)
                }

                return [...prev, val]
              })
            }}
          />
        </article>
      </details>
      <details>
        <summary>
          <h2 class="icon-next">Manage Data</h2>
        </summary>
        <hr />
        <article class="setting">
          <header>
            <h3>Export Save</h3>
            <p>
              Exports data into the <code>.save</code> file, so you can recover
              your progress if you clear the browser data, or change a
              browser/device.
            </p>
          </header>
          <div class="action">
            <button
              classList={{ ok: true, loading: loading().export }}
              style={{ "min-width": "12em" }}
              onClick={async () => {
                setLoading((prev) => ({ ...prev, export: true }))
                await exportSave()
                setLoading((prev) => ({ ...prev, export: false }))
              }}
            >
              Export
            </button>
          </div>
        </article>
        <article class="setting">
          <header>
            <h3>Import Save</h3>
            <p>
              Imports progress from the <code>.save</code> file. The current
              data and the save data are merged. If you want to load only the
              progress from the save file, delete current progress first.
            </p>
          </header>
          <div class="action">
            <label
              classList={{
                file: true,
                warning: true,
                loading: loading().import,
              }}
              style={{ "min-width": "12em" }}
            >
              Import
              <input
                type="file"
                placeholder=""
                accept=".save"
                onChange={async (e) => {
                  setLoading((prev) => ({ ...prev, import: true }))
                  const file = e.target.files?.[0]
                  if (file) {
                    await importSave(file)
                  }
                  e.target.value = ""
                  setLoading((prev) => ({ ...prev, import: false }))
                }}
              />
            </label>
          </div>
        </article>
        <article class="setting">
          <header>
            <h3>Delete Progress</h3>
            <p>Deletes all progress, but preserves the settings.</p>
          </header>
          <div class="action">
            <button
              classList={{ error: true, loading: loading().deleteProgress }}
              style={{ "min-width": "12em" }}
              onClick={() => {
                setConfirm("delete progress")
              }}
            >
              Delete Progress
            </button>
          </div>
        </article>
        <article class="setting">
          <header>
            <h3>Reset Settings</h3>
            <p>
              Resets the settings to their default values, preserves the
              progress.
            </p>
          </header>
          <div class="action">
            <button
              class="error"
              style={{ "min-width": "12em" }}
              onClick={() => {
                setConfirm("reset settings")
              }}
            >
              Reset Settings
            </button>
          </div>
        </article>
      </details>
      <Show when={confirm()}>
        <Confirm
          requiredText={confirm() ?? ""}
          onSumbmit={async () => {
            switch (confirm()) {
              case "delete progress":
                setLoading((prev) => ({ ...prev, deleteProgress: true }))
                await resetProgress()
                setLoading((prev) => ({ ...prev, deleteProgress: false }))
                break
              case "reset settings":
                setLoading((prev) => ({ ...prev, resetSettings: true }))
                await resetSettings()
                setLoading((prev) => ({ ...prev, resetSettings: false }))
                break
            }

            setConfirm(null)
          }}
          onCancel={() => setConfirm(null)}
        />
      </Show>
    </main>
  )
}

export default Settings
