import { useSearchParams } from "@solidjs/router"
import {
  type Component,
  For,
  Show,
  createMemo,
  onCleanup,
  onMount,
} from "solid-js"
import "./Pagination.css"

type Props = {
  of: number
  shortcuts: boolean
  down?: boolean
}

type ButtonData = {
  type: "prev" | "next" | "direct" | "dots"
  disabled: boolean
  val: number
}

const Pagination: Component<Props> = (props) => {
  const [searchParams, setSearchParams] = useSearchParams()

  const currentPage = createMemo(() => {
    return Math.min(props.of, searchParams.page ? Number(searchParams.page) : 1)
  })

  if (props.shortcuts) {
    // TODO - prevent from triggering when in input fields or simmilar
    const registerShortcuts = (e: KeyboardEvent) => {
      switch (e.key) {
        case "ArrowLeft":
          e.preventDefault()
          if (currentPage() > 1) {
            const val = currentPage() - 1
            setSearchParams({ page: val === 1 ? null : val })
          }
          return
        case "ArrowRight":
          e.preventDefault()
          if (currentPage() < props.of) {
            const val = currentPage() + 1
            setSearchParams({ page: val === 1 ? null : val })
          }
          return
      }
    }

    onMount(() => {
      document.addEventListener("keydown", registerShortcuts)
    })

    onCleanup(() => {
      document.removeEventListener("keydown", registerShortcuts)
    })
  }

  const buttons = createMemo(() => {
    if (props.of < 10) {
      return Array.from({ length: props.of }, (_, i) => ({
        type: "direct",
        disabled: false,
        val: i + 1,
      }))
    }

    const of = props.of
    const page = currentPage()
    const x: ButtonData[] = new Array(9).fill({
      type: "direct",
      disabled: false,
      val: 1,
    })
    x[0] = { type: "prev", disabled: page === 1, val: page - 1 }
    x[1] = { type: "direct", disabled: false, val: 1 }
    x[2] = { type: page < 5 ? "direct" : "dots", disabled: page >= 5, val: 2 }
    x[3] = {
      type: "direct",
      disabled: false,
      val: page < 5 ? 3 : page > of - 4 ? of - 4 : page - 1,
    }
    x[4] = {
      type: "direct",
      disabled: false,
      val: page < 5 ? 4 : page > of - 4 ? of - 3 : page,
    }
    x[5] = {
      type: "direct",
      disabled: false,
      val: page < 5 ? 5 : page > of - 4 ? of - 2 : page + 1,
    }
    x[6] = {
      type: page > of - 4 ? "direct" : "dots",
      disabled: page <= of - 4,
      val: of - 1,
    }
    x[7] = { type: "direct", disabled: false, val: of }
    x[8] = { type: "next", disabled: page === of, val: page + 1 }

    return x
  })

  return (
    <Show when={props.of > 1}>
      <nav
        classList={{
          pagination: true,
          down: props.down,
        }}
      >
        <For each={buttons()}>
          {(button) => (
            <button
              classList={{
                [button.type]: true,
                current:
                  button.type === "direct" && button.val === currentPage(),
              }}
              disabled={button.disabled}
              onClick={() => {
                if (!button.disabled) {
                  setSearchParams({
                    page: button.val === 1 ? null : button.val,
                  })
                  window.scrollTo(0, 0)
                }
              }}
            >
              {button.type === "direct"
                ? String(button.val)
                : button.type === "dots"
                  ? ".."
                  : ""}
            </button>
          )}
        </For>
      </nav>
    </Show>
  )
}

export default Pagination
