import {
  Component,
  Show,
  createMemo,
  Switch,
  Match,
  createResource,
} from "solid-js"
import "./Hero.css"
import Cover from "../../common/book/Cover"
import state from "../../../state/state"
import { BookData } from "../../../actions/getBooksData"
import { formatPercentage } from "../../../utils/formatters"
import { getNextChapterId } from "../../../actions/getNextChapterId"
import { getBookInfo } from "../../../io/getBookInfo"
import storage from "../../../storage/storage"

const getNextChapter =
  (id: string, showHero: "welcome" | "last" | null) => async () => {
    if (showHero !== "last") {
      return null
    }

    const data = await getBookInfo(id)

    if (data === null) {
      return null
    }

    const chaptersStats = await storage.chaptersStats.get(id)
    const bookStats = await storage.booksStats.get(id)

    const nextChapterId = getNextChapterId({
      currentChapterId: bookStats?.lastChapter ?? "",
      chaptersStats: chaptersStats ?? {},
      chapters: data.chapters,
      progress: bookStats?.progress ?? 0,
    })

    return nextChapterId
  }

type Props = {
  lastBookData: BookData
}

const Hero: Component<Props> = (props) => {
  const showHero = createMemo(() => {
    const showWelcome =
      state.get.loaded &&
      state.get.settings.ui.showHero &&
      (state.get.booksIndex.find(
        (book) => book.id === "entertrained--first-steps",
      )?.progress ?? 0) === 0

    return showWelcome ? "welcome" : props.lastBookData ? "last" : null
  })

  const [nextChapterId] = createResource(
    getNextChapter(props.lastBookData?.id, showHero()),
  )

  return (
    <Show when={showHero()}>
      <section
        classList={{ hero: true, last: showHero() === "last" }}
        style={{
          "--bg-img":
            showHero() === "welcome"
              ? `url(/images/bg.webp)`
              : `url(${props.lastBookData.coverBigUrl})`,
        }}
      >
        <div>
          <Switch>
            <Match when={showHero() === "welcome"}>
              <div class="hero-intro">
                <h2>Welcome to Entertrained!</h2>
                <p>
                  Practice touch typing by{" "}
                  <strong>retyping famous novels</strong>. Not only will you{" "}
                  <strong>improve your typing skills</strong>, but you’ll also
                  enjoy some of the greatest stories ever told!
                </p>
                <p>
                  A good place to start is our step-by-step guide that
                  kickstarts your adventure into the world of touch typing.
                </p>
                <nav>
                  <button
                    class="secondary"
                    onClick={() =>
                      state.set("settings", "ui", "showHero", false)
                    }
                  >
                    Skip
                  </button>
                  <a
                    href="/prompt/entertrained--first-steps__000"
                    class="button primary ok"
                  >
                    Start the Tutorial
                  </a>
                </nav>
              </div>
              <div class="hero-call">
                <Cover url="/books/entertrained--first-steps/cover.min.webp" />
              </div>
            </Match>
            <Match when={showHero() === "last"}>
              <div class="hero-intro ">
                <small>Most recent</small>
                <h2>{props.lastBookData.title}</h2>
                <h3>
                  by <a href="#">{props.lastBookData.author}</a>
                </h3>
                <p>{props.lastBookData.description}</p>
                <p
                  class="progress"
                  style={{
                    "--width": formatPercentage(props.lastBookData.progress),
                  }}
                >
                  {formatPercentage(props.lastBookData.progress, 1)} done
                </p>
                <nav>
                  <a
                    href={`/books/${props.lastBookData.id}`}
                    class="button secondary"
                  >
                    Details
                  </a>
                  <a
                    href={`/prompt/${props.lastBookData.id}__${
                      nextChapterId() ?? "000"
                    }`}
                    class="button primary ok"
                  >
                    Continue
                  </a>
                </nav>
              </div>
              <div
                class="hero-call"
                style={{ "--pages": props.lastBookData.pages }}
              >
                <Cover url={props.lastBookData.coverBigUrl} />
              </div>
            </Match>
          </Switch>
        </div>
      </section>
    </Show>
  )
}

export default Hero
