import { type ParentComponent, Show, createEffect, on } from "solid-js"
import "./App.css"
import Header from "./common/header/Header.tsx"
import { useLocation } from "@solidjs/router"
import Toast from "./common/toast/Toast.tsx"
import state from "../state/state.ts"
import Loading from "./common/loading/Loading.tsx"

const App: ParentComponent = (props) => {
  const location = useLocation()

  // Clean notifications when navigating to the different place
  createEffect(
    on(
      () => location.pathname,
      () => {
        state.set("session", "notifications", [])
      },
    ),
  )

  return (
    <Show when={state.get.loaded} fallback={<Loading />}>
      <div
        classList={{
          "fit-height": location.pathname.startsWith("/prompt"),
          [state.get.settings.ui.theme]: true,
        }}
      >
        <Show when={!location.pathname.startsWith("/prompt")}>
          <Header />
        </Show>
        <Toast
          items={state.get.session.notifications}
          closeAll={() => state.set("session", "notifications", [])}
        />
        <main>{props.children}</main>
      </div>
    </Show>
  )
}

export default App
