import { type SetStoreFunction, produce } from "solid-js/store"
import type { LocalState } from "../types"
import { updateAverageWpm } from "./updateAverageWpm"
import { updateAverageAccuracy } from "./updateAverageAccuracy"

export function handleParagraphReset(
  local: LocalState,
  setLocal: SetStoreFunction<LocalState>,
) {
  if (local.paragraphNum < 0 || local.typed.length === 0) {
    return
  }

  setLocal("prev", "")
  setLocal("prevOk", true)

  setLocal("typed", local.paragraphNum, (prev) =>
    prev.map((word) => new Array(word.length).fill(null)),
  )
  setLocal("stats", local.paragraphNum, (prev) => {
    return {
      ...prev,
      correctCharCount: 0,
      wpm: null,
      acc: null,
      con: null,
      time: 0,
      inputTimes: [],
      startTime: 0,
      endTime: 0,
      totalTime: 0,
      typos: 0,
      nonTypos: 0,
      words: local.original[local.paragraphNum].map((chars) => ({
        length: chars.length,
        typedLength: 0,
        times: new Array(chars.length),
        isCorrect: true,
        hadTypos: false,
        typosIndicies: [],
      })),
    }
  })

  // At the end so stats are recalculated correctly
  setLocal(
    produce((state) => {
      state.wordNum = 0
      state.charNum = 0
      state.paused = true
    }),
  )

  updateAverageWpm(local, setLocal)
  updateAverageAccuracy(local, setLocal)
}
