import type { StaticBook, StaticChapter } from "../../types/common.ts"
import type { Book, Chapter } from "./types.ts"

function serializeChapter(chapter: Chapter, index: number): StaticChapter {
  return {
    id: String(index + 1).padStart(3, "0"),
    title: chapter.title,
    length: chapter.paragraphs
      .map((p) => p.length + 1)
      .reduce((a, b) => a + b, 0),
    skip: chapter.skip,
    text: chapter.paragraphs.join("\n"),
  }
}

export function serializeBook(book: Book): {
  all: StaticBook
  cover: Blob | null
} {
  const all = {
    ...book.info,
    series: null,
    part: 0,
    chapters: book.chapters.map(serializeChapter),
    createdAt: Date.now(),
  }

  if (
    new Set(all.chapters.map((chapter) => chapter.id)).size !==
    all.chapters.length
  ) {
    throw new Error(
      "Ambiguous chapter names (some chapters have the same titles)!",
    )
  }

  return {
    all,
    cover: book.cover,
  }
}
