import { produce } from "solid-js/store"
import state from "../../../../state/state"
import storage from "../../../../storage/storage"
import type { FinishedParagraphStats } from "../../../../types/common"
import { saveSummary } from "./saveSummary"
import { summarizeBookStats, summarizeChapterStats } from "./summarizeStats"

export async function saveParagraphs(
  path: string,
  skip: boolean,
  paragraph: {
    num: number
    stats: FinishedParagraphStats
    missedWords?: string[]
    isNextHr?: boolean
  },
  chapterLength: number,
  paragraphsLengths: number[] = [],
) {
  const changes = {
    readAdded: new Set<number>(),
    readDeleted: new Set<number>(),
    lastIndex: -1,
  }

  try {
    const [bookId, chapterId] = path.split("__")
    const bookLength =
      state.get.booksIndex.find((book) => book.id === bookId)?.length ?? 0

    const paragraphsStats = (await storage.paragraphsStats.get(path)) ?? {}
    const chaptersStats = (await storage.chaptersStats.get(bookId)) ?? {}

    if (paragraph.stats.type === "read") {
      const entries = Object.entries(paragraphsStats)
      const lastTypedIndex = Math.max(
        ...entries
          .filter(([_, entry]) => entry.type === "typed")
          .map(([id]) => Number(id)),
        -1,
      )
      const lastReadIndex = Math.max(
        ...entries
          .filter(([_, entry]) => entry.type === "read")
          .map(([id]) => Number(id)),
        -1,
      )

      const to = paragraph.isNextHr ? paragraph.num + 1 : paragraph.num
      const readToIndex = Math.max(lastTypedIndex, to)
      const maxIndex = Math.max(lastReadIndex, readToIndex)

      for (let i = 0; i <= maxIndex; i++) {
        if (i <= readToIndex && !paragraphsStats[i]) {
          paragraphsStats[i] = {
            ...paragraph.stats,
            length: paragraphsLengths[i],
          }
          changes.readAdded.add(i)
        } else if (i > readToIndex && paragraphsStats?.[i].type === "read") {
          delete paragraphsStats[i]
          changes.readDeleted.add(i)
        }
      }

      if (to === lastReadIndex) {
        delete paragraphsStats[paragraph.num]
        changes.readDeleted.add(paragraph.num)
        if (paragraph.isNextHr) {
          delete paragraphsStats[to]
          changes.readDeleted.add(to)
        }
      }

      changes.lastIndex = Math.max(
        ...Object.entries(paragraphsStats).map(([i]) => Number(i)),
        -1,
      )
    } else {
      paragraphsStats[paragraph.num] = paragraph.stats
    }

    const chapterStats = summarizeChapterStats(
      Object.values(paragraphsStats),
      chapterLength,
      skip,
    )

    chaptersStats[chapterId] = chapterStats

    const bookStats = summarizeBookStats(
      Object.values(chaptersStats),
      bookLength,
      chapterId,
    )
    await storage.paragraphsStats.set(path, paragraphsStats)
    await storage.chaptersStats.set(bookId, chaptersStats)
    await storage.booksStats.set(bookId, bookStats)

    await saveSummary(
      paragraph.stats,
      chapterStats,
      bookStats,
      path,
      bookId,
      paragraph.num,
    )

    if (paragraph.missedWords) {
      const existingMissedWords =
        (await storage.general.get("missedWords")) ?? []
      const updatedMissedWords = [
        ...new Set([...existingMissedWords, ...paragraph.missedWords]),
      ]
      await storage.general.set("missedWords", updatedMissedWords)
    }

    state.set(
      "booksIndex",
      state.get.booksIndex.findIndex((x) => x.id === bookId),
      produce((book) => {
        book.progress = bookStats.progress
        book.timestamp = bookStats.timestamp
      }),
    )
  } catch (e) {
    console.error(e)
  }

  return changes
}
