import { useNavigate } from "@solidjs/router"
import { type Component, Show } from "solid-js"
import "./Cover.css"

const Cover: Component<{ url: string; link?: string }> = (props) => {
  const navigate = useNavigate()
  const style = `background-image: url(${props.url});`

  return (
    <div classList={{ cover: true, link: Boolean(props.link) }}>
      <div
        style={style}
        onClick={() => {
          console.log("Cover click!")
          if (props.link) {
            navigate(props.link)
          }
        }}
      >
        <Show when={props.link}>
          <div>
            {" "}
            <p>Open</p>
          </div>
        </Show>
      </div>
    </div>
  )
}

export default Cover
