import config from "../../../../config"
import { delay } from "../../../../utils/delay"

async function getActiveWord() {
  let attempts = 0
  while (true) {
    const node = document.querySelector(
      ".word.active",
    ) as HTMLSpanElement | null
    if (node || attempts > 10) {
      return node
    }

    await delay(100)
  }
}

function scrollToNode(
  prev = -1,
  screenKeyboardPrompt: HTMLTextAreaElement | undefined,
  node: HTMLSpanElement | null,
  behavior: "smooth" | "instant",
) {
  if (node) {
    if (config.IS_MOBILE && !config.IS_APPLE && screenKeyboardPrompt) {
      screenKeyboardPrompt.style.top = `${node.getBoundingClientRect().top}px`
    }

    const offset = window.scrollY + node.getBoundingClientRect().top

    if (offset !== prev) {
      node.scrollIntoView({
        behavior,
        block: "center",
      })

      return offset
    }
  }

  return prev
}

export async function initScroll(
  screenKeyboardPrompt: HTMLTextAreaElement | undefined,
) {
  const node = await getActiveWord()
  scrollToNode(0, screenKeyboardPrompt, node, "smooth")
}

export function scrollToWord(
  prev = -1,
  screenKeyboardPrompt: HTMLTextAreaElement | undefined,
) {
  const node = document.querySelector(".word.active") as HTMLSpanElement | null
  scrollToNode(prev, screenKeyboardPrompt, node, "smooth")
  return prev
}
