export function getOS(): "Apple" | "Linux" | "Windows" | "Unknown" {
  // @ts-ignore
  const platform = navigator?.userAgentData?.platform || navigator?.platform

  if (typeof platform !== "string") {
    return "Unknown"
  }

  if (/^(iOS|iPhone|iPad|Mac)/i.test(platform)) {
    return "Apple"
  }

  if (/^(Linux)/i.test(platform)) {
    return "Linux"
  }

  if (/^(Win)/i.test(platform)) {
    return "Windows"
  }

  return "Unknown"
}
