import { HIDE_TITLES, SKIP_TITLES } from "./specialChapterNames"

const isMatchingTiles = (titles: typeof SKIP_TITLES) => (title: string) => {
  const titleLow = title.toLowerCase()

  return (
    titles.phrases.has(titleLow) ||
    [...titles.phrases].some((phrase) => titleLow.includes(phrase)) ||
    titles.patterns.some((pattern) => pattern.test(titleLow))
  )
}

export const shouldSkip = isMatchingTiles(SKIP_TITLES)
export const shouldHide = isMatchingTiles(HIDE_TITLES)
