import "./QuickBooks.css"
import { Component, For, Show } from "solid-js"
import Book from "../../common/book/Book"
import { BookData } from "../../../actions/getBooksData"

type Props = {
  booksInProgress: BookData[]
}

const QuickBooks: Component<Props> = (props) => {
  return (
    <Show when={props.booksInProgress.length > 0}>
      <section class="quick-books">
        <h2>In Progress</h2>
        <For each={props.booksInProgress.slice(0, 3)}>
          {(book) => (
            <Book
              id={book.id}
              coverUrl={book.coverUrl}
              title={book.title}
              author={book.author}
              description={book.description}
              pages={book.pages}
              progress={book.progress}
              favorite={book.favorite}
              isNew={book.isNew}
              genres={book.genres}
            />
          )}
        </For>
        <Show when={props.booksInProgress.length > 3}>
          <footer>
            <a class="button primary" href="/books?tab=in-progress">
              Show All in Progress
            </a>
          </footer>
        </Show>
      </section>
    </Show>
  )
}

export default QuickBooks
