import { type Component, For } from "solid-js"
import "./ButtonToggle.css"

const ButtonMulti: Component<{
  items: {
    name: string
    value: string
    tooltip?: string
  }[]
  active: string[]
  setValue: (val: string) => void
  joined?: boolean
  style?: string | { [key: string]: string }
  class?: string
}> = (props) => {
  return (
    <div
      classList={{
        "button-toggle": true,
        separate: !props.joined,
        [props.class ?? ""]: true,
      }}
    >
      <For each={props.items}>
        {(item) => (
          <button
            classList={{ active: props.active.includes(item.value) }}
            onClick={() => props.setValue(item.value)}
            style={props.style}
            title={item.tooltip}
          >
            {item.name}
          </button>
        )}
      </For>
    </div>
  )
}

export default ButtonMulti
