import { getFont } from "../../../actions/getFont"
import config from "../../../config"
import { layouts } from "../../../data/mobileKeyboardLayouts"
import state from "../../../state/state"
import "./ScreenKeyboard.css"
import { Component, For, Show, createMemo, createSignal } from "solid-js"

type SpecialKeys = {
  [key: string]: {
    size: number
    value: string
    label: string
    accent: boolean
    icon?: string
    special?: boolean
    mod?: boolean
  }
}

function getSpecialKeys(template: string): SpecialKeys {
  return {
    shift: {
      size: template === "type-b" ? 4 : 3,
      value: "Shift",
      label: "shift",
      accent: true,
      icon: "shift",
      special: true,
      mod: true,
    },
    backspace: {
      size: template === "type-b" ? 4 : 3,
      value: "Backspace",
      label: "backspace",
      accent: true,
      icon: "backspace",
      special: true,
    },
    delete: {
      size: template === "type-b" ? 4 : 3,
      value: "Delete",
      label: "Delete",
      accent: true,
      icon: "backspace-word",
      special: true,
    },
    skip: {
      size: 3,
      value: "Skip",
      label: "Skip",
      accent: true,
      icon: "skip",
      special: true,
    },
    space: { size: 6, value: " ", label: " ", accent: false },
    tab: {
      size: template === "type-b" ? 4 : 3,
      value: "Tab",
      label: "Tab",
      accent: true,
      icon: "reset",
      special: true,
    },
    dash: { size: 3, value: "-", label: "-", accent: true },
    more: {
      size: 3,
      value: "More",
      label: "more",
      accent: true,
      icon: "symbols",
      special: true,
      mod: true,
    },
    enter: {
      size: 3,
      value: "Enter",
      label: config.ENTER_SYMBOL,
      accent: true,
      icon: "enter",
    },
  }
}

function layoutToDetails(layout: string[][], template: string) {
  const specialKeys = getSpecialKeys(template)

  return layout.map((row, i) =>
    row.map(
      (key) =>
        specialKeys[key] ?? {
          size: 2,
          value: key,
          label: key,
          accent: i === 0 || i === layout.length - 1,
        },
    ),
  )
}

type Props = {
  handleTyping: (e: KeyboardEvent) => void
  expectedChar?: string
  prev: string
  prevOk: boolean
}

const ScreenKeyboard: Component<Props> = (props) => {
  const [layer, setLayer] = createSignal<"main" | "shift" | "more">("main")

  const showNext = createMemo(
    () =>
      state.get.settings.typingPrompt.keyboardIndicator === "next" ||
      state.get.settings.typingPrompt.keyboardIndicator === "both",
  )

  const showLast = createMemo(
    () =>
      state.get.settings.typingPrompt.keyboardIndicator === "last" ||
      state.get.settings.typingPrompt.keyboardIndicator === "both",
  )

  const layout = createMemo(() => {
    const layoutName = state.get.settings.typingPrompt.keyboardLayoutMobile
    const layoutData = layouts[layoutName]
    const main = layoutToDetails(layoutData.main, layoutData.template)
    const shift = layoutToDetails(layoutData.shift, layoutData.template)
    const more = layoutToDetails(layoutData.more, layoutData.template)

    const chars = {
      main: new Set(main.flat().map((key) => key.value)),
      shift: new Set(shift.flat().map((key) => key.value)),
      more: new Set(more.flat().map((key) => key.value)),
    }

    const getkeyLayer = (
      key: string | undefined,
      currentLayer: "main" | "shift" | "more",
    ) => {
      if (!key || chars[currentLayer].has(key)) {
        return
      }

      if (chars.main.has(key)) {
        return currentLayer
      }

      if (chars.shift.has(key)) {
        return "shift"
      }

      if (chars.more.has(key)) {
        return "more"
      }

      return chars.main.has(key)
    }

    return {
      name: layoutName,
      layers: { main, shift, more },
      getkeyLayer,
    }
  })

  const font = createMemo(() => {
    return getFont(state.get.settings.typingPrompt.keyBoardFont)
  })

  const [last, setLast] = createSignal({ x: -1, y: -1 })

  return (
    <section
      classList={{
        "screen-keyboard": true,
        [`keycaps-${state.get.settings.typingPrompt.keyboardKeycaps}`]: true,
        error: !props.prevOk,
        [layout().name]: true,
      }}
      style={{
        "--key-thickness":
          state.get.settings.typingPrompt.keyboardThickness ?? "2px",
        "--font": font().font,
        "--font-scale": font().size,
      }}
    >
      <div classList={{ [layer()]: true }}>
        <For each={layout().layers.main}>
          {(row, y) => (
            <div class="row">
              <For each={row}>
                {(_, x) => {
                  const key = createMemo(
                    () => layout().layers[layer()][y()][x()],
                  )

                  return (
                    <button
                      classList={{
                        accent: key().accent,
                        [props.prevOk ? "ok" : "error"]:
                          showLast() &&
                          !key().special &&
                          last().x === x() &&
                          last().y === y(),
                        warning: key().mod && layer() === key().label,
                        next:
                          showNext() &&
                          (props.expectedChar === key().label ||
                            layout().getkeyLayer(
                              props.expectedChar,
                              layer(),
                            ) === key().label),
                      }}
                      style={{ "grid-column-end": `span ${key().size}` }}
                      onPointerOut={() => {
                        setLast({ x: -1, y: -1 })
                      }}
                      onPointerUp={() => {
                        setLast({ x: -1, y: -1 })
                      }}
                      onPointerDown={() => {
                        setLast({ x: x(), y: y() })

                        switch (key().value) {
                          case "Shift": {
                            setLayer(layer() === "shift" ? "main" : "shift")
                            break
                          }
                          case "More": {
                            setLayer(layer() === "more" ? "main" : "more")
                            break
                          }
                          case "Delete": {
                            const event = new KeyboardEvent("keydown", {
                              key: "Backspace",
                              ctrlKey: true,
                            })
                            if (layer() !== "main") {
                              setLayer("main")
                            }
                            props.handleTyping(event)
                            break
                          }
                          case "Skip": {
                            const event = new KeyboardEvent("keydown", {
                              key: "Enter",
                              shiftKey: true,
                            })
                            if (layer() !== "main") {
                              setLayer("main")
                            }
                            props.handleTyping(event)
                            break
                          }
                          default: {
                            const event = new KeyboardEvent("keydown", {
                              key: key().value,
                            })
                            if (layer() !== "main") {
                              setLayer("main")
                            }
                            props.handleTyping(event)
                            break
                          }
                        }

                        const haptics =
                          state.get.settings.typingPrompt.keyboardHaptics

                        if (!config.IS_APPLE && haptics !== 0) {
                          navigator?.vibrate(haptics)
                        }
                      }}
                    >
                      <div>
                        <Show when={key().icon} fallback={key().label}>
                          <i class={`icon-${key().icon}`} />
                        </Show>
                      </div>
                    </button>
                  )
                }}
              </For>
            </div>
          )}
        </For>
      </div>
    </section>
  )
}

export default ScreenKeyboard
